/** @deprecated prefer createRpcClientV2 */
export function createRpcClientV1(options: {
  hubHttpClient: HubHttpClient;
  dismissErrorWithoutHandling?: (error: Error) => undefined;
}) {
  const httpRpcClient = createHttpRpcClientV1(options);

  function executeRemoteProcedure<
    Details extends HttpRpcDetails<any, any, any, any>,
  >(details: Details, inputs: RpcInputs<Details>): Promise<RpcResult<Details>> {
    return httpRpcClient.call(details, inputs as HttpRpcInputs<Details>);
  }

  return { call: executeRemoteProcedure };
}

export function createHttpRpcClientV2({
  hubHttpClient,
}: {
  hubHttpClient: HubHttpClient;
}) {
  function executeRemoteProcedure<
    Details extends HttpRpcDetails<any, any, any, any>,
  >(
    details: Details,
    { pathParameters, queryParameters, data }: HttpRpcInputs<Details>
  ): Promise<RpcResult<Details>> {
    const path = buildHttpRpcPath(details, pathParameters);
    const syncStackError = new Error(
      `HTTP RPC failed for ${details.method}-/${path}`
    );

    let options:
      | undefined
      | { data?: typeof data; query?: typeof queryParameters } = undefined;

    if (data) {
      options = options || {};
      options.data = data;
    }

    if (queryParameters) {
      options = options || {};
      options.query = queryParameters;
    }

    const apiPromise = hubHttpClient[details.method](path, options) as Promise<
      HttpRpcResult<Details>
    >;

    return apiPromise.catch((error: Error) => {
      syncStackError.cause = error;
      throw syncStackError;
    });
  }

  return { call: executeRemoteProcedure };
}

/**
 * This client factory takes a `hub-http` client and returns a client that
 * executes network requests as RPCs.
 *
 * An RPC (Remote Procedure Call) is a network request where the details of the
 * network have been abstracted away. In other words, RPCs are calls to typed
 * async functions that make network requests under the hood. The goal is to
 * minimize time spent thinking about network details (HTTP verbs, url
 * sanitization, CHIRP query names, etc.).
 *
 * This factory is suffixed "V2" because it is guaranteed to never change in a
 * breaking way. If features incompatible with this client factory are added
 * in the future, this client factory will remain available and a new "V3" will
 * be exposed as a sibling for new consumers.
 *
 * ```
 * // kudos service contains generated code
 * import { getKudos } from 'kudos-service/client';
 * import { createRpcClientV1 } from 'rpc-client-utils';
 *
 * const rpcClient = createRpcClientV1({ hubHttpClient: hubHttpClient });
 *
 * const kudos = await rpcClient.call(getKudos, {
 *   // depending on the RPC, data and pathParameters may also be passed
 *   queryParameters: {
 *     hubspotter: 'jdoe'
 *   }
 * });
 * ```
 */
export function createRpcClientV2(
  options:
    | {
        hubHttpClient: HubHttpClient;
        fetchClient?: FetchType;
      }
    | {
        axiosClient: AxiosClient;
      }
) {
  const effectiveHubHttpClient =
    'hubHttpClient' in options
      ? options.hubHttpClient
      : impersonateHubHttp(options.axiosClient);

  const httpRpcClient = createHttpRpcClientV2({
    hubHttpClient: effectiveHubHttpClient,
  });
  const chirpAvroRpcClient = createChirpAvroRpcClientV1({
    hubHttpClient: effectiveHubHttpClient,
    fetchClient: 'fetchClient' in options ? options.fetchClient : undefined,
  });

  function executeRemoteProcedure<
    Details extends
      | HttpRpcDetails<any, any, any, any>
      | ChirpAvroRpcDetails<any, any, any, any>,
  >(details: Details, inputs: RpcInputs<Details>): Promise<RpcResult<Details>> {
    if (!details) {
      throw new Error(
        'RPC details object is undefined. Please ensure RPC is properly initialized.'
      );
    }

    if (details.method) {
      return httpRpcClient.call(
        details,
        inputs as HttpRpcInputs<HttpRpcDetails<any, any, any, any>>
      );
    } else {
      return chirpAvroRpcClient.call(
        details as ChirpAvroRpcDetails<any, any, any>,
        inputs as ChirpAvroRpcInputs<ChirpAvroRpcDetails<any, any, any>>
      );
    }
  }

  function callWithResultUnion<
    Details extends ChirpAvroRpcDetails<any, any, any, any>,
  >(
    details: Details,
    inputs: ChirpAvroRpcInputs<Details>
  ): Promise<ResultUnion<Details>> {
    if (details.method) {
      throw new Error(
        'callWithResultUnion is not implemented for non-avro RPCs.'
      );
    }

    return chirpAvroRpcClient.callWithResultUnion(details, inputs);
  }

  function stream<Details extends ChirpAvroRpcDetails<any, any, any, true>>(
    details: Details,
    inputs: ChirpAvroRpcInputs<Details>
  ): StreamHandler<ResultUnion<Details>> {
    if (details.method || 'gateway' in details || !details.isStreaming) {
      throw new Error('stream is only implemented for streaming Avro RPCs.');
    }

    return chirpAvroRpcClient.stream(details, inputs);
  }

  return {
    call: executeRemoteProcedure,
    callWithResultUnion,
    stream,
  };
}

export type RpcClientV2 = ReturnType<typeof createRpcClientV2>;

/** @deprecated prefer createHttpRpcClientV2 */
export function createHttpRpcClientV1({
  hubHttpClient,
  dismissErrorWithoutHandling = (error) =>
    defaultDismissErrorWithoutHandling(error),
}: {
  hubHttpClient: HubHttpClient;
  dismissErrorWithoutHandling?: (error: Error) => undefined;
}) {
  function executeRemoteProcedure<
    Details extends HttpRpcDetails<any, any, any, any>,
  >(
    details: Details,
    { pathParameters, queryParameters, data }: HttpRpcInputs<Details>
  ): Promise<RpcResult<Details>> {
    const path = buildHttpRpcPath(details, pathParameters);
    const syncStackError = new Error(
      `HTTP RPC failed for ${details.method}-/${path}`
    );

    let options:
      | undefined
      | { data?: typeof data; query?: typeof queryParameters } = undefined;

    if (data) {
      options = options || {};
      options.data = data;
    }

    if (queryParameters) {
      options = options || {};
      options.query = queryParameters;
    }

    const apiPromise = hubHttpClient[details.method](path, options) as Promise<
      HttpRpcResult<Details>
    >;

    return apiPromise.catch((error: Error) => {
      dismissErrorWithoutHandling(syncStackError);
      throw error;
    });
  }

  return { call: executeRemoteProcedure };
}

export function createChirpAvroRpcClientV1({
  hubHttpClient,
  fetchClient,
}: {
  hubHttpClient: HubHttpClient;
  fetchClient?: FetchType;
}) {
  function call<Details extends ChirpAvroRpcDetails<any, any, any, any>>(
    details: Details,
    inputs: ChirpAvroRpcInputs<Details>
  ): Promise<RpcResult<Details>> {
    return makeChirpAvroCall({
      hubHttpClient,
      details,
      responseParser: parseRejectingChirpErrors,
      inputs,
      alternateData: getChirpEarlyRequest({ details, requestObject: inputs }),
    }) as RpcResult<Details>;
  }

  function callWithResultUnion<
    Details extends ChirpAvroRpcDetails<any, any, any, any>,
  >(
    details: Details,
    inputs: ChirpAvroRpcInputs<Details>
  ): Promise<ResultUnion<Details>> {
    return makeChirpAvroCall<Details, ResultUnion<Details>>({
      hubHttpClient,
      details,
      responseParser: parseIntoUnion,
      inputs,
      alternateData: getChirpEarlyRequest({ details, requestObject: inputs }),
    });
  }

  // Create a wrapper that passes the fetch client to createStreamHandler
  function stream<Details extends ChirpAvroRpcDetails<any, any, any, true>>(
    details: Details,
    inputs: ChirpAvroRpcInputs<Details>
  ) {
    if (!fetchClient) {
      throw new Error(
        'Streaming RPCs require a fetchClient to be provided when creating the RPC client. ' +
          'Please provide a fetchClient in the options: createRpcClientV2({ hubHttpClient, fetchClient })'
      );
    }
    return createStreamHandler(details, inputs, fetchClient);
  }

  return {
    call,
    callWithResultUnion,
    stream,
  };
}

