import { Tab } from 'sales-clients-common/chrome/types';
import { CALENDAR_CONNECTED } from 'sales-ext-common/constants/backgroundMessagingMethods';
// @ts-expect-error untyped
import chrome from 'SignalsExtension/globals/chrome';

export const calendarConnectedHandler = () => {
  chrome.tabs.query({ url: '*://calendar.google.com/*' }, (tabs: Tab[]) => {
    tabs.forEach((tab) => {
      chrome.tabs.sendMessage(tab.id, { method: CALENDAR_CONNECTED });
    });
  });
};
