export interface PortalIdParserOptions {
  preserveGlobalId?: boolean;
  regex?: RegExp;
  reparse?: boolean;
}

export default {
  id: null as unknown as number,
  pathRegex: /^\/(?:[A-Za-z0-9-_]*)\/(\d+)(?:\/|$)/,
  queryParamRegex: /(?:\?|&)portalid=(\d+)/i,
  getPortalIdFromPath(regex?: RegExp) {
    if (document) {
      if (regex == null) {
        regex = this.pathRegex;
      }
      return this.parsePortalIdFromString(document.location.pathname, regex);
    }
  },
  getPortalIdFromQueryParam() {
    if (document) {
      return this.parsePortalIdFromString(
        document.location.search,
        this.queryParamRegex
      );
    }
  },
  parsePortalIdFromString(string: string, regex: RegExp) {
    const idRe = regex.exec(string);
    const portalId = idRe != null ? idRe[1] : undefined;
    return portalId ? +portalId : undefined;
  },
  get(options?: PortalIdParserOptions) {
    if (options == null) {
      options = {};
    }
    if (this.id && !options.reparse) {
      return this.id;
    }
    const id =
      this.getPortalIdFromPath(options.regex) ||
      this.getPortalIdFromQueryParam();
    if (!options.preserveGlobalId) {
      if (window.hubspot == null) {
        window.hubspot = {};
      }
      if (window.hubspot.portal == null) {
        window.hubspot.portal = {};
      }
      if (window.hubspot.portal.id == null) {
        window.hubspot.portal.id = id;
      }
      if (id) {
        this.id = id;
      }
    }
    return id;
  },
};
