import { parseUrl, buildUrl } from 'hub-http/helpers/url';
import * as UrlParams from 'hub-http/helpers/params';

interface UrlParam {
  [key: string]: string | number | boolean | null;
}

/**
 * Validate if a string is a URL
 * @param {string} url
 */
export const isValidUrl = (str: string) => {
  const url = parseUrl(str);

  return !!(url.protocol && url.hostname && (url.path || url.path === ''));
};

/**
 * Take a url string and push some params onto it
 * @param {string} url
 * @param {{[key: string]: string | number}} params
 */
export const addParamsToUrl = (url: string, params: UrlParam) => {
  if (typeof url === 'string') {
    const urlObject = parseUrl(url);
    urlObject.query = UrlParams.stringify({
      ...UrlParams.parse(urlObject.query),
      ...params,
    });

    return buildUrl(urlObject);
  }
  return url;
};
