import { localGet, localSet } from 'sales-clients-common/chrome/safeStorage';
import {
  DEFAULT_SHOW_NEW_FEATURE_ALERT,
  DEFAULT_SHOW_NEW_FEATURE_TAG,
  DEFAULT_ACTION_NEW_BADGE_DISMISSED,
} from 'sales-clients-common/constants/StorageKeys';

const getLocalState = (key: string) => {
  return localGet(key).then((data) => {
    return data[key];
  });
};

/* When introducing new features, consume the following functions to get the state of the new feature alert, new feature tag, and action new badge dismissed.
 * Use specific keys to store the state of the new feature alert, new feature tag, and action new badge dismissed as needed.
 */

export const getNewFeatureAlertState = (
  newFeatureAlert = DEFAULT_SHOW_NEW_FEATURE_ALERT
) => {
  return getLocalState(newFeatureAlert);
};

export const updateNewFeatureAlertState = (
  val = false,
  newFeatureAlert = DEFAULT_SHOW_NEW_FEATURE_ALERT
) => {
  void localSet({ [newFeatureAlert]: val });
};

export const getNewFeatureTagState = (
  newFeature = DEFAULT_SHOW_NEW_FEATURE_TAG
) => {
  return getLocalState(newFeature);
};

export const updateNewFeatureTagState = (
  val = false,
  newFeature = DEFAULT_SHOW_NEW_FEATURE_TAG
) => {
  void localSet({ [newFeature]: val });
};

export const getActionNewBadgeDismissed = (
  newFeatureDismissed = DEFAULT_ACTION_NEW_BADGE_DISMISSED
) => {
  return getLocalState(newFeatureDismissed);
};

export const updateActionNewBadgeDismissed = (
  val = true,
  newFeatureDismissed = DEFAULT_ACTION_NEW_BADGE_DISMISSED
) => {
  void localSet({ [newFeatureDismissed]: val });
};
