import {
  ChirpAvroRpcDetails,
  ChirpAvroRpcInputs,
  StreamHandler,
} from './types';
import {
  ResultUnion,
  makeChirpAvroCallWithFetch,
  ChirpInternalError,
  ResponseUnion,
  spreadPropertiesIntoError,
} from './chirp-avro';
import streamFn from './streamClient';
import { FetchType } from './apiClients/global';
import { shouldUseLocalBackend, isLocalService } from './localRequestUtils';
import {
  consumeEarlyStreamEntry,
  getEarlyStreamEntryFor,
} from './chirp-streaming-quick-fetch';

/**
 * Creates a handler for streaming RPC responses.
 *
 * The returned StreamHandler extends Promise and supports both callback and Promise patterns:
 * - Use .onMessage() to handle data chunks (including domain errors)
 * - Use .then() to handle stream completion
 * - Use .catch() to handle transport errors
 * - Use .abort() to stop the stream
 *
 * @param details The details of the streaming RPC
 * @param inputs The inputs for the RPC call
 * @param fetchClient The fetch client to use for making requests
 * @returns A StreamHandler that allows handling data and supports Promise patterns
 */
export function createStreamHandler<
  Details extends ChirpAvroRpcDetails<any, any, any, true>,
>(
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>,
  fetchClient: FetchType
): StreamHandler<ResultUnion<Details>> {
  const chirpFetch = fetchClient;

  let entryCallback: ((data: ResultUnion<Details>) => void) | null = null;
  let promiseResolve: (() => void) | null = null;
  let promiseReject: ((error: Error) => void) | null = null;
  let abortController: AbortController | null = null;
  let streamActive = false;

  // Create base promise
  const promise = new Promise<void>((resolve, reject) => {
    promiseResolve = resolve;
    promiseReject = reject;
  });

  // Add streaming handler methods to promise
  const handler = Object.assign(promise, {
    onMessage(
      callback: (data: ResultUnion<Details>) => void
    ): StreamHandler<ResultUnion<Details>> {
      entryCallback = callback;
      void startStreamIfReady();
      return handler as StreamHandler<ResultUnion<Details>>;
    },
    abort(): void {
      if (abortController && streamActive) {
        abortController.abort();
        streamActive = false;

        // Clean up resources and resolve the promise
        if (promiseResolve) {
          promiseResolve();
        }
      }
    },
  }) as StreamHandler<ResultUnion<Details>>;

  // Start streaming when callback is ready
  async function startStreamIfReady(): Promise<void> {
    if (!entryCallback || streamActive) {
      return; // Wait until entry callback is set and ensure we don't start multiple streams
    }

    const syncStackError = new Error(
      `CHIRP streaming RPC failed for ${details.rpcName}`
    );

    try {
      // eslint-disable-next-line compat/compat
      abortController = new AbortController();
      streamActive = true;

      // If an early stream exists for the same key, consume it; otherwise start a new one
      const earlyEntry = getEarlyStreamEntryFor(details, inputs);
      let response: Response;
      if (earlyEntry) {
        // Detach early controller, we'll manage cancellation locally; do not abort existing response
        consumeEarlyStreamEntry(details, inputs);
        response = await earlyEntry.response;
      } else {
        // Determine if we should make a local request
        const { serviceName } = details;
        const shouldMakeLocalRequest =
          shouldUseLocalBackend() && (await isLocalService(serviceName));

        // Include the signal in the fetch call
        response = await makeChirpAvroCallWithFetch(
          chirpFetch,
          details,
          inputs,
          shouldMakeLocalRequest,
          abortController.signal
        );
      }

      await streamFn<ResponseUnion<Details>>(response, {
        onProgress: (data: ResponseUnion<Details>) => {
          if (data.type === 'internalError') {
            const internalErrorResponse = data;
            const chirpInternalError = new ChirpInternalError(
              'CHIRP RPC failed with a CHIRP internal error',
              { cause: internalErrorResponse.internalError }
            );
            spreadPropertiesIntoError(
              chirpInternalError,
              internalErrorResponse.internalError
            );
            syncStackError.cause = chirpInternalError;

            streamActive = false;
            if (promiseReject) promiseReject(syncStackError);
            return;
          }

          if (entryCallback && streamActive) {
            entryCallback(data as ResultUnion<Details>);
          }
        },
        onError: (error: Error) => {
          if (!streamActive) return; // Ignore errors after abort

          streamActive = false;
          const chirpInternalError = new ChirpInternalError(
            'CHIRP RPC failed with a CHIRP internal error',
            { cause: error }
          );

          syncStackError.cause = chirpInternalError;
          if (promiseReject) promiseReject(syncStackError);
        },
        onClose: () => {
          if (!streamActive) return; // Ignore close after abort

          streamActive = false;
          if (promiseResolve) promiseResolve();
        },
        signal: abortController.signal, // Pass the abort signal to the stream handler
      });
    } catch (error) {
      if (!streamActive) return; // Ignore errors after abort

      streamActive = false;
      const typedError =
        error instanceof Error ? error : new Error(String(error));
      const chirpInternalError = new ChirpInternalError(
        'CHIRP RPC failed with a CHIRP internal error',
        { cause: typedError }
      );
      syncStackError.cause = chirpInternalError;
      if (promiseReject) {
        promiseReject(syncStackError);
      }
    }
  }

  return handler;
}
