'use es6';

import { createStack } from 'hub-http';
import promiseClient from 'hub-http/adapters/promiseClient';
import * as core from 'hub-http/middlewares/core';
import { rewriteUrl } from 'hub-http/middlewares/debug';
import sidekickDefaults from 'hub-http-contrib/middlewares/sidekickDefaults';
import { immutableResponse } from 'hub-http-contrib/middlewares/immutable';
import {
  dynamicHubletEnvironmentUrl,
  customRetryOnError,
} from 'SignalsExtension/api/middleware';
import withFetch from 'SignalsExtension/api/withFetch';
import withPortalId from 'SignalsExtension/api/withPortalId';

export default promiseClient(
  createStack(
    core.services,
    withFetch,
    sidekickDefaults,
    core.jsonBody,
    core.httpsOnly,
    dynamicHubletEnvironmentUrl('app', 'hubspot'),
    withPortalId,
    core.withQuery,
    rewriteUrl,
    core.validateStatus,
    immutableResponse,
    customRetryOnError
  )
);
