'use es6';

import {
  GET_ACTION,
  POST_ACTION,
  PUT_ACTION,
  DELETE_ACTION,
  PATCH_ACTION,
  OPTIONS_ACTION,
  GET_WITH_RESPONSE_ACTION,
  POST_WITH_RESPONSE_ACTION,
  PUT_WITH_RESPONSE_ACTION,
  DELETE_WITH_RESPONSE_ACTION,
  PATCH_WITH_RESPONSE_ACTION,
  OPTIONS_WITH_RESPONSE_ACTION,
  apiProxy,
} from './ApiProxy';

import { createRpcClientV2 } from 'rpc-client-utils';

const HubApiHttpClientProxy = {
  get: (relativeUrl, options) => apiProxy(GET_ACTION, relativeUrl, options),
  post: (relativeUrl, options) => apiProxy(POST_ACTION, relativeUrl, options),
  put: (relativeUrl, options) => apiProxy(PUT_ACTION, relativeUrl, options),
  delete: (relativeUrl, options) =>
    apiProxy(DELETE_ACTION, relativeUrl, options),
  patch: (relativeUrl, options) => apiProxy(PATCH_ACTION, relativeUrl, options),
  options: (relativeUrl, options) =>
    apiProxy(OPTIONS_ACTION, relativeUrl, options),
  getWithResponse: (relativeUrl, options) =>
    apiProxy(GET_WITH_RESPONSE_ACTION, relativeUrl, options),
  postWithResponse: (relativeUrl, options) =>
    apiProxy(POST_WITH_RESPONSE_ACTION, relativeUrl, options),
  putWithResponse: (relativeUrl, options) =>
    apiProxy(PUT_WITH_RESPONSE_ACTION, relativeUrl, options),
  deleteWithResponse: (relativeUrl, options) =>
    apiProxy(DELETE_WITH_RESPONSE_ACTION, relativeUrl, options),
  patchWithResponse: (relativeUrl, options) =>
    apiProxy(PATCH_WITH_RESPONSE_ACTION, relativeUrl, options),
  optionsWithResponse: (relativeUrl, options) =>
    apiProxy(OPTIONS_WITH_RESPONSE_ACTION, relativeUrl, options),
};

export default HubApiHttpClientProxy;

export const hubApiRpcClient = createRpcClientV2({
  hubHttpClient: HubApiHttpClientProxy,
});
