'use es6';

let gates = [];

export const setGates = (newGates) => {
  gates = newGates;
};

export const clearGates = () => {
  gates = [];
};

// To check v3 gates, wrap the gate name with: { gate } from 'hub-http/gates';
export const userUngatedFor = (gateName) => {
  return gates.includes(gateName);
};
