// @ts-expect-error missing types
import chrome from 'SignalsExtension/globals/chrome';
import {
  sendToContentScripts,
  sendToPopup,
  // @ts-expect-error missing types
} from 'SignalsExtension/background/backgroundMessaging';
import {
  PERMISSION_CHECK_ORIGIN,
  PERMISSION_REQUEST_ORIGIN,
} from 'sales-ext-common/constants/backgroundMessagingMethods';
import { EverywhereWebsiteAllowlistValues } from 'sales-ext-common/constants/chromeStorage';
import { setEverywhereWebsiteAllowlist } from 'sales-ext-common/utils/everywhereWebsiteSettings';
import { LogInfo } from 'sales-clients-common/utils/reportError';

export const checkOriginPermissionHandler = (origin: string) => {
  return chrome.permissions
    .contains({
      origins: [origin],
    })
    .then((granted: boolean) => {
      sendToContentScripts(PERMISSION_CHECK_ORIGIN, {
        granted,
      });
    })
    .catch((err: Error) => {
      LogInfo({
        error: err,
        fingerprint: ['permissionHandler/checkOriginPermissionHandler'],
        tags: { source: 'chrome.permissions.contains' },
        extraData: {
          params: [
            {
              origins: [origin],
            },
          ],
        },
      });
    });
};

export const requestOriginPermissionHandler = async (permissionRequest: {
  origin: string;
  newOriginPermissions: EverywhereWebsiteAllowlistValues[];
}) => {
  const { origin, newOriginPermissions } = permissionRequest;

  const granted = await chrome.permissions.request({
    origins: [origin],
  });
  if (granted) {
    await setEverywhereWebsiteAllowlist(newOriginPermissions);
  }

  sendToPopup(PERMISSION_REQUEST_ORIGIN, {
    granted,
    origin,
  });

  // This is for any content scripts to update the permission state
  sendToContentScripts(PERMISSION_REQUEST_ORIGIN, {
    granted,
    origin,
  });
};
