import {
  CHROME_EXTENSION,
  GMAIL,
  WHERE_APP,
} from 'sales-clients-common/data/Analytics';
import events from 'SignalsExtension/events.yaml';
// @ts-expect-error untyped
import { SignalsExtensionUsageTracker } from '../utils/signalsExtensionUsageTracker';
import getExtensionVersion from 'sales-clients-common/utils/getExtensionVersion';

const baseTrackerProperties = {
  clientType: CHROME_EXTENSION,
  screen: GMAIL,
  namespace: WHERE_APP,
  fromSignalsExtension: true,
  extensionVersion: getExtensionVersion(),
};

export const initUnauthedTracker = () => {
  SignalsExtensionUsageTracker.initUnauthedTracker(
    events,
    baseTrackerProperties
  );
};

export const initAuthedTracker = (email: string, portalId: number) => {
  SignalsExtensionUsageTracker.initAuthedTracker(events, {
    ...baseTrackerProperties,
    email,
    hubId: portalId,
  });
};

export const UsageTracker = SignalsExtensionUsageTracker;
