'use es6';
/* eslint-disable  no-unused-vars */
import chrome from 'SignalsExtension/globals/chrome';

const getRest = (storage) => {
  const { setItem, getItem, removeItem, init, ...rest } = storage;
  return rest;
};
class LocalContainer {
  getItem(key) {
    return this[key];
  }
  setItem(key, value) {
    if (this[key] !== value) {
      this[key] = value;
      chrome.storage.local.set({ localStorage: getRest(this) });
    }
    return true;
  }
  removeItem(key) {
    delete this[key];
    chrome.storage.local.set({ localStorage: getRest(this) });
    return true;
  }
  init() {
    // Using the promise syntax for this breaks gmail
    chrome.storage.local.get(['localStorage'], (result) => {
      if (result && result.localStorage) {
        for (const key in result.localStorage) {
          if (key && key !== 'hubspot:crx::HUBLET') {
            this[key] = result.localStorage[key];
          }
        }
      }
    });
  }
}
class SessionContainer {
  getItem(key) {
    return this[key];
  }
  setItem(key, value) {
    if (this[key] !== value) {
      this[key] = value;
    }
    return true;
  }
  removeItem(key) {
    delete this[key];
    return true;
  }
}
class SyncContainer {
  getItem(key) {
    return this[key];
  }
  setItem(key, value) {
    if (this[key] !== value) {
      this[key] = value;
      chrome.storage.sync.set({ syncStorage: getRest(this) });
    }
    return true;
  }
  removeItem(key) {
    delete this[key];
    chrome.storage.sync.set({ syncStorage: getRest(this) });
    return true;
  }
  init() {
    // Using the promise syntax for this breaks gmail
    chrome.storage.sync.get(['syncStorage'], (result) => {
      if (result && result.syncStorage) {
        for (const key in result.syncStorage) {
          if (key && key !== 'hubspot:crx::HUBLET') {
            this[key] = result.syncStorage[key];
          }
        }
      }
    });
  }
}

const localData = new LocalContainer();
const sessionData = new SessionContainer();
const syncData = new SyncContainer();

const localHandlers = {
  set: (target, key, value) => {
    if (target[key] !== value) {
      target[key] = value;
      chrome.storage.local.set({ localStorage: getRest(target) });
    }
    return true;
  },
  get: (target, prop, receiver) => {
    const value = target[prop];

    if (value instanceof Function) {
      return function (...args) {
        return value.apply(this === receiver ? target : this, args);
      };
    }
    return value;
  },
  deleteProperty: (target, prop) => {
    if (prop in target) {
      delete target[prop];
      chrome.storage.local.set({ localStorage: getRest(target) });
    }
    return true;
  },
};
const sessionHandlers = {
  set: (target, key, value) => {
    if (target[key] !== value) {
      target[key] = value;
    }
    return true;
  },
  get: (target, prop, receiver) => {
    const value = target[prop];

    if (value instanceof Function) {
      return function (...args) {
        return value.apply(this === receiver ? target : this, args);
      };
    }

    return value;
  },
  deleteProperty: (target, prop) => {
    if (prop in target) {
      delete target[prop];
    }
    return true;
  },
};
const syncHandlers = {
  set: (target, key, value) => {
    if (target[key] !== value) {
      target[key] = value;
      chrome.storage.sync.set({ syncStorage: getRest(target) });
    }
    return true;
  },
  get: (target, prop, receiver) => {
    const value = target[prop];

    if (value instanceof Function) {
      return function (...args) {
        return value.apply(this === receiver ? target : this, args);
      };
    }
    return value;
  },
  deleteProperty: (target, prop) => {
    if (prop in target) {
      delete target[prop];
      chrome.storage.sync.set({ syncStorage: getRest(target) });
    }
    return true;
  },
};

export const localStorage = new Proxy(localData, localHandlers);
export const sessionStorage = new Proxy(sessionData, sessionHandlers);
export const syncStorage = new Proxy(syncData, syncHandlers);
