'use es6';

import { initUnauthedTracker } from '../utils/usageTracker';
import logger from 'SignalsExtension/utils/logger';

export default () => {
  // Startup anonymous analytics tracker.
  // Should quickly be reinitialized by accountManager with an email
  try {
    initUnauthedTracker();
  } catch (e) {
    logger.error(`initUnauthedTracker: ${e.message || e}`);
  }
};
