import { shouldRunEverywhereScript } from '../utils/tabs';
import { LogMessage } from 'sales-clients-common/utils/reportError';
import { Feature_Everywhere } from 'sales-ext-common/managers/featureToggles';
import { isFeatureEnabledForSigExt } from 'SignalsExtension/background/managers/featureToggleManager';
// @ts-expect-error untyped
import { chrome } from 'sales-clients-common/globals/window';
import logger from 'SignalsExtension/utils/logger';
import { Tab } from 'sales-clients-common/chrome/types';

let hasSetUpEverywhereListener = false;

export const injectEverywhereContentScript = async (tab: Tab) => {
  const tabId = tab.id;
  // Refetch to check for updates
  try {
    if (await shouldRunEverywhereScript(tab)) {
      chrome.scripting
        .executeScript({
          target: { tabId },
          files: ['js/everywhere.js'],
        })
        .catch((error: Error) => {
          LogMessage({
            error,
            fingerprint: ['setupEverywhereListener'],
            tags: { source: 'setupEverywhereListener' },
            stacktrace: true,
          });
        });
    }
  } catch (err) {
    logger.error(`Failed to load everywhere: ${err}`);
  }
};

export const setUpEverywhereListener = () => {
  if (
    isFeatureEnabledForSigExt(Feature_Everywhere) &&
    !hasSetUpEverywhereListener
  ) {
    hasSetUpEverywhereListener = true;
    chrome.tabs.onUpdated.addListener(
      async (tabId: number, changeInfo: { status: string }, tab: Tab) => {
        if (changeInfo.status === 'loading') {
          await injectEverywhereContentScript(tab);
        }
      }
    );
  }
};

export const applyEverywhereToAllTabs = async () => {
  const tabs = await chrome.tabs.query({});
  const injectionPromises = tabs.map(injectEverywhereContentScript);
  Promise.all(injectionPromises).catch((error: Error) => {
    LogMessage({
      error,
      fingerprint: ['applyEverywhereToAllTabs'],
      tags: { source: 'applyEverywhereToAllTabs' },
      stacktrace: true,
    });
  });
};

export const removeEverywhereFromAllTabs = () => {
  const message = { method: 'destroyContentScript' };
  chrome.tabs
    .query({})
    .then((tabs: Tab[]) => {
      tabs.forEach((tab) => {
        chrome.tabs.sendMessage(tab.id, message);
      });
    })
    .catch((error: Error) => {
      console.error('Failed while removing everywhere from all tabs: ', error);
    });
};
