/* hs-eslint ignored failing-rules */
/* eslint-disable prefer-promise-reject-errors */

'use es6';

import { fromJS } from 'immutable';
import { sendMessage } from 'sales-clients-common/chrome/safeRuntime';

export const API_PROXY_METHOD = 'apiProxyMethod';
export const GET_ACTION = 'get';
export const POST_ACTION = 'post';
export const PUT_ACTION = 'put';
export const DELETE_ACTION = 'delete';
export const PATCH_ACTION = 'patch';
export const OPTIONS_ACTION = 'options';
export const GET_WITH_RESPONSE_ACTION = 'getWithResponse';
export const POST_WITH_RESPONSE_ACTION = 'postWithResponse';
export const PUT_WITH_RESPONSE_ACTION = 'putWithResponse';
export const DELETE_WITH_RESPONSE_ACTION = 'deleteWithResponse';
export const PATCH_WITH_RESPONSE_ACTION = 'patchWithResponse';
export const OPTIONS_WITH_RESPONSE_ACTION = 'optionsWithResponse';

const ValidApiProxyActions = [
  GET_ACTION,
  POST_ACTION,
  PUT_ACTION,
  DELETE_ACTION,
  PATCH_ACTION,
  OPTIONS_ACTION,
  GET_WITH_RESPONSE_ACTION,
  POST_WITH_RESPONSE_ACTION,
  PUT_WITH_RESPONSE_ACTION,
  DELETE_WITH_RESPONSE_ACTION,
  PATCH_WITH_RESPONSE_ACTION,
  OPTIONS_WITH_RESPONSE_ACTION,
];

export const apiProxy = (
  action,
  relativeUrl,
  options,
  withoutCsrf = false,
  useImmutable = true
) => {
  if (!ValidApiProxyActions.includes(action)) {
    return Promise.reject('Invalid action');
  }
  return sendMessage({
    method: API_PROXY_METHOD,
    data: {
      action,
      relativeUrl,
      options,
      withoutCsrf,
    },
  })
    .then((response) => {
      if (response.success) {
        if (useImmutable) {
          return fromJS(response.data);
        } else {
          return response.data;
        }
      } else {
        const error = response.data;
        // Fix object corruption after going through chrome messaging.
        if (error.toString() === '[object Object]') {
          error.toString = () => error.message;
        }
        throw error;
      }
    })
    .catch((err) => {
      throw err;
    });
};
