'use es6';

import { LAST_LOGIN_PORTAL_ID_KEY } from 'sales-clients-common/constants/StorageKeys';
import { localGet } from 'sales-clients-common/chrome/safeStorage';

// Must call without portalId
export const fetchPickedPortalAndHublet = () =>
  localGet(LAST_LOGIN_PORTAL_ID_KEY).then(
    (data) => data[LAST_LOGIN_PORTAL_ID_KEY]
  );
