'use es6';

import logger from 'SignalsExtension/utils/logger';
import chrome from 'SignalsExtension/globals/chrome';
import { getPortalId } from 'SignalsExtension/data/PortalIdContainer';
import { getSessionMonitor } from 'SignalsExtension/data/sessionMonitorContainer';
import { NotificationCache } from 'SignalsExtension/background/notificationCache';
import { localSet } from 'sales-clients-common/chrome/safeStorage';
import { OPEN_POPUP_IN_SETTINGS } from 'sales-ext-common/constants/chromeStorage';
import {
  updateNewFeatureAlertState,
  updateNewFeatureTagState,
} from 'sales-clients-common/utils/storageStateUtils';
import { getActionNewBadgeDismissed } from 'sales-clients-common/utils/storageStateUtils';

const BADGE_COLOR = {
  NEW_FEATURE: '#00BDA5',
  NEW_FEATURE_TEXT: '#FFFFFF',
  DEFAULT: '#00000000',
};

const badgeState = {
  text: '',
  notificationCount: 0,
  color: BADGE_COLOR.DEFAULT,
  textColor: BADGE_COLOR.DEFAULT,
};

export const updateBadge = () => {
  // Update the Badge text to 'New' when logged in and hasn't been dismissed
  getActionNewBadgeDismissed()
    .then((dismissed) => {
      // Prioritize "new" badge before anything else
      if (dismissed === undefined) {
        badgeState.text = 'New';
        badgeState.color = BADGE_COLOR.NEW_FEATURE;
        badgeState.textColor = BADGE_COLOR.NEW_FEATURE_TEXT;

        // These feel out of place, but I'm not sure where to put them
        updateNewFeatureAlertState(true);
        updateNewFeatureTagState(true);
      } else if (badgeState.notificationCount > 0) {
        badgeState.text = badgeState.notificationCount.toString();
      }

      chrome.action.setBadgeText({ text: badgeState.text });
      chrome.action.setBadgeBackgroundColor({ color: badgeState.color });
      // Make sure the setBadgeTextColor is available before calling it
      if (chrome.action.setBadgeTextColor) {
        chrome.action.setBadgeTextColor({ color: badgeState.textColor });
      }
    })
    .catch((error) => {
      logger.error('Error getting new badge dismissed state', error);
      chrome.action.setBadgeText({ text: '' });
      chrome.action.setBadgeBackgroundColor({ color: BADGE_COLOR.DEFAULT });
      // Make sure the setBadgeTextColor is available before calling it
      if (chrome.action.setBadgeTextColor) {
        chrome.action.setBadgeTextColor({ color: BADGE_COLOR.DEFAULT });
      }
    });
};

// DO NOT REMOVE unless no longer needed in SERA
export const clearBadge = () => {
  badgeState.notificationCount = 0;
  badgeState.text = '';
  badgeState.color = BADGE_COLOR.DEFAULT;
  badgeState.textColor = BADGE_COLOR.DEFAULT;
  NotificationCache.clear();
  updateBadge();
};

export const openPopupSupported = !!chrome.action.openPopup;

// Open the popup and navigate to the settings tab
export const openPopup = (openSettings = false) => {
  try {
    if (openSettings) {
      localSet({ [OPEN_POPUP_IN_SETTINGS]: true });
    }
    chrome.action.openPopup();
  } catch (e) {
    logger.debug('Error opening popup', e);
  }
};

// DO NOT REMOVE unless no longer needed in SERA
export const updateIcon = () => {
  const isLoggedIn = getSessionMonitor().isLoggedIn();
  logger.debug(`Updating Browser Action Icon, loggedIn: ${isLoggedIn}`);

  const image = {};
  if (!isLoggedIn || !getPortalId()) {
    image.path = {
      19: chrome.runtime.getURL('img/hubspot-logo-bg-19-grey.png'),
      38: chrome.runtime.getURL('img/hubspot-logo-bg-48-grey.png'),
    };
  } else {
    image.path = {
      19: chrome.runtime.getURL('img/hubspot-logo-bg-19.png'),
      38: chrome.runtime.getURL('img/hubspot-logo-bg-48.png'),
    };
  }
  if (chrome && chrome.action && image.path) {
    chrome.action.setIcon(image);
    updateBadge();
  }
};

export const incrementBadge = (id = '') => {
  if (id) {
    if (NotificationCache.has(id)) {
      return;
    }

    NotificationCache.add(id);
  }

  badgeState.notificationCount++;
  updateBadge();
};

// We are only updating the text here, if needed we can add more properties at later point
// including the color of the badge and the text color dynamically
// Here we are always using the white color for the text and green color for the badge
export const updateBadgeText = ({ text = '' }) => {
  badgeState.text = text;
  badgeState.color = BADGE_COLOR.NEW_FEATURE;
  badgeState.textColor = BADGE_COLOR.NEW_FEATURE_TEXT;

  updateBadge();
};
