'use es6';

import { sendToContentScripts } from 'SignalsExtension/background/backgroundMessaging';
import { restartExtension } from '../utils/tabs';
import { LogMessage } from 'sales-clients-common/utils/reportError';
import PortalIdParser from 'PortalIdParser';

export const RESTART_DELAY = 1000;
const PORTAL_SWITCH_NOTIFICATION_TIMEOUT = 2000;

let portalId = null;

// DO NOT REMOVE unless no longer needed in SERA
export const getPortalId = () => {
  return portalId;
};

export const setPortalId = (value) => {
  PortalIdParser.id = value;
  portalId = value;
};

export const updatePickedPortal = (pickedPortalId) => {
  sendToContentScripts('message', {
    sender: 'SIG_EXTENSION',
    type: 'PICKED_PORTAL_CHANGED',
    value: pickedPortalId,
  });
  setPortalId(pickedPortalId);

  // Adds a delay to allow the success screen to show up in Gmail
  setTimeout(() => {
    restartExtension();
  }, RESTART_DELAY);
};

export const portalSwitchHandler = (request) => {
  return setTimeout(() => {
    LogMessage({
      message:
        'UserContextActions: SignalsExtension did not receive notification to restart',
    });
    updatePickedPortal(request);
  }, PORTAL_SWITCH_NOTIFICATION_TIMEOUT);
};

export const portalSwitchFromPopupHandler = () => {
  setTimeout(() => {
    restartExtension();
  }, RESTART_DELAY);
};
