/* hs-eslint ignored failing-rules */
/* eslint-disable promise/catch-or-return */

'use es6';

import { sendToContentScripts } from 'SignalsExtension/background/backgroundMessaging';
import { getInstalledExtensions } from 'SignalsExtension/background/extensionChecker';
import { getDoNotDisturb } from 'SignalsExtension/background/models/attributes';
import { getSessionId } from 'SignalsExtension/init/PerformanceTelemetry';

export const sendDebugLogHandler = () => {
  getInstalledExtensions().then((extensions) => {
    const debugLogData = {
      notification: !getDoNotDisturb(),
      installedExtensions: extensions,
      sessionId: getSessionId(),
    };
    sendToContentScripts('sendDebugLog', {
      debugLogData,
    });
  });
  return null;
};
