import { syncSet, syncGet } from '../chrome/safeStorage';

export const setCooldown = (syncKey: string) =>
  syncSet({ [syncKey]: Date.now() });

export const hasCooldownElapsed = (
  syncKey: string,
  cooldownDurationMs: number
) =>
  syncGet(syncKey).then((items: Record<string, number | string>) => {
    if (typeof items[syncKey] !== 'number') {
      return true;
    } else {
      const cooldownStartMs = new Date(items[syncKey]).getTime();
      return Date.now() > cooldownStartMs + cooldownDurationMs;
    }
  });
