'use es6';

import NoAuthHttpClient from 'SignalsExtension/api/NoAuthHttpClient';

const URL_PROTOCOL_PATTERN = /^(https?|ftp):\/\//i;

export const noAuthApiProxyMethodHandler = (requestData, sendResponse) => {
  try {
    // Filter out url protocol so we only execute under HubSpot domain
    if (URL_PROTOCOL_PATTERN.test(requestData.relativeUrl)) {
      sendResponse({
        success: false,
        data: { message: 'Invalid param: relativeUrl' },
      });
    } else {
      NoAuthHttpClient[requestData.action](
        requestData.relativeUrl,
        requestData.options
      )
        .then((response) => {
          return sendResponse({ success: true, data: response });
        })
        .catch((err) => {
          return sendResponse({ success: false, data: err });
        });
      return true;
    }
  } catch (ex) {
    sendResponse({
      success: false,
      data: { message: `Error processing request: ${ex}` },
    });
  }
  return null;
};
