/* eslint-disable  hubspot-dev/no-confusing-browser-globals, no-unused-vars, hubspot-dev/prefer-hubspot-url-utils */
/* global G_BUILD_ENVIRONMENT */

'use es6';
import { localStorage, sessionStorage, syncStorage } from './localStorage';
import { XMLHttpRequestWithFetch } from 'SignalsExtension/utils/XMLHttpRequestWithFetch';
// Manifest 3 notes
// self is the globalState var
// Packages like enviro require access to window objects like document
// By placing this file as the first import, we can mock the window objects
// needed in other packages before any other packages are loaded.

const monitoringMocks = {
  //Monitoring tools for raven
  addEventListener: self.addEventListener.bind(self),
  removeEventListener: self.removeEventListener.bind(self),
  dispatchEvent: self.dispatchEvent.bind(self),
  createEvent: (eventArg) => {
    const event = new self.Event(eventArg, {
      bubbles: true,
      cancelable: true,
    });
    event.initEvent = (eventType, bubbles, cancelable) =>
      new self.Event(eventType, { bubbles, cancelable });

    return event;
  },
};

export const initSelf = () => {
  self.document = {
    location: {
      hostname: 'app.hubspot.com',
      host: 'app.hubspot.com',
    },
    ...monitoringMocks,
  };

  if (G_BUILD_ENVIRONMENT === 'qa') {
    self.document.location = {
      hostname: 'app.hubspotqa.com',
      host: 'app.hubspotqa.com',
    };
  }

  self.window = {
    ...self.window,
    chrome: self.chrome,
    document: self.document,
    sessionStorage,
    localStorage,
    syncStorage,
    Promise: self.Promise,
    // required for monitoring tools
    XMLHttpRequest: XMLHttpRequestWithFetch,
    addEventListener: self.addEventListener.bind(self),
    removeEventListener: self.removeEventListener.bind(self),
  };

  // required for monitoring tools
  self.XMLHttpRequest = XMLHttpRequestWithFetch;

  // Load localStorage from chrome storage, w/o portalId
  self.window.localStorage.init();
  // Load syncStorage from chrome storage
  self.window.syncStorage.init();
};
initSelf();
