import quickFetch from 'quick-fetch';
import { getAvroGateway, parseRejectingChirpErrors } from './chirp-avro';
import {
  ChirpAvroRpcDetails,
  ChirpAvroRpcEarlyRequestDetails,
  ChirpAvroRpcInputs,
  ChirpAvroRpcResult,
} from './types';
import { createRequestName } from './quick-fetch-utils';

/**
 * Makes a chirp early request using quick-fetch.
 *
 * This REQUIRES the details object from `/earlyRequest/{SERVICE}.early.ts for tree shaking purposes.
 *
 * To receive this data, use the normal details object in a call. If the data is ready, it will be returtned.
 */
export const makeChirpEarlyRequest = <
  Details extends ChirpAvroRpcEarlyRequestDetails<
    any,
    any,
    any,
    false | undefined
  >,
>(
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>
): Promise<ChirpAvroRpcResult<Details>> => {
  const { request } = inputs;
  const { auth, serviceName, rpcName, fingerprint } = details;
  const { earlyRequest: __earlyRequest, ...detailsCopyWithoutEarlyRequest } =
    details;
  const requestName = createRequestName(
    detailsCopyWithoutEarlyRequest,
    request
  );

  const endpointUrl = `${quickFetch.getApiUrl(
    getAvroGateway({ auth, timeout: 5000 })
  )}/${serviceName}/${rpcName}`;

  return new Promise((resolve, reject) => {
    quickFetch.makeEarlyRequest(requestName, {
      type: 'POST',
      data: JSON.stringify(request ?? {}),
      extraHeaders: [{ header: 'x-hs-fingerprint', value: fingerprint }],
      url: endpointUrl,
      timeout: 5000,
      whenFinished: (response: string) => {
        try {
          const parsed = JSON.parse(response);
          const result = parseRejectingChirpErrors(parsed);
          resolve(result);
        } catch (e) {
          reject(e);
        }
      },
      onError: (xhr, __errorMessage) => {
        try {
          const parsed = JSON.parse(xhr.responseText);
          parseRejectingChirpErrors(parsed);
        } catch (e) {
          reject(e);
        }
      },
    });
  });
};

export const getChirpEarlyRequestName = <
  Details extends ChirpAvroRpcEarlyRequestDetails<any, any, any, any>,
>(
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>
) => {
  const request = inputs?.request ?? {};
  return createRequestName(details, request);
};

export const getChirpEarlyRequest = ({
  details,
  requestObject,
}: {
  details: ChirpAvroRpcDetails<any, any, any, any>;
  requestObject?: ChirpAvroRpcInputs<ChirpAvroRpcDetails<any, any, any, any>>;
}) => {
  const request = requestObject?.request ?? {};
  const requestName = createRequestName(details, request);

  const requestState = quickFetch.getRequestStateByName(requestName);
  if (requestState && !requestState.error) {
    return new Promise((resolve, reject) => {
      requestState.whenFinished((response: any) => {
        quickFetch.removeEarlyRequest(requestName);
        // Quick fetch responses are always strings
        resolve(JSON.parse(response));
      });
      requestState.onError((xhr, errorMessage) => {
        quickFetch.removeEarlyRequest(requestName);
        try {
          const parsed = JSON.parse(xhr.responseText);
          if (
            typeof parsed === 'object' &&
            (parsed.type === 'rpcError' || parsed.type === 'internalError')
          ) {
            resolve(parsed);
          } else {
            reject(errorMessage);
          }
        } catch (e) {
          reject(errorMessage);
        }
      });
    });
  }
  return undefined;
};
