export type EndpointConfigurationOptions = {
  __environment?: 'qa' | 'prod';
  // Using string in place of `'na1' | 'na2' | 'na3' | 'eu1' | 'ap1' | 'test2';` to be more open
  __hublet?: string;
};

type HubHttpOptions = {
  data?: unknown;
  headers?: Record<string, string>;
  query?: Record<string, string>;
  timeout?: number;
  environment?: EndpointConfigurationOptions['__environment'];
  hublet?: EndpointConfigurationOptions['__hublet'];
  portalId?: number;
};

type HubHttpMethod = (
  url: string,
  options?: HubHttpOptions
) => Promise<unknown>;

type AxiosOptions = {
  headers?: Record<string, string>;
  validateStatus?: (status: number) => boolean;
  timeout?: number;
  params?: Record<string, string>;
};

type AxiosMethod = (
  url: string,
  options: AxiosOptions
) => Promise<{ data: unknown }>;

type AxiosMethodWithData = (
  url: string,
  data: unknown,
  options: AxiosOptions
) => Promise<{ data: unknown }>;

export type HubHttpClient = {
  get: HubHttpMethod;
  post: HubHttpMethod;
  delete: HubHttpMethod;
  put: HubHttpMethod;
  patch: HubHttpMethod;
};
export type AxiosClient = {
  get: AxiosMethod;
  delete: AxiosMethod;
  post: AxiosMethodWithData;
  put: AxiosMethodWithData;
  patch: AxiosMethodWithData;
};
export type PostOnlyHubHttpClient = Pick<HubHttpClient, 'post'>;
/**
 * An Object with type annotations containing all the network and type
 * information needed to model an http request as an RPC. See
 * `createHttpRpcClientV1` for more details.
 */
export interface HttpRpcDetails<
  __RequestBody,
  __PathParameters,
  __QueryParameters,
  __Result,
> {
  method: 'get' | 'post' | 'delete' | 'put' | 'patch';
  baseUrl: string;
  pathParameters?: string[];
  pathSegments: (string | undefined)[];
}

export interface ChirpAvroRpcDetails<
  __Parameters,
  __Result,
  __Error,
  __IsStreaming extends boolean | undefined = undefined | false,
> {
  method?: undefined;
  serviceName: string;
  rpcName: string;
  auth: string;
  fingerprint: string;
  isStreaming?: __IsStreaming;
  // TODO: This is mandatory, optional to pass builds for now
  na1Only?: boolean;
  isHublessAppAuth?: boolean;
  isLocal?: boolean;
}

export interface ChirpAvroRpcEarlyRequestDetails<
  __Parameters,
  __Result,
  __Error,
  __IsStreaming extends boolean | undefined = undefined | false,
> extends ChirpAvroRpcDetails<__Parameters, __Result, __Error, __IsStreaming> {
  earlyRequest: true;
}

/**
 * Represents a handler for streaming RPC responses.
 * Extends Promise to allow for both streaming callbacks and traditional await pattern.
 * When the stream completes successfully, the Promise resolves.
 * When there's an error, the Promise rejects with the error.
 *
 * Usage:
 * - Use .onMessage() to process data chunks as they arrive
 * - Use .then() to handle stream completion (instead of onClose)
 * - Use .catch() to handle transport errors (instead of onError)
 * - Use .abort() to stop the stream early
 * - Domain errors (from the RPC) come through the data stream as results with type: 'rpcError'
 */
export type StreamHandler<T> = Promise<void> & {
  /**
   * Register a callback to handle each chunk of data and domain errors in the stream.
   * The callback receives data chunks with type 'data' or domain errors with type 'rpcError'.
   * @param callback Function to execute for each data chunk
   * @returns The StreamHandler for chaining
   */
  onMessage: (callback: (data: T) => void) => StreamHandler<T>;

  /**
   * Abort the stream.
   * This will stop receiving new data, clean up resources, and resolve the promise.
   */
  abort: () => void;
};

type _RequiredKeys<T> = {
  [K in keyof T]-?: {} extends Pick<T, K> ? never : K;
}[keyof T];

type _AllPropsOptional<T> = T extends object
  ? _RequiredKeys<T> extends never
    ? true
    : false
  : false;

/**
 * The path parameters, query parameters, and request body for an RPC request
 * are passed as object properties to the RPC client function. This object is
 * relatively simple, but its type is highly overloaded depending on which
 * pieces of data are required for a given RPC. The complexity of this type is
 * mostly to do with determining whether each of the three properties on this
 * type should be required given the RPC being executed. Most of the time
 * you can think of this type as:
 *
 * ```
 * interface RpcInputs<RequestBody, PathParameters, QueryParameters> {
 *   data?: RequestBody,
 *   pathParameters?: PathParameters,
 *   queryParameters?: QueryParameters
 * }
 * ```
 */
export type HttpRpcInputs<Details extends HttpRpcDetails<any, any, any, any>> =
  Details extends HttpRpcDetails<
    infer RequestBody,
    infer PathParameters,
    infer QueryParameters,
    any
  >
    ? (undefined extends RequestBody
        ? { data?: RequestBody }
        : { data: RequestBody }) &
        (PathParameters extends undefined | Record<string, never>
          ? { pathParameters?: PathParameters }
          : { pathParameters: PathParameters }) &
        (QueryParameters extends undefined | Record<string, never>
          ? { queryParameters?: QueryParameters }
          : _AllPropsOptional<QueryParameters> extends true
          ? { queryParameters?: QueryParameters }
          : { queryParameters: QueryParameters })
    : never;

export type ChirpAvroRpcInputs<
  Details extends
    | ChirpAvroRpcDetails<any, any, any, any>
    | ChirpAvroRpcEarlyRequestDetails<any, any, any>,
> = Details extends
  | ChirpAvroRpcDetails<infer Parameters, any, any, any>
  | ChirpAvroRpcEarlyRequestDetails<infer Parameters, any, any>
  ? {
      timeout?: number;
      portalId?: number;
      userId?: number;
      environment?: EndpointConfigurationOptions['__environment'];
      hublet?: EndpointConfigurationOptions['__hublet'];
    } & (undefined extends Parameters
      ? { request?: Parameters }
      : { request: Parameters })
  : never;

export type RpcInputs<
  Details extends
    | HttpRpcDetails<any, any, any, any>
    | ChirpAvroRpcDetails<any, any, any, any>
    | ChirpAvroRpcEarlyRequestDetails<any, any, any>,
> = Details extends HttpRpcDetails<any, any, any, any>
  ? HttpRpcInputs<Details>
  : Details extends
      | ChirpAvroRpcDetails<any, any, any, any>
      | ChirpAvroRpcEarlyRequestDetails<any, any, any>
  ? ChirpAvroRpcInputs<Details>
  : never;

export type HttpRpcResult<Details extends HttpRpcDetails<any, any, any, any>> =
  Details extends HttpRpcDetails<any, any, any, infer Response>
    ? Response
    : never;

export type ChirpAvroRpcResult<
  Details extends
    | ChirpAvroRpcDetails<any, any, any, any>
    | ChirpAvroRpcEarlyRequestDetails<any, any, any>,
> = Details extends
  | ChirpAvroRpcDetails<any, infer Result, any, any>
  | ChirpAvroRpcEarlyRequestDetails<any, infer Result, any>
  ? Result
  : never;

export type RpcResult<
  Details extends
    | HttpRpcDetails<any, any, any, any>
    | ChirpAvroRpcDetails<any, any, any, any>
    | ChirpAvroRpcEarlyRequestDetails<any, any, any>,
> = Details extends HttpRpcDetails<any, any, any, any>
  ? HttpRpcResult<Details>
  : Details extends
      | ChirpAvroRpcDetails<any, any, any, any>
      | ChirpAvroRpcEarlyRequestDetails<any, any, any>
  ? ChirpAvroRpcResult<Details>
  : never;

export type ChirpAvroRpcError<
  Details extends
    | ChirpAvroRpcDetails<any, any, any, any>
    | ChirpAvroRpcEarlyRequestDetails<any, any, any>,
> = Details extends
  | ChirpAvroRpcDetails<any, any, infer Error, any>
  | ChirpAvroRpcEarlyRequestDetails<any, any, infer Error>
  ? Error
  : never;

const _unhandledEnumCase = Symbol('unhandled-enum-case');
/**
 * `unhandledEnumCase` is an opaque type brand representing values which could
 * later be added to an enum. You can access the underlying `string` type of
 * unhandled enum cases using the `readUnhandledEnumCase` utility function. The
 * presence of this type is intended to force consumers to handle enum expansion
 * gracefully. This guardrail is massively beneficial to backend engineers
 * because it means enum expansion doesn't count as a breaking change from a
 * schema evolution perspective.
 */
export type unhandledEnumCase = typeof _unhandledEnumCase;

export function readUnhandledEnumCase(
  unhandledEnumCase: string | unhandledEnumCase
): string {
  if (typeof unhandledEnumCase === 'string') {
    return unhandledEnumCase;
  }
  // this should never happen. The type unhandledEnumCase should always at
  // runtime be a string. We don't support non-string enums.
  throw new Error('unhandledEnumCase had unexpected non-string type');
}

export const HUBSPOT_CORRELATION_ID_KEY = '__correlationId';

export type HubspotMetadata = {
  [HUBSPOT_CORRELATION_ID_KEY]?: string;
};
