'use es6';
/* eslint-disable hubspot-dev/no-async-await,  hubspot-dev/no-confusing-browser-globals */

import initEnviro from '../shims/initEnviro-sw';
import enviro from 'SignalsExtension/shims/enviro';
import { initDebugLog } from 'SignalsExtension/background/managers/debugLogManager';
import { initCsrfCookieMonitor } from 'SignalsExtension/background/csrfCookieMonitor';
import { fetchPickedPortalAndHublet } from 'SignalsExtension/api/SalesClientInitApi';
import initAnonymousTracker from 'SignalsExtension/background/initAnonymousTracker';
import { initFirstRunAndLoginTracker } from 'SignalsExtension/data/firstRunTracker';
import accountManager from 'SignalsExtension/background/managers/accountManager';
import * as browserAction from 'SignalsExtension/background/browserAction';
import * as attributes from 'SignalsExtension/background/models/attributes';
import logger from 'SignalsExtension/utils/logger';
import { LogError } from 'sales-clients-common/utils/reportError';
import { initPerformanceTelemetry } from 'SignalsExtension/init/PerformanceTelemetry';
import { isFeatureEnabledForSigExt } from 'SignalsExtension/background/managers/featureToggleManager';
import { Feature_PerformanceTelemetry } from 'sales-ext-common/managers/featureToggles';

export const setupPerformanceTelemetry = (portalId) => {
  if (isFeatureEnabledForSigExt(Feature_PerformanceTelemetry) && portalId) {
    logger.debug('[initServiceWorker]: setupPerformanceTelemetry', portalId);
    initPerformanceTelemetry(portalId);
  }
};

export const initExtension = async (reloadGmail = false) => {
  logger.debug('[BACKGROUND SW]: start service worker init');
  try {
    let pickedPortal = null;
    let hublet = null;

    initDebugLog();
    await initEnviro();
    // Wait for the csrf token to be fetched. This ensures account initialization has the CSRF
    const hasCsrfCookie = await initCsrfCookieMonitor();

    if (hasCsrfCookie) {
      try {
        const pickedPortalAndHublet = await fetchPickedPortalAndHublet();
        pickedPortal = pickedPortalAndHublet.portal_id;
        hublet = pickedPortalAndHublet.hublet;
        enviro.setHublet(hublet);
        logger.debug(
          'initExtension - fetchPickedPortalAndHublet',
          pickedPortalAndHublet
        );
      } catch (error) {
        logger.error(`initExtension - has CsrfCookie`, error);
      }
    } else {
      logger.debug('initExtension - has no CsrfCookie');
    }

    initAnonymousTracker();
    initFirstRunAndLoginTracker();
    setupPerformanceTelemetry(pickedPortal);

    const sessionMonitor = await accountManager.init(
      reloadGmail,
      hasCsrfCookie
    );

    self.window.hubspot.resolvePopupServices = {
      attributes,
      browserAction,
      hublet,
      portalId: pickedPortal,
      sessionMonitor,
      hasCsrfCookie,
    };

    self.window.sessionStorage.serviceWorkerActive = true;
    logger.log('[Background SW]: Service worker init complete');
  } catch (error) {
    LogError({
      error,
      fingerprint: ['initExtension'],
      tags: { source: 'initExtension' },
      stacktrace: true,
    });
  }
};
