// @ts-expect-error missing types
import { userUngatedFor } from 'SignalsExtension/data/salesGates';
import {
  initFeatureToggles,
  isFeatureEnabled,
} from 'sales-ext-common/managers/featureToggles';
// @ts-expect-error missing types
import HubApiWithoutImmutableHttpClient from 'SignalsExtension/api/HubApiWithoutImmutableHttpClient';

export const initFeatureToggleManager = () =>
  initFeatureToggles(HubApiWithoutImmutableHttpClient);

// To minimize error path impact, feature override returns true by default, unless disabled explicitly.
export const isFeatureEnabledForSigExt = (featureName: string) => {
  return isFeatureEnabled(featureName, userUngatedFor);
};
