// @ts-expect-error untyped
import { parse } from 'graphql';
// @ts-expect-error untyped
import { createApolloClient } from 'hubspot-apollo-client';
import { GraphQLHttpStack } from 'SignalsExtension/api/GraphQLHttpClient';
import { FetchPolicy } from '@apollo/client';

const apolloClient = createApolloClient({
  connectToDevTools: false,
  httpStack: GraphQLHttpStack,
});

interface RequestData {
  action: 'GRAPHQL_QUERY' | 'GRAPHQL_MUTATION';
  payload: {
    query: string;
    variables?: Record<string, any>;
    fetchPolicy?: FetchPolicy;
  };
}

interface SendResponse {
  (response: { success: boolean; data: any }): void;
}

export const clearApolloClientCache = () => {
  apolloClient.resetStore().catch((err) => {
    console.error('Error resetting Apollo Client cache: ', err);
  });
};

export const graphQLProxyMethodHandler = (
  requestData: RequestData,
  sendResponse: SendResponse
) => {
  try {
    const { action, payload } = requestData;

    if (action === 'GRAPHQL_QUERY' || action === 'GRAPHQL_MUTATION') {
      const { query, variables, fetchPolicy } = payload;

      const gqlQuery = typeof query === 'string' ? parse(query) : query;
      const operation =
        action === 'GRAPHQL_QUERY'
          ? apolloClient.query({
              query: gqlQuery,
              variables,
              fetchPolicy: fetchPolicy || 'no-cache',
            })
          : apolloClient.mutate({
              mutation: gqlQuery,
              variables,
              fetchPolicy: 'no-cache',
            });

      operation
        .then((response) => {
          sendResponse({
            success: true,
            data: response.data,
          });
        })
        .catch((err) => {
          console.error('GraphQL Query: ', err);
          sendResponse({
            success: false,
            data: err,
          });
        });
    }
  } catch (ex) {
    console.error('GraphQL Query: ', ex);
    sendResponse({
      success: false,
      data: { message: `Error processing request: ${ex}` },
    });
  }
  return true;
};
