import { isPrerendering } from './client/browserHelper';
import * as helpers from './common/helpers';
import type { ExactWithoutOptional } from './common/types';
import type { ClientDependencies } from './schemas';
import * as storageKeys from './storageKeys';

const SESSION_LENGTH_IN_MILLISECONDS = 1000 * 60 * 30; // 30 minutes

const OPERATING_SYSTEMS = [
  {
    name: 'windows 10',
    pattern: /(Windows 10.0|Windows NT 10.0)/,
  },
  {
    name: 'windows 8',
    pattern: /(Windows 8|Windows8.1|Windows NT 6.2|Windows NT 6.3)/,
  },
  {
    name: 'windows 7',
    pattern: /(Windows 7|Windows NT 6.1)/,
  },
  {
    name: 'windows vista',
    pattern: /Windows NT 6.0/,
  },
  {
    name: 'windows xp',
    pattern: /(Windows NT 5.1|Windows XP)/,
  },
  {
    name: 'android',
    pattern: /Android/,
  },
  {
    name: 'linux',
    pattern: /(Linux|X11)/,
  },
  {
    name: 'ios',
    pattern: /(iPhone|iPad|iPod)/,
  },
  {
    name: 'mac',
    pattern: /Mac OS X|MacPPC|MacIntel|Mac_PowerPC|Macintosh/,
  },
];

export type HAmplitudeProperties = {
  device_id: string;
  last_event_id: number;
  last_sequence_number: number;
  last_timestamp_checked_against_session: number;
  session_id: number;
};

const getDefaultHamplitudeProperties = (
  time: number
): HAmplitudeProperties => ({
  device_id: helpers.makeUuid(),
  last_event_id: 0,
  last_sequence_number: 0,
  last_timestamp_checked_against_session: time,
  session_id: time,
});

export const getHamplitudeProperties = (
  getTempStorage: ClientDependencies['getTempStorage']
): HAmplitudeProperties | null => {
  const storageProperties = getTempStorage(storageKeys.hamplitudeKey);

  if (storageProperties) {
    try {
      return JSON.parse(storageProperties);
    } catch (err) {
      // noop
    }
  }

  return null;
};

const setHamplitudeProperties = (
  setTempStorage: ClientDependencies['setTempStorage'],
  hamplitudeProperties: HAmplitudeProperties
) =>
  setTempStorage(
    storageKeys.hamplitudeKey,
    JSON.stringify(hamplitudeProperties)
  );

type HamplitudePropertiesDependencies = {
  currentTime: number;
  deviceId?: string;
};

export const refreshHamplitudeProperties = (
  hamplitudeProperties: HAmplitudeProperties,
  { currentTime, deviceId }: HamplitudePropertiesDependencies
) => {
  const {
    device_id,
    last_event_id = 0,
    last_sequence_number = 0,
    last_timestamp_checked_against_session = currentTime,
    session_id = currentTime,
  } = hamplitudeProperties;

  const refreshed = {
    device_id,
    last_event_id,
    last_sequence_number,
    last_timestamp_checked_against_session,
    session_id,
  };

  // If the current deviceId is different from the stored one
  // then we reset the Session Properties and provide new ones
  if (deviceId && deviceId !== device_id) {
    const resettedProperties = getDefaultHamplitudeProperties(currentTime);

    resettedProperties.device_id = deviceId;

    return resettedProperties;
  }

  const timeSinceSessionRefresh =
    currentTime - last_timestamp_checked_against_session;

  if (timeSinceSessionRefresh > SESSION_LENGTH_IN_MILLISECONDS) {
    refreshed.last_event_id = 0;
    refreshed.session_id = currentTime;
  }

  // @NOTE: These values are not reliable anymore due to getMetaProperties()
  // being called within `defaultTrackers` meaning it will keep incrementing
  // the values on each call, even if no `.track()` is called.
  refreshed.last_event_id = last_event_id + 1;
  refreshed.last_sequence_number = last_sequence_number + 1;

  refreshed.last_timestamp_checked_against_session = currentTime;

  return refreshed;
};

export const getAndRefreshHamplitudeProperties = (
  getTempStorage: ClientDependencies['getTempStorage'],
  setTempStorage: ClientDependencies['setTempStorage'],
  { currentTime, deviceId }: HamplitudePropertiesDependencies
) => {
  const currentHamplitudeProperties =
    getHamplitudeProperties(getTempStorage) ||
    getDefaultHamplitudeProperties(currentTime);

  const hamplitudeProperties = refreshHamplitudeProperties(
    currentHamplitudeProperties,
    { currentTime, deviceId }
  );

  setHamplitudeProperties(setTempStorage, hamplitudeProperties);

  return hamplitudeProperties;
};

const lookupOperatingSystem = (fingerprint: string) => {
  const match = OPERATING_SYSTEMS.find(({ pattern }) =>
    pattern.test(fingerprint)
  );

  return match ? match.name : 'unknown';
};

type MetaPropertiesDependencies = Pick<
  ExactWithoutOptional<ClientDependencies>,
  | 'clientName'
  | 'getCurrentHref'
  | 'getNetworkSpeed'
  | 'getNetworkType'
  | 'getTempStorage'
  | 'setTempStorage'
  | 'getReferrer'
  | 'getUserAgent'
  | 'getScreenWidth'
  | 'getScreenHeight'
  | 'getWindowWidth'
  | 'getWindowHeight'
  | 'getDeployableName'
  | 'getDeployableVersion'
  | 'getHubSpotSessionId'
>;

// These are Properties always sent on the Event Payload
// and that have dynamic values that are recalculated on each `.track` call
export type DynamicProperties = HAmplitudeProperties & {
  timestamp: number;
  currentPageUrl: string;
  networkType: string;
  networkSpeed: string;
  prerendering: boolean;
};

export const getDynamicMetaProperties = ({
  deviceId,
  getCurrentHref,
  getNetworkType,
  getNetworkSpeed,
  getTempStorage,
  setTempStorage,
}: MetaPropertiesDependencies & { deviceId?: string }) => {
  const currentTime = Date.now();

  const hamplitudeProperties = getAndRefreshHamplitudeProperties(
    getTempStorage,
    setTempStorage,
    { currentTime, deviceId }
  );

  const dynamicProperties: DynamicProperties = {
    ...hamplitudeProperties,
    timestamp: currentTime,
    currentPageUrl: '',
    networkType: '',
    networkSpeed: '',
    prerendering: false,
  };

  dynamicProperties.prerendering = isPrerendering();
  dynamicProperties.currentPageUrl = helpers.truncate(getCurrentHref(), 256);
  dynamicProperties.networkType = getNetworkType();
  dynamicProperties.networkSpeed = getNetworkSpeed();

  return dynamicProperties;
};

// These are Properties always sent on the Event Payload
// and that have static value (properties defined when the client is created)
export type StaticProperties = {
  windowWidth: number;
  windowHeight: number;
  screenWidth: number;
  screenHeight: number;
  screenSize: string;
  lastPageUrl: string;
  howOsDetailed: string;
  singlePageAppSessionId: number;
  trackingClient: string | 'custom';
  deployableName: string;
  deployableVersion: string;
  hubspotSessionId: string;
};

export const getStaticMetaProperties = ({
  clientName,
  getReferrer,
  getUserAgent,
  getScreenWidth,
  getScreenHeight,
  getWindowWidth,
  getWindowHeight,
  getDeployableName,
  getDeployableVersion,
  getHubSpotSessionId,
}: MetaPropertiesDependencies) => {
  const staticProperties: StaticProperties = {
    windowWidth: -1,
    windowHeight: -1,
    screenWidth: -1,
    screenHeight: -1,
    screenSize: '',
    lastPageUrl: '',
    howOsDetailed: '',
    singlePageAppSessionId: Date.now(),
    trackingClient: clientName || 'custom',
    deployableName: '',
    deployableVersion: '',
    hubspotSessionId: '',
  };

  staticProperties.windowWidth = getWindowWidth();
  staticProperties.windowHeight = getWindowHeight();
  staticProperties.deployableName = getDeployableName();
  staticProperties.deployableVersion = getDeployableVersion();
  staticProperties.hubspotSessionId = getHubSpotSessionId();

  staticProperties.howOsDetailed = lookupOperatingSystem(
    helpers.between(getUserAgent(), '(', ')')
  );

  staticProperties.screenWidth = getScreenWidth();
  staticProperties.screenHeight = getScreenHeight();
  staticProperties.screenSize = helpers.getScreenWidthSize(getScreenWidth());
  staticProperties.lastPageUrl = helpers.truncate(getReferrer(), 256);

  return staticProperties;
};

export const getMetaProperties = (
  staticProperties: StaticProperties,
  dynamicProperties: DynamicProperties
) => {
  // metaProperties = properties that get stamped to each event
  // staticProperties = properties that get set once on tracker initialisation
  // dynamicProperties = properties that can change for each event
  const metaProperties = { ...dynamicProperties, ...staticProperties };

  const emptyProperties = helpers
    .getObjectKeys(metaProperties)
    .filter((key) => !metaProperties[key]);

  // We cast the emptyProperties as [] since the emptyProperties are evaluated
  // during runtime, and we don't know which ones are empty during build time.
  return helpers.omit(metaProperties, emptyProperties as []);
};
