const getBrowserVersion = () => {
  // eslint-disable-next-line hubspot-dev/no-confusing-browser-globals
  const userAgent = self.navigator.userAgent;

  const edgeMatches = /Edg\/([0-9.]+)/.exec(userAgent);
  if (edgeMatches && edgeMatches.length > 1) {
    return `Edge/${edgeMatches[1]}`;
  }

  const chromeMatches = /Chrome\/([0-9.]+)/.exec(userAgent);
  if (chromeMatches && chromeMatches.length > 1) {
    return `Chrome/${chromeMatches[1]}`;
  }

  return null;
};

export default getBrowserVersion;
