'use es6';
import logger from 'SignalsExtension/utils/logger';
import Raven from 'raven-js';
import enviro from 'SignalsExtension/shims/enviro';
import {
  getExtensionId,
  getManifestVersion,
} from 'sales-clients-common/chrome/safeRuntime';
import getBrowserVersion from 'SignalsExtension/utils/getBrowserVersion';

const GMAIL_STATIC_URL = 'https://mail.google.com/_/scs/mail-static';

const IGNORE_ERRORS = [
  'Extension context invalidated.', // When user updates extension
  'n.apply is not a function', // I18n internal error
  'a.apply is not a function', // I18n internal error
  "Cannot read property 'apply' of undefined", // I18n internal error
  /failed with status (code )?(0|403|404|50[2-4]|522|524)/,
  /api status (0|401|403|404|50[2-4]|522|524)/,
  /search - (0|401|403|404|50[2-4]|522|524)/,
  /Invocation of form runtime\.connect/,
  /app-preferences\/user-attributes\/(fetch|write)(.)* failed with status (400|401|477)/,
];

// Sentry "transactions" often list where the error was triggered from
const IGNORE_TRANSACTIONS = [];

const INCLUDE_PATHS = [
  new RegExp(`\
chrome-extension://${getExtensionId()}\
`),
];

// Set up unhandled error capture
// eslint-disable-next-line hubspot-dev/no-confusing-browser-globals
self.addEventListener('error', (...args) => {
  // Find actual Error object, and send to Sentry if we find it
  const error = args.find((arg) => arg && arg.message);
  if (error) {
    logger.error(error);
    if (error.stack) {
      return Raven.captureException(error, {
        tags: { source: 'background-sw-unhandled-errors' },
        stacktrace: true,
      });
    } else {
      return Raven.captureException(error.message || error, {
        tags: { source: 'background-sw-unhandled-errors' },
        stacktrace: true,
      });
    }
  }
  return null;
});

const init = () => {
  if (!enviro.deployed('raven')) {
    return;
  }

  const configs = {
    ignoreErrors: IGNORE_ERRORS,
    includePaths: INCLUDE_PATHS,

    shouldSendCallback(data) {
      const shouldIgnore = IGNORE_TRANSACTIONS.find((t) => {
        const { culprit, transaction } = data;
        const ignoreTransaction = transaction && transaction.search(t) > -1;
        const ignoreCulprit = culprit && culprit.search(t) > -1;

        return ignoreTransaction || ignoreCulprit;
      });
      if (shouldIgnore) {
        return false;
      }
      return true;
    },

    dataCallback(data) {
      const { stacktrace } = data;

      if (!(stacktrace && stacktrace.frames)) {
        return data;
      }

      stacktrace.frames = stacktrace.frames.filter(
        ({ filename }) => !filename.startsWith(GMAIL_STATIC_URL)
      );

      return data;
    },

    release: getManifestVersion(),
    browser: getBrowserVersion(),
  };

  const ravenURL =
    'https://ca51b55e85514c39bb26d69b40c4621b@exceptions.hubspot.com/v2/1';

  Raven.config(ravenURL, configs).install();

  logger.debug('Raven initialized');
};

export default { init };
