import {
  clearBadge,
  openPopup,
  openPopupSupported,
  updateIcon,
  updateBadgeText,
  // @ts-expect-error untyped
} from 'SignalsExtension/background/browserAction';
import { LogError } from 'sales-clients-common/utils/reportError';
import {
  PopupMessage,
  PopupMessageAction,
} from 'sales-ext-common/constants/backgroundMessagingMethods';
import { updateActionNewBadgeDismissed } from 'sales-clients-common/utils/storageStateUtils';

export const popupMethodHandler = (
  { action, data }: PopupMessage,
  sendResponse: (response: boolean | string) => void
) => {
  try {
    switch (action) {
      case PopupMessageAction.CLEAR_BADGE:
        updateActionNewBadgeDismissed(true);
        // There's something very strange with timing here where the badge doesn't get cleared
        //   unless we push this call a bit. I'm guessing there's something wrong with the promises here,
        //   whether it's another promise bug in the chrome api, or something in our safeStorage implementation.
        setTimeout(() => {
          clearBadge();
        }, 10);

        break;
      case PopupMessageAction.UPDATE_BADGE_ICON:
        updateIcon();
        break;
      case PopupMessageAction.UPDATE_BADGE_TEXT:
        updateBadgeText({ text: data });
        break;
      case PopupMessageAction.OPEN_POPUP:
        // Open popup and navigate to the Settings page
        openPopup(true);
        break;
      case PopupMessageAction.IS_OPEN_POPUP_SUPPORTED:
        sendResponse(openPopupSupported);
        break;
      default:
        break;
    }
    return null;
  } catch (ex) {
    LogError({
      error: ex,
      fingerprint: ['popupMethodHandler'],
      tags: { source: 'popupMethodHandler' },
      extraData: { request: action },
      stacktrace: true,
    });
  }
  return null;
};
