// @ts-expect-error untyped
import chrome from 'SignalsExtension/globals/chrome';
import { hstcKey } from 'usage-tracker-core/storageKeys';
import logger from 'SignalsExtension/utils/logger';
// @ts-expect-error untyped
import { waitForChromeCookies } from 'SignalsExtension/data/chrome/Cookies';
import { ChromeCookie } from '../types/chrome';

export const getHstcCookie = () => {
  return waitForChromeCookies()
    .then(() =>
      chrome.cookies
        .get({ url: 'https://hubspot.com', name: hstcKey })
        .then((cookie: ChromeCookie) => {
          const hstcCookie = cookie != null ? cookie.value : undefined;
          if (hstcCookie) {
            return hstcCookie;
          } else {
            throw new Error('getHstcCookie: unable to get hstc cookie');
          }
        })
        .catch((err: Error) => {
          let errorMessage = null;
          if (err) {
            errorMessage = `getHstcCookie: Unable to get hstc cookie: ${err.message}`;
          } else {
            errorMessage = 'getHstcCookie: unable to get hstc cookie';
          }
          logger.error(errorMessage);
        })
    )
    .catch((err: Error) => {
      logger.error(`waitForChromeCookies: ${err.message || err}`);
    });
};
