'use es6';

import HubApiHttpClient from 'SignalsExtension/api/HubApiHttpClient';
import enviro from 'SignalsExtension/shims/enviro';
import { LogInfo } from 'sales-clients-common/utils/reportError';

export const fetchPortalId = () => {
  return HubApiHttpClient.get(`sidekick-users/v1/app`); // works on any hublet
};

export const fetchHubletFromPortal = (portalId) => {
  return HubApiHttpClient.get(`cookie-echo/v1?portalId=${portalId}`)
    .then(() => {
      return enviro.getHublet();
    })
    .catch((error) => {
      if (error.status === 488 && error.responseJSON) {
        return error.responseJSON.correctHublet;
      }
      LogInfo({
        error,
        fingerprint: ['UsersApi/fetchHubletFromPortal'],
        tags: {
          source: 'fetchHubletFromPortal',
        },
      });
      throw error;
    });
};

export const setPortalIdThroughApi = (newPortalId) => {
  return HubApiHttpClient.put(`sidekick-users/v1/app/${newPortalId}`); // works on any hublet
};
