'use es6';

import {
  apiProxyMethodHandler,
  clearDataFetchingClientCache,
} from 'SignalsExtension/background/apiProxyMethodHandler';
import {
  graphQLProxyMethodHandler,
  clearApolloClientCache,
} from 'SignalsExtension/background/graphQLProxyMethodHandler';
import { getUser } from 'SignalsExtension/background/models/user';
import { extensionCheckerHandler } from 'SignalsExtension/background/extensionChecker';
import {
  checkOriginPermissionHandler,
  requestOriginPermissionHandler,
} from 'SignalsExtension/background/permissionHandler';
import { restartExtension } from 'SignalsExtension/utils/tabs';
import { openSignupHandler } from 'SignalsExtension/background/onboarding';
import { addConnectListener } from 'sales-clients-common/chrome/safeRuntime';
import {
  portalSwitchHandler,
  portalSwitchFromPopupHandler,
} from 'SignalsExtension/data/PortalIdContainer';
import { xhrInterceptorMethodHandler } from 'SignalsExtension/background/xhrInterceptorMethodHandler';
import { notificationReceivedHandler } from 'SignalsExtension/background/notificationReceivedHandler';
import { popupMethodHandler } from 'SignalsExtension/background/popupMethodHandler';
import { hubspotEverywhereMethodHandler } from 'SignalsExtension/background/hubspotEverywhereMethodHandler';
import { controlSidePanelMethodHandler } from 'SignalsExtension/background/controlSidePanelMethodHandler';
import { noAuthApiProxyMethodHandler } from 'SignalsExtension/background/noAuthApiProxyMethodHandler';
import { calendarConnectedHandler } from 'SignalsExtension/background/calendarConnectedHandler';
import {
  POPUP_METHOD,
  PERMISSION_CHECK_ORIGIN as PERMISSION_CHECK_ORIGIN_METHOD,
  PERMISSION_REQUEST_ORIGIN as PERMISSION_REQUEST_ORIGIN_METHOD,
  CALENDAR_CONNECTED,
} from 'sales-ext-common/constants/backgroundMessagingMethods';
import logger from 'SignalsExtension/utils/logger';
import window from 'SignalsExtension/globals/window';
import {
  shortlinkMethodHandler,
  shortlinkResetMethodHandler,
} from 'SignalsExtension/background/shortlinkMethodHandler';
import { listenToContentScriptMethodsBatch } from 'SignalsExtension/background/backgroundMessaging';
import Raven from 'raven-js';
import { getSessionIdMessageHandler } from 'SignalsExtension/init/PerformanceTelemetry';
import { getPortalId } from 'SignalsExtension/data/PortalIdContainer';

const EXTENSION_CHECKER_METHOD = 'blacklistedExtensions';
const API_PROXY_METHOD = 'apiProxyMethod';
const GRAPHQL_PROXY_METHOD = 'graphqlProxyMethod';
const CLEAN_APOLLO_CACHE = 'cleanApolloCache';
const NO_AUTH_API_PROXY_METHOD = 'noAuthApiProxyMethod';
const ROUTE_RESTART_METHOD = 'route:restart';
const ONBOARDING_OPEN_SIGNUP_METHOD = 'openSignup';
const SWITCHED_PORTAL_METHOD = 'switchedPortal';
const SWITCHED_PORTAL_FROM_POPUP_METHOD = 'switchedPortalFromPopup';
const XHR_INTERCEPTOR_METHOD = 'xhr_interceptor';
const HUBSPOT_EVERYWHERE_METHOD = 'hubspotEverywhereMethod';
const NETWORK_ACTIVE_METHOD = 'networkActiveMethod';
const NOTIFICATION_RECEIVED_METHOD = 'notificationReceived';
const SHORTLINK_METHOD = 'shortlink';
const SHORTLINK_RESET_METHOD = 'shortlinkReset';
const CONTROL_SIDE_PANEL = 'controlSidePanelMethod';
const GET_SESSION_ID = 'getSessionId';
const GET_PORTAL_ID = 'getPortalId';

const contentScriptMethodHandlers = {
  [EXTENSION_CHECKER_METHOD]: extensionCheckerHandler,
  [PERMISSION_CHECK_ORIGIN_METHOD]: checkOriginPermissionHandler,
  [PERMISSION_REQUEST_ORIGIN_METHOD]: requestOriginPermissionHandler,
  [API_PROXY_METHOD]: apiProxyMethodHandler,
  [GRAPHQL_PROXY_METHOD]: graphQLProxyMethodHandler,
  [CLEAN_APOLLO_CACHE]: () => {
    clearDataFetchingClientCache();
    clearApolloClientCache();
    return null;
  },
  [ROUTE_RESTART_METHOD]: () => restartExtension(),
  [ONBOARDING_OPEN_SIGNUP_METHOD]: openSignupHandler,
  [SWITCHED_PORTAL_METHOD]: (request) => portalSwitchHandler(request),
  [SWITCHED_PORTAL_FROM_POPUP_METHOD]: portalSwitchFromPopupHandler,
  [XHR_INTERCEPTOR_METHOD]: xhrInterceptorMethodHandler,
  [NETWORK_ACTIVE_METHOD]: () => {
    window.hubspot.resolvePopupServices.sessionMonitor.updateOnlineStatus();
    return null;
  },
  [NO_AUTH_API_PROXY_METHOD]: noAuthApiProxyMethodHandler,
  [HUBSPOT_EVERYWHERE_METHOD]: hubspotEverywhereMethodHandler,
  [NOTIFICATION_RECEIVED_METHOD]: notificationReceivedHandler,
  [POPUP_METHOD]: popupMethodHandler,
  [CONTROL_SIDE_PANEL]: controlSidePanelMethodHandler,
  [SHORTLINK_METHOD]: (isShortlinkLoginWindowOpen) =>
    shortlinkMethodHandler(isShortlinkLoginWindowOpen),
  [SHORTLINK_RESET_METHOD]: () => shortlinkResetMethodHandler(),
  [CALENDAR_CONNECTED]: calendarConnectedHandler,
  [GET_SESSION_ID]: getSessionIdMessageHandler,
  [GET_PORTAL_ID]: (request, sendResponse) => sendResponse(getPortalId()),
};

export default () => {
  listenToContentScriptMethodsBatch(contentScriptMethodHandlers);
  const listener = (port) => {
    logger.debug('Got connection');

    port.postMessage({
      method: 'userModel',
      user_profile: getUser().toJS(),
    });
  };
  addConnectListener(listener);
};

const methodHandlers = { ...contentScriptMethodHandlers };

export const messageHandler = (request, sender, sendResponse) => {
  const handler = methodHandlers[request.method];
  if (handler) {
    try {
      return handler(request.data, sendResponse, sender);
    } catch (err) {
      logger.error(err);
      logger.info(request.method);
      logger.debug(request);
      Raven.captureBreadcrumb({
        message: `Handler for ${request.method} failed`,
        data: { err, request },
      });
    }
  }
  Raven.captureBreadcrumb({
    message: `Handler for ${request.method} not found`,
    data: { contentScriptMethodHandlers, request },
  });
  return null;
};

// This returns the current user obj
// Called by fetchUser in SERA: Gmail and Popup
// This creates a long lived connection with SERA and Popup
export const portListener = (port) => {
  port.postMessage({
    method: 'userModel',
    user_profile: getUser().toJS(),
  });
};
