'use es6';

import HubApiHttpClient from 'SignalsExtension/api/HubApiHttpClient';

// Must call without portalId
export const fetchAlarms = () =>
  HubApiHttpClient.get('firealarm/v4/alarm/clients-gmail');

export const fetchExtensionAlarms = () =>
  HubApiHttpClient.get('firealarm/v4/alarm/clients-chrome-extension');
