'use es6';

import HubApiWithoutImmutableHttpClient from 'SignalsExtension/api/HubApiWithoutImmutableHttpClient';
import { UserAttributesClient } from 'frontend-preferences-client';
import { getPortalId } from 'SignalsExtension/data/PortalIdContainer';

export const fetchAttributes = (keys) => {
  if (!getPortalId()) {
    return Promise.reject(Error('fetchAttributes: PortalId is not available'));
  }

  const userAttributesClient = UserAttributesClient.forCaller(
    'SignalsExtension'
  ).withHttpClient(HubApiWithoutImmutableHttpClient);
  return userAttributesClient.batchFetch(keys);
};

export const saveAttribute = (key, value) => {
  if (!getPortalId()) {
    return Promise.reject(Error('saveAttribute: PortalId is not available'));
  }

  const userAttributesClient = UserAttributesClient.forCaller(
    'SignalsExtension'
  ).withHttpClient(HubApiWithoutImmutableHttpClient);
  return userAttributesClient.write(key, value);
};
