import { ChirpAvroRpcEarlyRequestDetails } from '.';
import { FetchType, getGlobal } from './apiClients/global';
import { makeChirpAvroCallWithFetch } from './chirp-avro';
import { isLocalService, shouldUseLocalBackend } from './localRequestUtils';
import { createRequestName } from './quick-fetch-utils';
import { ChirpAvroRpcDetails, ChirpAvroRpcInputs } from './types';

type AnyStreamingDetails = ChirpAvroRpcDetails<any, any, any, true>;

export type EarlyStreamRequest = {
  response: Promise<Response>;
  abortController: AbortController;
  startTime: number;
};

export function getEarlyStreamEntryFor<Details extends AnyStreamingDetails>(
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>
): EarlyStreamRequest | undefined {
  const key = createRequestName(details, inputs?.request);
  const entry = getEarlyStreamEntry(key);
  return entry;
}

export function consumeEarlyStreamEntry<Details extends AnyStreamingDetails>(
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>
): EarlyStreamRequest | undefined {
  const key = createRequestName(details, inputs?.request);
  const entry = getEarlyStreamEntry(key);
  if (entry) {
    removeEarlyStreamEntry(key);
    return entry;
  }
  return undefined;
}

function getEarlyStreamsCache(): Map<string, EarlyStreamRequest> {
  const g = getGlobal() as any;
  if (!g.__chirpEarlyStreams) {
    g.__chirpEarlyStreams = new Map<string, EarlyStreamRequest>();
  }
  return g.__chirpEarlyStreams as Map<string, EarlyStreamRequest>;
}

export function getEarlyStreamEntry(
  key: string
): EarlyStreamRequest | undefined {
  return getEarlyStreamsCache().get(key);
}

export function removeEarlyStreamEntry(key: string): void {
  getEarlyStreamsCache().delete(key);
}

/**
 * Starts an early streaming request and stores the Response promise in a global cache.
 * Does not consume the response body; consumers should attach later and stream it.
 */
export async function makeChirpEarlyStreamRequest<
  Details extends ChirpAvroRpcEarlyRequestDetails<any, any, any, true>,
>(
  fetcher: FetchType,
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>
): Promise<{ key: string; entry: EarlyStreamRequest }> {
  if (!details.isStreaming) {
    throw new Error(
      'makeEarlyStreamRequest may only be used for streaming RPCs'
    );
  }
  const { earlyRequest: __earlyRequest, ...detailsCopyWithoutEarlyRequest } =
    details;

  const key = createRequestName(
    detailsCopyWithoutEarlyRequest,
    inputs?.request
  );
  const existing = getEarlyStreamEntry(key);
  if (existing) {
    return { key, entry: existing };
  }

  // eslint-disable-next-line compat/compat
  const abortController = new AbortController();
  const startTime = Date.now();

  const shouldMakeLocalRequest =
    shouldUseLocalBackend() &&
    (await isLocalService(detailsCopyWithoutEarlyRequest.serviceName));

  const responsePromise = makeChirpAvroCallWithFetch(
    fetcher,
    detailsCopyWithoutEarlyRequest,
    inputs,
    shouldMakeLocalRequest,
    abortController.signal
  );

  const entry: EarlyStreamRequest = {
    response: responsePromise,
    abortController,
    startTime,
  };
  getEarlyStreamsCache().set(key, entry);
  return { key, entry };
}
