/* hs-eslint ignored failing-rules */
/* eslint-disable hubspot-dev/no-async-await */

'use es6';

const REQUEST_HEADERS = Symbol('headers');
const RESPONSE_HEADERS = Symbol('response headers');
const ABORT_CONTROLLER = Symbol('AbortController');
const REQUEST_METHOD = Symbol('method');
const REQUEST_URL = Symbol('URL');
const MIME = Symbol('MIME');
const DISPATCH = Symbol('dispatch');
const ERRORED = Symbol('errored');
const TIMEOUT = Symbol('timeout');
const TIMED_OUT = Symbol('timedOut');
const IS_RESPONSE_TEXT = Symbol('isResponseText');

export class XMLHttpRequestWithFetch extends EventTarget {
  constructor() {
    super();
    this.readyState = this.constructor.UNSENT;
    this.response = null;
    this.responseType = '';
    this.responseURL = '';
    this.status = 0;
    this.statusText = '';
    this.timeout = 0;
    this.withCredentials = false;
    this[REQUEST_HEADERS] = new Headers();
    this[REQUEST_HEADERS].set('accept', '*/*');
    this[RESPONSE_HEADERS] = new Headers();
    // eslint-disable-next-line compat/compat
    this[ABORT_CONTROLLER] = new AbortController();
    this[REQUEST_METHOD] = '';
    this[REQUEST_URL] = '';
    this[MIME] = '';
    this[ERRORED] = false;
    this[TIMEOUT] = 0;
    this[TIMED_OUT] = false;
    this[IS_RESPONSE_TEXT] = true;
  }
  static get UNSENT() {
    return 0;
  }
  static get OPENED() {
    return 1;
  }
  static get HEADERS_RECEIVED() {
    return 2;
  }
  static get LOADING() {
    return 3;
  }
  static get DONE() {
    return 4;
  }
  get responseText() {
    if (this[ERRORED]) return null;
    if (this.readyState < this.constructor.HEADERS_RECEIVED) return '';
    if (this[IS_RESPONSE_TEXT]) return this.response;
    throw new DOMException(
      'Response type not set to text',
      'InvalidStateError'
    );
  }
  get responseXML() {
    throw new Error('XML not supported');
  }
  [DISPATCH](evt) {
    const attr = `on${evt.type}`;
    if (typeof this[attr] === 'function') {
      this.addEventListener(evt.type, this[attr].bind(this), {
        once: true,
      });
    }
    this.dispatchEvent(evt);
  }
  abort() {
    this[ABORT_CONTROLLER].abort();
    this.status = 0;
    this.setReadyState(this.constructor.UNSENT);
  }
  open(method, url) {
    this.status = 0;
    this[REQUEST_METHOD] = method;
    this[REQUEST_URL] = url;
    this.setReadyState(this.constructor.OPENED);
  }
  setRequestHeader(header, value) {
    header = String(header).toLowerCase();
    this[REQUEST_HEADERS].append(header, value);
  }
  overrideMimeType(mimeType) {
    this[MIME] = String(mimeType);
  }
  getAllResponseHeaders() {
    if (this[ERRORED] || this.readyState < this.constructor.HEADERS_RECEIVED)
      return '';

    const headers = [];

    for (const pair of this[RESPONSE_HEADERS].entries()) {
      headers.push(`${pair[0]}: ${pair[1]}`);
    }

    return headers.join('\r\n');
  }
  getResponseHeader(headerName) {
    const value = this[RESPONSE_HEADERS].get(String(headerName).toLowerCase());
    return typeof value === 'string' ? value : null;
  }
  setReadyState(value) {
    this.readyState = value;
    this[DISPATCH](new CustomEvent('readystatechange'));
  }
  send(body = null) {
    if (this.timeout > 0) {
      this[TIMEOUT] = setTimeout(() => {
        this[TIMED_OUT] = true;
        this[ABORT_CONTROLLER].abort();
      }, this.timeout);
    }
    const responseType = this.responseType || 'text';
    this[IS_RESPONSE_TEXT] = responseType === 'text';
    fetch(this[REQUEST_URL], {
      method: this[REQUEST_METHOD] || 'GET',
      signal: this[ABORT_CONTROLLER].signal,
      headers: this[REQUEST_HEADERS],
      credentials: this.withCredentials ? 'include' : 'same-origin',
      body,
    })
      .finally(() => {
        clearTimeout(this[TIMEOUT]);
        this[DISPATCH](new CustomEvent('loadstart'));
      })
      .then(
        async (resp) => {
          this.responseURL = resp.url;
          this.status = resp.status;
          this.statusText = resp.statusText;
          this.setReadyState(this.constructor.DONE);
          const finalMIME =
            this[MIME] ||
            this[RESPONSE_HEADERS].get('content-type') ||
            'text/plain';
          this[RESPONSE_HEADERS] = resp.headers;
          switch (responseType) {
            case 'text':
              this.response = await resp.text();
              break;
            case 'blob':
              this.response = new Blob([await resp.arrayBuffer()], {
                type: finalMIME,
              });
              break;
            case 'arraybuffer':
              this.response = await resp.arrayBuffer();
              break;
            case 'json':
              this.response = await resp.json();
              break;
            default:
              this.response = await resp.text();
              break;
          }
          this[DISPATCH](new CustomEvent('load'));
        },
        (err) => {
          let eventName = 'abort';
          if (err.name !== 'AbortError') {
            this[ERRORED] = true;
            eventName = 'error';
          } else if (this[TIMED_OUT]) {
            eventName = 'timeout';
          }
          this[DISPATCH](new CustomEvent(eventName));
        }
      )
      .finally(() => this[DISPATCH](new CustomEvent('loadend')))
      .catch(() => {
        this.setReadyState(this.constructor.DONE);
        this[ERRORED] = true;
        this[DISPATCH](new CustomEvent('error'));
        return;
      });
  }
}
