import { cacheErrorEntryToChromeStorage } from 'sales-clients-common/utils/debugLogManagerChromeExt';

/**
 *
 * @param {string} message
 */
const logToCache = (message: string, error = false) => {
  // TODO centralize and expand logging messages to cache in sale-clients-common
  cacheErrorEntryToChromeStorage({
    message: `[HubSpot Sales] - %c${new Date()
      .toISOString()
      .slice(11, -1)}%c: ${message}`,
  });
  if (error) {
    console.error(
      `[HubSpot Sales] - %c${new Date()
        .toISOString()
        .slice(11, -1)}%c: ${message}}`,
      'color:red',
      'color:black'
    );
  } else {
    console.log(
      `[HubSpot Sales] - %c${new Date()
        .toISOString()
        .slice(11, -1)}%c: ${message}`,
      'color:red',
      'color:black'
    );
  }
};

export default logToCache;
