import { MetricsDaemon } from './daemon/MetricsDaemon';
import { Counter } from './metric/Counter';
import { Histogram } from './metric/Histogram';
import { Dimensions } from './metric/Metric';
import { SessionCounter } from './metric/SessionCounter';
import { Timer } from './metric/Timer';

const $SessionCounter = Symbol('SessionCounter');
class MetricsFactory {
  namespace: string;
  globalDimensions: Dimensions;

  constructor(namespace: string, globalDimensions: Dimensions = {}) {
    this.namespace = namespace;
    this.globalDimensions = globalDimensions;
  }

  private namespaceMetric(name: string) {
    return `${this.namespace}.${name}`;
  }

  public counter(name: string, dimensions: Dimensions = {}): Counter {
    return MetricsDaemon.instance().getMetric(
      this.namespaceMetric(name),
      { ...this.globalDimensions, ...dimensions },
      Counter
    ) as Counter;
  }

  /**
   * SessionCounters have some nuance that make them challenging to use
   * correctly. For now, we access them via a Symbol to avoid external
   * consumers accessing this API.
   * @internal
   */
  [$SessionCounter](name: string, dimensions: Dimensions = {}): SessionCounter {
    return MetricsDaemon.instance().getMetric(
      this.namespaceMetric(name),
      { ...this.globalDimensions, ...dimensions },
      SessionCounter
    ) as SessionCounter;
  }

  public histogram(name: string, dimensions: Dimensions = {}): Histogram {
    return MetricsDaemon.instance().getMetric(
      this.namespaceMetric(name),
      { ...this.globalDimensions, ...dimensions },
      Histogram
    ) as Histogram;
  }

  public timer(name: string, dimensions: Dimensions = {}): Timer {
    return MetricsDaemon.instance().getMetric(
      this.namespaceMetric(name),
      { ...this.globalDimensions, ...dimensions },
      Timer
    ) as Timer;
  }
}

export { MetricsFactory, $SessionCounter };
