/* hs-eslint ignored failing-rules */
/* eslint-disable hubspot-dev/no-async-await */

'use es6';
import env from 'SignalsExtension/shims/enviro';
import {
  HUBSPOT_API_COOKIE,
  HUBSPOTAPI_COOKIE_NAME,
  HUBSPOTAPI_CSRF_COOKIE_NAME,
} from 'SignalsExtension/constants/cookieDescriptors';
import { waitForChromeCookies } from 'SignalsExtension/data/chrome/Cookies';
import chrome from 'SignalsExtension/globals/chrome';
import navigator from 'SignalsExtension/globals/navigator';
import logger from 'SignalsExtension/utils/logger';
import onCookieChanged from 'SignalsExtension/utils/onCookieChanged';
import { fetchUser } from './models/user';
import {
  getAndSetCsrfForContentScript,
  truncateCookieValue,
  initCsrfCookieMonitor,
} from 'SignalsExtension/background/csrfCookieMonitor';
import Raven from 'raven-js';
import { LAST_LOGIN_PORTAL_ID_KEY } from 'sales-clients-common/constants/StorageKeys';
import { shouldRunEverywhereScript } from '../utils/tabs';
import { Feature_Everywhere } from 'sales-ext-common/managers/featureToggles';
import { isFeatureEnabledForSigExt } from 'SignalsExtension/background/managers/featureToggleManager';
import { SHORTLINK_OPEN } from 'SignalsExtension/constants/StorageKeys';
import localStorage from 'SignalsExtension/globals/localStorage';
import contextChangeEventEmitter from 'sales-ext-common/utils/contextChangeEventEmitter';
import { setSidePanels } from 'SignalsExtension/data/sidePanelContainer';
import { controlSidePanelMethodHandlerPort } from 'SignalsExtension/background/controlSidePanelMethodHandler';
import { SEND_DEBUG_LOG } from 'sales-ext-common/constants/backgroundMessagingMethods';
import { sendDebugLogHandler } from './sendDebugLogHandler';
import { gate } from 'hub-http/gates';

const GMAIL_URL_REGEX = new RegExp(/.*:\/\/mail\.google\.com/);
export class SessionMonitor {
  currentUserId = null;
  onReady = null;
  onLogin = null;
  onLogout = null;
  // Do once for now - avoid flapping network problems
  networkReinitialized = false;
  didAddLastPortalIdChangeListener = false;
  didAddSidePanelListener = false;
  contentScriptConnection = null;

  constructor(onReady, onLogin, onLogout) {
    this.onReady = async (...args) => {
      await onReady(...args);
    };
    this.onLogin = (...args) => {
      onLogin(...args);
    };
    this.onLogout = (...args) => {
      onLogout(...args);
    };
  }

  isLoggedIn = () => !!this.currentUserId; // DO NOT REMOVE unless no longer needed in SERA

  // On hubspot csrf cookie change, re-fetch the user id and call handleNextUserId with either a
  // userId or null if the user is logged out. Note: we're using the hubspot CSRF token to watch
  // user login, the hubspotapi auth cookie changes too frequently to correlate to login/logout/user change
  static monitorAuthCookie = async (
    handleNextUserId,
    hasCsrfCookie,
    addSidePanelListener
  ) => {
    let prevCsrf;

    // We need to handle reject errors (e.g. cookie store not being available quickly enough)
    try {
      if (hasCsrfCookie) {
        prevCsrf = await getAndSetCsrfForContentScript();
      }
    } catch (err) {
      console.log(err);
    }

    onCookieChanged(
      HUBSPOTAPI_CSRF_COOKIE_NAME,
      HUBSPOT_API_COOKIE[env.getShort('api')].domain,
      async (cookieInfo) => {
        const curCsrf = cookieInfo.cookie.value;

        if (cookieInfo.removed) {
          logger.debug(
            '[SessionMonitor] CSRF cookie removed',
            truncateCookieValue(cookieInfo)
          );
          prevCsrf = null;
          handleNextUserId(null);
        } else if (prevCsrf !== curCsrf) {
          logger.debug(
            '[SessionMonitor] CSRF cookie changed',
            'prevCsrf: ',
            prevCsrf,
            'curCsrf: ',
            curCsrf,
            'cookieInfo: ',
            truncateCookieValue(cookieInfo)
          );

          prevCsrf = curCsrf;

          // Skip user fetch if the shortlink popup is open
          // Extension will reload when the shortlink popup is closed
          if (localStorage.getItem(SHORTLINK_OPEN) === 'true') {
            logger.debug('[SessionMonitor] cookie changed, shortlink open');
            return;
          }
          const user = await fetchUser();

          try {
            addSidePanelListener();
          } catch (error) {
            logger.error('Error in addSidePanelListener', error);
          }

          handleNextUserId(user && user.get('userId'));
        }
      }
    );
  };

  static logout = async () => {
    try {
      await waitForChromeCookies();
      chrome.cookies
        .remove({
          url: HUBSPOT_API_COOKIE[env.getShort('api')].url,
          name: HUBSPOTAPI_COOKIE_NAME,
        })
        .catch((err) => {
          logger.error(
            `[SessionMonitor] logout: unable to remove HubSpot cookie: ${err.message}`
          );
        });
    } catch (err) {
      logger.error(`[SessionMonitor] logout failed: ${err}`);
    }
  };

  updateOnlineStatus = () => {
    if (!this.networkReinitialized) {
      // Wait to initialize for network stability
      logger.debug('Network now online - Reinitializing in 1 second');
      setTimeout(this.reinitialize, 1000);
      this.networkReinitialized = true;
    } else {
      logger.error('Network already reinitialized once - not running again');
    }
  };

  handleNextUserId = (nextUserId) => {
    logger.log(
      `[SessionMonitor] User changed from ${this.currentUserId} to ${nextUserId}`
    );

    const nextLoggedIn = nextUserId > 0;
    const prevLoggedIn = this.currentUserId > 0;
    const prevUserId = this.currentUserId;

    this.currentUserId = nextUserId; // Update currentUserId BEFORE calling onLogin/onLogout

    if (!prevLoggedIn && nextLoggedIn) {
      this.onLogin(null, true);
    } else if (prevLoggedIn && !nextLoggedIn) {
      this.onLogout();
    }

    // Handles the case when the cookie suddenly changes from one user to another
    if (prevLoggedIn && nextLoggedIn && prevUserId !== nextUserId) {
      this.reinitialize();
    }

    this.addLastPortalIdChangeListener();
  };

  reinitialize = () => {
    this.onLogout();
    this.onLogin(null, true);
  };

  addLastPortalIdChangeListener() {
    if (this.didAddLastPortalIdChangeListener) {
      return;
    }
    chrome.storage.onChanged.addListener((changes) => {
      if (!changes[LAST_LOGIN_PORTAL_ID_KEY]) {
        return;
      }
      const { oldValue, newValue } = changes[LAST_LOGIN_PORTAL_ID_KEY];
      const wasLoggedOut = oldValue === undefined && newValue !== undefined;
      const didChange =
        oldValue &&
        newValue &&
        (oldValue.hublet !== newValue.hublet ||
          oldValue.portal_id !== newValue.portal_id);
      if (wasLoggedOut || didChange) {
        this.reinitialize();
      }
    });
    this.didAddLastPortalIdChangeListener = true;
  }

  // This is handled separately from shouldRunEverywhereScript for now because of the introduced
  //   async behavior.  After the gate is eliminated, we shouldn't need to do this separately.
  allowSidePanelInGmail = async (tab) => {
    const gmailSite = tab.url.match(GMAIL_URL_REGEX);
    if (!gmailSite) {
      return Promise.resolve(false);
    }
    const user = await fetchUser();
    return user?.get('gates')?.includes(gate('Clients:IframeSidebar')) ?? false;
  };

  addSidePanelListener = () => {
    const isHSEverywhereEnabled = isFeatureEnabledForSigExt(Feature_Everywhere);
    const canUseSidepanel = !!chrome.sidePanel;

    if (!canUseSidepanel) {
      return;
    } else if (!isHSEverywhereEnabled) {
      chrome.sidePanel.setOptions({
        enabled: false,
      });
      return;
    } else if (this.didAddSidePanelListener) {
      return;
    }

    contextChangeEventEmitter.onContextChange((contexts) => {
      setSidePanels(contexts);
    });

    const connectAndListen = async (tab) => {
      // Disconnect any existing connection
      if (this.contentScriptConnection) {
        this.contentScriptConnection.disconnect();
        this.contentScriptConnection = null;
      }
      // Create the connection to the content script (everywhere and gmail)
      if (
        !(await shouldRunEverywhereScript(tab.url)) &&
        !tab.url.match(GMAIL_URL_REGEX)
      ) {
        return;
      }

      this.contentScriptConnection = chrome.tabs.connect(tab.id);
      this.contentScriptConnection.onMessage.addListener((message) => {
        if (message.method === 'controlSidePanelMethod') {
          controlSidePanelMethodHandlerPort(
            message.data,
            tab.id,
            this.contentScriptConnection
          );
        } else if (message.method === SEND_DEBUG_LOG) {
          sendDebugLogHandler();
        }
      });
      this.contentScriptConnection.onDisconnect.addListener(() => {
        this.contentScriptConnection = null;
      });
    };

    // This should handle tab switching to maintain a single connection
    chrome.tabs.onActivated.addListener(async (activeInfo) => {
      const tab = await chrome.tabs.get(activeInfo.tabId);
      connectAndListen(tab);
    });

    // This should handle navigation changes - the content script getting loaded again
    //   will kill the connection and we need to make sure the event handler has been
    //   established to handle the connection in order to maintain a single connection
    chrome.runtime.onMessage.addListener((request, sender) => {
      if (
        request.method === 'readyForConnectionToContentScript' &&
        sender.tab.active
      ) {
        connectAndListen(sender.tab);
      }
      return false;
    });

    chrome.tabs.onUpdated.addListener(async (tabId, changeInfo, tab) => {
      if (changeInfo.status === 'loading') {
        // TODO: improve how allowedSite is managed to include all side panel cases
        const allowedSite = await shouldRunEverywhereScript(tab);
        const useInSidePanelInGmail = await this.allowSidePanelInGmail(tab);
        if (allowedSite || useInSidePanelInGmail) {
          await chrome.sidePanel.setOptions({
            tabId,
            enabled: true,
            path: `pages/sidePanel.html?tabId=${tabId}`,
          });
        } else {
          await chrome.sidePanel.setOptions({
            tabId,
            enabled: false,
          });
        }
      }
    });

    this.didAddSidePanelListener = true;
  };

  // Monitors the hubspot session (currently logged in user id) using the HS csrf cookie. Once we
  // have user id, onReady is called and given a bool indicating the current login state.
  // onLogin/onLogout are called on state change, not initialization.
  initialize = async (reloadGmail, hasCsrfCookie) => {
    logger.debug(
      `[SessionMonitor] initialize - with CSRF Cookie: ${hasCsrfCookie}`
    );
    // First, get the initial state and pass that to onReady. Then initialize the cookie monitor.
    const user = await fetchUser(hasCsrfCookie);
    this.currentUserId = user && user.get('userId');
    try {
      await this.onReady(this.isLoggedIn(), user, reloadGmail);
    } catch (err) {
      Raven.captureMessage(
        `[SessionMonitor onReady] ${(err && err.message) || err}`
      );
    }
    this.addLastPortalIdChangeListener();
    this.addSidePanelListener();
  };

  init = async (reloadGmail, hasCsrfCookie) => {
    logger.log('[SessionMonitor] init');

    await SessionMonitor.monitorAuthCookie(
      this.handleNextUserId,
      hasCsrfCookie,
      this.addSidePanelListener
    );

    // Fetch the user and initialize the majority of the background context
    await this.initialize(reloadGmail, hasCsrfCookie);

    // Now that the gates have been set init csrfMonitor again
    if (hasCsrfCookie) {
      await initCsrfCookieMonitor();
    }

    // In the event that the network is down, register for re-initialization when network comes back
    if (!navigator.onLine) {
      logger.warn(
        'Detected browser in offline mode - will try to reinitialize when browser comes online'
      );
    }

    try {
      chrome.storage.session.setAccessLevel({
        accessLevel: 'TRUSTED_AND_UNTRUSTED_CONTEXTS',
      });
    } catch (err) {
      logger.error('Error when setting session access level', err);
    }
  };
}
