'use es6';

import { getPortalId } from 'SignalsExtension/data/PortalIdContainer';
import logger from 'SignalsExtension/utils/logger';

const portalIdQueryMatcher = new RegExp(/\?*portalId=[0-9]+/);

const hublessRequests = [
  'login-api/v1/cookie-verify',
  'login-verify/echo/user',
  'users/v2/app/sidekick',
  'sidekick-users/v1/app',
  'firealarm/v4/alarm/clients-gmail',
  'firealarm/v4/alarm/clients-chrome-extension',
  'cookie-echo/v1/user',
  'accounts/v1/accounts',
  'email-blacklist/v1/blacklist.json',
  'api/cartographer/v1/ipl',
];

/**
 * Ideally, hubless requests would use a different client - note, these are different from
 * the "NoAuth" case, as these are often user level requests, but critically, need to omit
 * a portalId
 */
const isHublessRequest = (url) => {
  return hublessRequests.some((req) => url.includes(req));
};

const withPortalId = (options) => {
  const portalId = getPortalId();

  if (portalId && !isHublessRequest(options.url)) {
    const hasPortalIdInOptions = !!options.portalId;
    const hasPortalIdInQuery = options.query && options.query.portalId;
    const hasPortalIdInUrl = options.url.match(portalIdQueryMatcher);

    if (!hasPortalIdInOptions && !hasPortalIdInQuery && !hasPortalIdInUrl) {
      options.portalId = portalId;
      if (options.query) {
        options.query.portalId = portalId;
      } else {
        options.query = {
          portalId,
        };
      }

      logger.debug(`portal id missing: ${options.url}`);
    }
  }

  return options;
};

export default withPortalId;
