/* hs-eslint ignored failing-rules */
/* eslint-disable hubspot-dev/no-async-await */

'use es6';

import { fetchUserInfo } from 'SignalsExtension/api/LoginVerifyApi';
import {
  fetchPortalId,
  fetchHubletFromPortal,
} from 'SignalsExtension/api/UsersApi';
import { sendToContentScripts } from 'SignalsExtension/background/backgroundMessaging';
import { setPortalId } from 'SignalsExtension/data/PortalIdContainer';
import enviroInstance from 'SignalsExtension/shims/enviro';
import logger from 'SignalsExtension/utils/logger';
import { Map as ImmutableMap } from 'immutable';
import { localGet, localSet } from 'sales-clients-common/chrome/safeStorage';
import { LAST_LOGIN_PORTAL_ID_KEY } from 'sales-clients-common/constants/StorageKeys';
import { LogHttpError, LogInfo } from 'sales-clients-common/utils/reportError';
import { setGates } from '../../data/salesGates';
import { fetchUserPortalInfo } from 'SignalsExtension/api/LoginVerifyApi';
import enviro from 'SignalsExtension/shims/enviro';
import { transformGates } from 'SignalsExtension/shims/userInfo';

let user = ImmutableMap();

export const getUser = () => user;

export const setUser = (nextUser) => {
  user = nextUser;
  if (user.get('hublet')) {
    enviroInstance.setHublet(user.get('hublet'));
  }
  sendToContentScripts('userprofile:change', user.toJS());
};

// While users are logged in with the old picked portal flow, lets go ahead
// and save which portal they are on so that when they get ungated for
// portal recommend we reduce confusion as they will auto log into their usual portal.
const presaveShortlinkPortal = (portalId, hublet) => {
  localGet(LAST_LOGIN_PORTAL_ID_KEY)
    .then((data) => {
      // Don't override unless it's unset.
      if (!data[LAST_LOGIN_PORTAL_ID_KEY] && hublet && portalId) {
        return localSet({
          [LAST_LOGIN_PORTAL_ID_KEY]: {
            portal_id: portalId,
            hublet,
          },
        });
      }
      return undefined;
    })
    .catch(() => {
      logger.error('models/user.js - unable to set portal id in storage.local');
    });
};

export const initializePortalAndHublet = async () => {
  let pickedPortalId = null;
  let pickedHublet = null;

  try {
    const { [LAST_LOGIN_PORTAL_ID_KEY]: portalAndHubletFromStorage } =
      await localGet(LAST_LOGIN_PORTAL_ID_KEY);
    if (portalAndHubletFromStorage) {
      pickedPortalId = portalAndHubletFromStorage.portal_id;
      pickedHublet = portalAndHubletFromStorage.hublet;
      enviroInstance.setHublet(pickedHublet);
    } else {
      //if not found then pickedPortalId = null and pickedHublet = null
      logger.log(
        'User has no picked portal and hublet in chrome local storage'
      );
    }
  } catch (error) {
    LogInfo({
      message: 'Failed to get pickedPortalAndHublet from local storage',
      fingerprint: ['UsersApi/initializePortalAndHublet'],
      tags: {
        source: 'initializePortalAndHublet',
      },
      extraData: {
        error,
      },
    });
  }

  if (!pickedPortalId) {
    try {
      pickedPortalId = await fetchPortalId(); // api call to any hublet, does not need portalId. If this fails the remain steps will be skipped
      pickedHublet = await fetchHubletFromPortal(pickedPortalId);
    } catch (err) {
      if (err && err.status === 404) {
        pickedPortalId = null;
        pickedHublet = null;
        LogInfo({
          message: 'User has no picked portal ID',
          fingerprint: ['UsersApi/initializePortalAndHublet/fetchPortalId'],
          tags: {
            source: 'initializePortalAndHublet',
          },
          extraData: {
            err,
          },
        });
      } else {
        LogInfo({
          message: 'Failed to fetch picked portal ID and hublet',
          fingerprint: ['UsersApi/initializePortalAndHublet/fetchPortalId'],
          tags: {
            source: 'initializePortalAndHublet',
          },
          extraData: {
            err,
          },
        });
      }
    }
  }

  return { pickedPortalId, pickedHublet };
};

const getUserPortalInfo = (portalId) => {
  return fetchUserPortalInfo(portalId).catch((error) => {
    // If the hublet is incorrect, set the correct hublet and retry
    if (
      error.status === 488 &&
      error.responseJSON &&
      error.responseJSON.correctHublet
    ) {
      enviro.setHublet(error.responseJSON.correctHublet);

      LogInfo({
        message: `fetchUserPortalInfo called with incorrect hublet. Should be ${error.responseJSON.correctHublet}. Retrying...`,
        fingerprint: ['getUserPortalInfo'],
        tags: { source: 'fetchUserPortalInfo' },
        extraData: { error },
      });

      return fetchUserPortalInfo(portalId);
    }

    throw error;
  });
};

export const fetchUser = async () => {
  let cookieExpired = true;
  try {
    const hubspotUserInfo = await fetchUserInfo(); // api call to any hublet, does not need portalId
    cookieExpired = false;

    // eslint-disable-next-line prefer-const
    let { pickedPortalId, pickedHublet } = await initializePortalAndHublet();

    setPortalId(pickedPortalId);
    presaveShortlinkPortal(pickedPortalId, pickedHublet);

    let gates = undefined;
    let userPortalInfo = undefined;
    if (pickedPortalId) {
      logger.log('Fetch User Portal Info: ');
      try {
        userPortalInfo = await getUserPortalInfo(pickedPortalId);

        const { portal } = userPortalInfo;
        gates = transformGates(portal.enabled_gates, portal.enabled_v3_gates);
        setGates(gates);
      } catch (error) {
        if (error.status === 403) {
          pickedPortalId = null;
        } else {
          LogHttpError({
            error,
            source: 'fetchUser_getUserPortalInfo',
            method: 'fetchUser_getUserPortalInfo',
            extraData: {
              pickedPortalId,
            },
          });
        }
      }
    } else {
      logger.debug('Skipped getUserPortalInfo - No pickedPortalId for user');
    }

    const mergedUser = hubspotUserInfo.merge({
      sidekickId: hubspotUserInfo.get('sidekickUserId'),
      portal_id: pickedPortalId,
      hublet: pickedHublet,
      gates,
      cookieExpired,
      theme_name: userPortalInfo.user.theme_name,
    });
    setUser(mergedUser);
    logger.log('picked portalId: ', pickedPortalId);
    logger.log('picked hublet: ', pickedHublet);
    logger.log('signalsUserId: ', hubspotUserInfo.get('sidekickUserId'));

    return mergedUser;
  } catch (err) {
    if (err.status !== 401) {
      const errorMessage = `Unable to fetchUser: ${err.responseText}`;
      logger.error(errorMessage);
    }
    return null;
  }
};

export const clearUser = () => setUser(ImmutableMap());

export const getSidekickId = () => user.get('sidekickId');

export const getUserId = () => user.get('userId');

export const getEmail = () => user.get('email');

export const setUserCookieExpired = () => {
  if (!user.get('cookieExpired')) {
    setUser(user.set('cookieExpired', true));
  }
};
