/* eslint-disable no-useless-escape */

/*
  Captures the user ID ("Secondary ID") of any EventTracking URL.
*/
export const EVENT_TRACKER_SECONDARY_ID = new RegExp(
  `\
.*/(?:s|e)[1-9]t/[oc]/.*\?.*\
(?:\
si=([0-9a-z]+).*\
&(?:amp;)?pi=[0-9a-z\-]+\
|\
pi=[0-9a-z\-]+.*\
&(?:amp;)?si=([0-9a-z]+)\
)\
`,
  'i'
);

/*
  Captures the tracking key ("Primary ID") of any EventTracking URL.
*/
export const EVENT_TRACKER_PRIMARY_ID = new RegExp(
  `\
.*/(?:s|e)[1-9]t/[oc]/.*\?.*\
(?:\
si=[0-9a-z]+.*\
&(?:amp;)?pi=([0-9a-z\-]+)\
|\
pi=([0-9a-z\-]+).*\
&(?:amp;)?si=[0-9a-z]+\
)\
`,
  'i'
);

/*
  Captures the link being tracked ("Target") by an EventTracking URL.
*/
export const EVENT_TRACKER_TARGET = new RegExp(
  `\
.*/(?:s|e)[1-9]t/[oc]/.*\?.*\
(te?)=([^\&]*)\
`,
  'i'
);

export const GOOGLE_CONTENT_PROXY = new RegExp(
  `\
://[0-9a-z]+\.googleusercontent\.com/proxy/\
`,
  'i'
);

export const V2_LINK_TRACKER = new RegExp(
  `[0-9A-Z]{1,2}t(o|c)\\/[0-9A-Za-z]{1,3}\\+\\d*\\/(.*)\\/(.*)\\??`,
  'i'
);
