'use es6';
/* global DEV_SERVER_WEBSOCKET_PORT */

import { restartExtension } from '../utils/tabs';

let connection = null;
const RETRY_TIMEOUT = 10000;

export const setupWebsocketListener = () => {
  if (typeof DEV_SERVER_WEBSOCKET_PORT === 'undefined') {
    // Only setup for local development
    return;
  }

  const SOCKET_HOST = 'localhost:';
  console.log('[WebSocket] Setting up listener');

  const setupWebsocket = () => {
    connection = new WebSocket(
      `ws://${SOCKET_HOST}${DEV_SERVER_WEBSOCKET_PORT}`
    );
    connection.onopen = function (event) {
      console.log('[WebSocket] WebSocket is open now, event:', event);
    };

    connection.onclose = function (data) {
      console.log('[WebSocket] onclose:', data);
      if (data.code === 1006) {
        // Code 1006 indicates client-side error- in our case, it means that the websocket server
        // isn't running, so we try again after a timeout
        setTimeout(setupWebsocket, RETRY_TIMEOUT);
      }
    };

    connection.onmessage = function (e) {
      if (e.data && e.data === 'reload') {
        restartExtension();
      }
    };
  };
  setupWebsocket();
};
