// @ts-expect-error untyped
import chrome from 'SignalsExtension/globals/chrome';
import { ChromeCookie } from '../types/chrome';

type ChangeInfo = {
  cookie: ChromeCookie;
};

export default (
  name: string,
  domain: string,
  onCookieChanged: (change: ChangeInfo) => void
) => {
  chrome.cookies.onChanged.addListener((changeInfo: ChangeInfo) => {
    const { cookie } = changeInfo;
    const isOurs = cookie.domain.includes(domain);

    if (isOurs && cookie.name === name) {
      onCookieChanged(changeInfo);
    }
  });
};
