import { LogMessage } from 'sales-clients-common/utils/reportError';
// @ts-expect-error untyped
import chrome from 'SignalsExtension/globals/chrome';
import { reload } from 'sales-clients-common/chrome/safeRuntime';
import logger from 'SignalsExtension/utils/logger';
import { SHORTLINK_OPEN } from 'SignalsExtension/constants/StorageKeys';
// @ts-expect-error untyped
import localStorage from 'SignalsExtension/globals/localStorage';
import { origins } from 'sales-ext-common/constants/backgroundMessagingMethods';
import {
  HS_EVERYWHERE_WEBSITE_SALESFORCE_VALUE,
  HS_EVERYWHERE_ALL_OTHER_SITES_VALUE,
  HS_EVERYWHERE_DEMO_WEBSITES,
} from 'sales-ext-common/constants/chromeStorage';
import { LogInfo } from 'sales-clients-common/utils/reportError';
import { ChromeTab } from '../types/chrome';
import {
  getEverywhereWebsiteAllowlist,
  getEverywhereWebsiteBlocklist,
} from 'sales-ext-common/utils/everywhereWebsiteSettings';
import { HS_EVERYWHERE_BLOCKED_HUBSPOT_DOMAINS } from 'sales-ext-common/constants/everywhereDomains';
import { Tab, Window, WindowType } from 'sales-clients-common/chrome/types';

const GMAIL_URL_REGEX = new RegExp(/.*:\/\/mail\.google\.com/);
const BLOCKED_WINDOW_TYPES: WindowType[] = ['popup'];

// Iterates over all tabs with the given url
// If url == null, iterate over all tabs
export const forAllTabs = (url: string, fn: (tab: ChromeTab) => void) =>
  chrome.tabs
    .query({})
    .then((tabs: ChromeTab[]) => {
      for (const tab of Array.from(tabs)) {
        if (url == null || tab.url.indexOf(url) !== -1) {
          fn(tab);
        }
      }
    })
    .catch(() => {
      // This function is usually used for non critical operations (ex: reloading gmail)
      // so it shouldn't be a blocker if something goes wrong
      throw new Error('Error in SignalsExtension: forAllTabs');
    });

export const reloadTabs = async (pattern: string) =>
  await forAllTabs(pattern, (tab) => {
    // Bypass cache to ensure that the CSP headers get updated
    chrome.tabs.reload(tab.id, { bypassCache: true }).catch((error: Error) => {
      LogMessage({
        error,
        fingerprint: ['reloadTabs'],
        tags: { source: 'reloadTabs' },
        stacktrace: true,
      });
    });
  });

export const reloadGmailTabs = async () => {
  try {
    if (localStorage.getItem(SHORTLINK_OPEN) !== 'true')
      await reloadTabs('mail.google.com/mail');
  } catch (error) {
    logger.log(`Failed to get SHORTLINK_OPEN : ${error}`);
    await reloadTabs('mail.google.com/mail');
  }
};

export const reloadSFDCTabs = (
  { overridePermissionsCheck } = { overridePermissionsCheck: false }
) => {
  if (overridePermissionsCheck) {
    void reloadTabs(origins.sfdc.domain);
    return;
  }

  chrome.permissions
    .contains({
      origins: [origins.sfdc.matcher],
    })
    .then(async (granted: boolean) => {
      if (granted) {
        await reloadTabs(origins.sfdc.domain);
      }
    })
    .catch((err: Error) => {
      LogInfo({
        error: err,
        fingerprint: ['tabs/reloadSFDCTabs'],
        tags: { source: 'chrome.permissions.contains' },
        extraData: {
          params: [
            {
              origins: [origins.sfdc.matcher],
            },
          ],
        },
      });
    });
};

export const restartExtension = () => {
  try {
    if (localStorage.getItem(SHORTLINK_OPEN) !== 'true') reload();
  } catch (error) {
    logger.log(`Failed to get SHORTLINK_OPEN : ${error}`);
    reload();
  }
};

export const openInActiveTab = (redirectUrl: string) =>
  chrome.tabs.update(undefined, { url: redirectUrl });

export async function shouldRunEverywhereScript(tab: Tab): Promise<boolean> {
  const tabUrl = tab.url;
  if (!tabUrl) {
    return false;
  }

  // Gmail page script handled separately as it doesn't run the everywhere script
  if (tabUrl.match(GMAIL_URL_REGEX)) {
    return false;
  }

  const windowType = await chrome.windows
    .get(tab.windowId)
    .then((w: Window) => w.type);

  if (BLOCKED_WINDOW_TYPES.includes(windowType)) {
    return false;
  }

  const everywhereAllowlist = await getEverywhereWebsiteAllowlist();
  const everywhereBlocklist = await getEverywhereWebsiteBlocklist();
  const everywhereBlocklistWithHSDomains = [
    ...everywhereBlocklist,
    ...HS_EVERYWHERE_BLOCKED_HUBSPOT_DOMAINS,
  ];

  const blocklistMatch = everywhereBlocklistWithHSDomains.find(
    (blocklistUrl) => {
      return (
        tabUrl.includes(blocklistUrl) &&
        // If the tab is a everywhere onboarding demo page, we should allow the script
        !HS_EVERYWHERE_DEMO_WEBSITES.some((demoUrl) => tabUrl.includes(demoUrl))
      );
    }
  );

  if (blocklistMatch) {
    return false;
  }

  if (tabUrl.includes(origins.sfdc.domain)) {
    return everywhereAllowlist.includes(HS_EVERYWHERE_WEBSITE_SALESFORCE_VALUE);
  }

  return everywhereAllowlist.includes(HS_EVERYWHERE_ALL_OTHER_SITES_VALUE);
}
