'use es6';

//import debugLogCache from 'SignalsExtension/globals/debugLogCache';
import {
  cacheErrorEntryToChromeStorage,
  initCacheWithChromeStorage,
} from 'sales-clients-common/utils/debugLogManagerChromeExt';

// eslint-disable-next-line hubspot-dev/no-confusing-browser-globals
self.addEventListener('error', (error, url, line) => {
  cacheErrorEntryToChromeStorage({
    message: error,
    detail: `${url} (${line})`,
  });
});

export const initDebugLog = () => {
  //Since the service worker turns on/off randomly, debug log cache(extension errors & httplogs) is maintained in chrome storage.
  //Whenever service worker is turned on, `initCache` is initialized with cache that exists in chrome storage.
  initCacheWithChromeStorage();
};
