'use es6';

import chrome from 'SignalsExtension/globals/chrome';
import { LogInfo } from 'sales-clients-common/utils/reportError';

let signUpWindowId = null;
let signUpTabId = null;
const SIGNUP_COMPLETE_PATHS = ['getting-started', 'mail.google.com'];
const SIGNUP_CHECK_YOUR_EMAIL_PATHS = ['check-your-email-go-to-mail'];

const isSignupCompletedUrl = (url) => {
  return SIGNUP_COMPLETE_PATHS.some((path) => url.includes(path));
};

const isCheckYourEmailUrl = (url) => {
  return SIGNUP_CHECK_YOUR_EMAIL_PATHS.some((path) => url.includes(path));
};

// This method opens sign up in a popup window, and then closes it once sign up is "done"
// (defined as reaching the getting-started or mail.google.com URL)
// We specifically do this because signup sets HS cookies before actual sign up is finished,
// so we need to leave the window open even after gmail has refreshed
export const openSignupHandler = ({ windowOptions, url }) => {
  chrome.windows
    .create({
      url,
      ...windowOptions,
    })
    .then((win) => {
      signUpWindowId = win.id;
      signUpTabId = win.tabs[0] && win.tabs[0].id;
    })
    .catch((err) => {
      LogInfo({
        error: err,
        fingerprint: ['onboarding/openSignupHandler'],
        tags: { source: 'chrome.windows.create' },
        extraData: {
          params: [
            {
              url,
              ...windowOptions,
            },
          ],
        },
      });
    });

  chrome.tabs.onUpdated.addListener((tabId, changeInfo) => {
    if (tabId === signUpTabId) {
      if (
        changeInfo.url &&
        (isSignupCompletedUrl(changeInfo.url) ||
          isCheckYourEmailUrl(changeInfo.url))
      ) {
        chrome.windows.remove(signUpWindowId);
      }
    }
  });
  return null;
};
