'use es6';

import { throttle } from 'underscore';
import logger from 'SignalsExtension/utils/logger';
import {
  requestUpdateCheck,
  getManifestVersion,
} from 'sales-clients-common/chrome/safeRuntime';
import { LogInfo } from 'sales-clients-common/utils/reportError';

export const ONE_MINUTE = 60 * 1000;
export const FOUR_HOURS = 4 * 60 * ONE_MINUTE;

export const EXTENSION_VERSION = getManifestVersion();

export const checkForUpdate = throttle(() => {
  logger.log('Requesting update check');
  requestUpdateCheck()
    .then((status, param = {}) => {
      logger.log(`Update check result: ${status} ${param.version}`);
    })
    .catch((err) => {
      LogInfo({
        error: err,
        fingerprint: ['updateManager/checkForUpdate'],
        tags: { source: 'requestUpdateCheck' },
      });
    });
}, FOUR_HOURS);
