// @ts-expect-error untyped
import { chrome } from 'sales-clients-common/globals/window';
import { LogInfo, LogError } from 'sales-clients-common/utils/reportError';
import { RUNTIME_ERROR_THRESHOLD } from '../constants/chrome';
import { handleInternalCallback } from './utils';
import {
  MessageListenerCallback,
  Port,
  OnInstalledReason,
  RequestUpdateResult,
  RequestUpdateCallback,
} from './types';

export const SEND_MESSAGE = 'SEND_MESSAGE';
export const ON_MESSAGE_LISTENER = 'ON_MESSAGE_LISTENER';
export const ON_REMOVE_MESSAGE_LISTENER = 'ON_REMOVE_MESSAGE_LISTENER';
export const ON_CONNECT_LISTENER = 'ON_CONNECT_LISTENER';
export const ON_INSTALLED_LISTENER = 'ON_INSTALLED_LISTENER';
export const ON_UPDATE_AVAILABLE_LISTENER = 'ON_UPDATE_AVAILABLE_LISTENER';
export const REQUEST_UPDATE_CHECK = 'REQUEST_UPDATE_CHECK';
export const CONNECT = 'CONNECT';
export const RELOAD = 'RELOAD';
export const LAST_ERROR = 'LAST_ERROR';
export const GET_MANIFEST_VERSION = 'GET_MANIFEST_VERSION';
export const GET_EXTENSION_ID = 'GET_EXTENSION_ID';
type Method =
  | typeof SEND_MESSAGE
  | typeof ON_MESSAGE_LISTENER
  | typeof ON_REMOVE_MESSAGE_LISTENER
  | typeof ON_CONNECT_LISTENER
  | typeof CONNECT
  | typeof RELOAD
  | typeof LAST_ERROR
  | typeof GET_MANIFEST_VERSION
  | typeof GET_EXTENSION_ID
  | typeof ON_INSTALLED_LISTENER
  | typeof ON_UPDATE_AVAILABLE_LISTENER
  | typeof REQUEST_UPDATE_CHECK;

// This method attempts to call the chrome.runtime methods directly, but logs a generic sentry error on failure
// in order to allow us to track how often this fails, without getting individual sentries per
// method failure
export const callRuntimeMethodSafe = (method: Method, ...args: any[]) => {
  try {
    switch (method) {
      case SEND_MESSAGE:
        return chrome.runtime.sendMessage(...args);
      case ON_MESSAGE_LISTENER:
        return chrome.runtime.onMessage.addListener(...args);
      case ON_REMOVE_MESSAGE_LISTENER:
        return chrome.runtime.onMessage.removeListener(...args);
      case ON_CONNECT_LISTENER:
        return chrome.runtime.onConnect.addListener(...args);
      case CONNECT:
        return chrome.runtime.connect(...args);
      case RELOAD:
        return chrome.runtime.reload();
      case LAST_ERROR:
        return chrome.runtime.lastError;
      case GET_MANIFEST_VERSION:
        return chrome.runtime.getManifest().version;
      case GET_EXTENSION_ID:
        return chrome.runtime.id;
      case ON_INSTALLED_LISTENER:
        return chrome.runtime.onInstalled.addListener(...args);
      case ON_UPDATE_AVAILABLE_LISTENER:
        return chrome.runtime.onUpdateAvailable.addListener(...args);
      case REQUEST_UPDATE_CHECK:
        return chrome.runtime.requestUpdateCheck(...args);
      default:
        return null;
    }
  } catch (error) {
    // segment missing chrome runtime error into a new Sentry error.
    if (!chrome.runtime) {
      if (!sessionStorage.runtimeError) {
        sessionStorage.runtimeError = 1;
      } else if (sessionStorage.runtimeError > RUNTIME_ERROR_THRESHOLD) {
        LogInfo({
          error,
          fingerprint: ['chrome runtime/storage is undefined'],
          tags: { runtimeUndefined: method },
          extraData: {
            runtimeUndefined: method,
            messageString: 'SafeRuntime chrome.runtime is undefined',
          },
        });
      } else {
        sessionStorage.runtimeError =
          parseInt(sessionStorage.runtimeError, 10) + 1;
      }
      return null;
    }

    // This should be safe - log as error
    LogError({
      error,
      fingerprint: [`Error in chrome safeRuntime`, method], // Group by method
      tags: { failingRuntimeMethod: method },
      extraData: {
        failingRuntimeMethod: method,
        errorMessageString: error instanceof Error ? error.message : 'unknown',
        messageString: 'Error in chrome safeRuntime',
      },
    });
    return null;
  }
};

export const isMv3 = () => {
  try {
    return chrome.runtime.getManifest().manifest_version >= 3;
  } catch {
    return false;
  }
};

export const addMessageListener = (callback: MessageListenerCallback): void =>
  callRuntimeMethodSafe(ON_MESSAGE_LISTENER, callback);

export const removeMessageListener = (
  callback: MessageListenerCallback
): void => callRuntimeMethodSafe(ON_REMOVE_MESSAGE_LISTENER, callback);

export const addConnectListener = (callback: (port: Port) => void): void =>
  callRuntimeMethodSafe(ON_CONNECT_LISTENER, callback);

export const getLastError = (): Error | undefined =>
  callRuntimeMethodSafe(LAST_ERROR);

export function sendMessage<T>(
  message: any,
  options?: { includeTlsChannelId?: boolean }
): Promise<T>;
/**
 * @deprecated Please use promises instead of callbacks
 */
export function sendMessage<T>(
  message: any,
  options?: { includeTlsChannelId?: boolean },
  callback?: (response: T) => void
): Promise<T>;
export function sendMessage<T>(
  message: any,
  options?: { includeTlsChannelId?: boolean },
  callback?: (response: T) => void
): Promise<T> {
  if (callback || !isMv3()) {
    return new Promise((resolve, reject) => {
      const internalCallback = (response: any): void =>
        handleInternalCallback(callback, response, resolve, reject);
      callRuntimeMethodSafe(SEND_MESSAGE, message, options, internalCallback);
    });
  }
  return callRuntimeMethodSafe(SEND_MESSAGE, message, options);
}

export const connect = (): Port => callRuntimeMethodSafe(CONNECT);

export const reload = (): void => callRuntimeMethodSafe(RELOAD);

export const getManifestVersion = (): string =>
  callRuntimeMethodSafe(GET_MANIFEST_VERSION);

export const getExtensionId = (): string =>
  callRuntimeMethodSafe(GET_EXTENSION_ID);
// Master branch of SigExt uses getId, need to keep this for backwards compatibility until MV3 release
/**
 * @deprecated Please use getExtensionId instead
 */
export const getId = (): string => getExtensionId();

export const addInstalledListener = (
  callback: (details: {
    id?: string;
    previousVersion?: string;
    reason: OnInstalledReason;
  }) => void
): void => callRuntimeMethodSafe(ON_INSTALLED_LISTENER, callback);

export const addUpdateAvailableListener = (
  callback: (details: { version: string }) => void
): void => callRuntimeMethodSafe(ON_UPDATE_AVAILABLE_LISTENER, callback);

export function requestUpdateCheck(): Promise<RequestUpdateResult>;
/**
 * @deprecated Please use promises instead of callbacks
 */
export function requestUpdateCheck(
  callback?: RequestUpdateCallback
): Promise<RequestUpdateResult>;
export function requestUpdateCheck(
  callback?: RequestUpdateCallback
): Promise<RequestUpdateResult> {
  if (callback || !isMv3()) {
    return new Promise((resolve, reject) => {
      const internalCallback = (response: RequestUpdateResult): void =>
        handleInternalCallback(callback, response, resolve, reject);
      callRuntimeMethodSafe(SEND_MESSAGE, internalCallback);
    });
  }
  return callRuntimeMethodSafe(REQUEST_UPDATE_CHECK);
}
