import { Container } from 'sales-ext-common/data/Container';
import { ExtensionContext } from 'sales-clients-common/chrome/types';

type SidePanelsMap = { [tabId: string]: ExtensionContext };
const sidePanelContainer = new Container<SidePanelsMap>({});

export const setSidePanels = (contexts: ExtensionContext[]) => {
  const sidePanelsMap: SidePanelsMap = contexts.reduce((acc, context) => {
    let sidePanelUrl;
    let sidePanelTabId;
    try {
      sidePanelUrl = new URL(context.documentUrl || '');
      sidePanelTabId = sidePanelUrl.searchParams.get('tabId');
    } catch (err) {
      return acc;
    }
    return {
      /* eslint-disable-next-line hubspot-dev/no-reduce-accumulator-copy */
      ...acc,
      ...(sidePanelTabId && { [sidePanelTabId]: context }),
    };
  }, {});
  sidePanelContainer.set(sidePanelsMap);
};

export const getSidePanel = (tabId: string) => {
  const sidePanelsMap = sidePanelContainer.get() || {};
  return sidePanelsMap[tabId];
};
