import {
  HS_EVERYWHERE_WEBSITE_ALLOWLIST_KEY,
  EverywhereWebsiteAllowlistValues,
  HS_EVERYWHERE_DEFAULT_WEBSITE_ALLOWLIST,
  HS_EVERYWHERE_WEBSITE_BLOCKLIST_KEY,
  HS_EVERYWHERE_SETTINGS_CARD_VISIBILITY_PREFERENCES_KEY,
  DEFAULT_EVERYWHERE_SETTINGS_VISIBILITY_PREFERENCES,
  SettingsCard,
  HS_EVERYWHERE_COPILOT_ENABLED_SETTINGS,
} from '../constants/chromeStorage';
import { HS_EVERYWHERE_DEFAULT_WEBSITE_BLOCKLIST } from '../constants/everywhereDomains';
import { localGet, localSet } from 'sales-clients-common/chrome/safeStorage';
import { Tab } from 'sales-clients-common/chrome/types';
// @ts-expect-error untyped
import chrome from 'sales-ext-common/globals/chrome';
import { sendMessage } from 'sales-clients-common/chrome/safeRuntime';

type LocalStorageType = {
  [HS_EVERYWHERE_WEBSITE_ALLOWLIST_KEY]?: EverywhereWebsiteAllowlistValues[];
  [HS_EVERYWHERE_WEBSITE_BLOCKLIST_KEY]?: string[];
  [HS_EVERYWHERE_SETTINGS_CARD_VISIBILITY_PREFERENCES_KEY]?: Record<
    SettingsCard,
    boolean
  >;
  [HS_EVERYWHERE_COPILOT_ENABLED_SETTINGS]?: boolean;
};

export function getEverywhereWebsiteAllowlist(): Promise<
  EverywhereWebsiteAllowlistValues[]
> {
  return localGet(HS_EVERYWHERE_WEBSITE_ALLOWLIST_KEY).then(
    (data?: LocalStorageType) =>
      (data && data[HS_EVERYWHERE_WEBSITE_ALLOWLIST_KEY]) ||
      HS_EVERYWHERE_DEFAULT_WEBSITE_ALLOWLIST
  );
}

export function setEverywhereWebsiteAllowlist(
  allowlist: EverywhereWebsiteAllowlistValues[]
) {
  return localSet({ [HS_EVERYWHERE_WEBSITE_ALLOWLIST_KEY]: allowlist });
}

export function getEverywhereWebsiteBlocklist(): Promise<string[]> {
  return localGet(HS_EVERYWHERE_WEBSITE_BLOCKLIST_KEY).then(
    (data?: LocalStorageType) =>
      (data && data[HS_EVERYWHERE_WEBSITE_BLOCKLIST_KEY]) ||
      HS_EVERYWHERE_DEFAULT_WEBSITE_BLOCKLIST
  );
}

export function setEverywhereWebsiteBlocklist(blocklist: string[]) {
  return localSet({ [HS_EVERYWHERE_WEBSITE_BLOCKLIST_KEY]: blocklist });
}

export function getEverywhereSettingsCardVisibilityPreferences(): Promise<
  Record<SettingsCard, boolean>
> {
  return localGet(HS_EVERYWHERE_SETTINGS_CARD_VISIBILITY_PREFERENCES_KEY).then(
    (data?: LocalStorageType) =>
      (data && data[HS_EVERYWHERE_SETTINGS_CARD_VISIBILITY_PREFERENCES_KEY]) ||
      DEFAULT_EVERYWHERE_SETTINGS_VISIBILITY_PREFERENCES
  );
}

export function setEverywhereSettingsCardVisibilityPreferences(
  preferences: Record<SettingsCard, boolean>
) {
  return localSet({
    [HS_EVERYWHERE_SETTINGS_CARD_VISIBILITY_PREFERENCES_KEY]: preferences,
  });
}

export function getCopilotSettings(): Promise<boolean> {
  return localGet(HS_EVERYWHERE_COPILOT_ENABLED_SETTINGS).then(
    (data?: LocalStorageType) => {
      return data && data[HS_EVERYWHERE_COPILOT_ENABLED_SETTINGS] !== undefined
        ? data[HS_EVERYWHERE_COPILOT_ENABLED_SETTINGS]
        : true;
    }
  );
}

export function setCopilotSettings(value: boolean) {
  informRuntimeAndTabs('switchedCopilotSettingsFromPopup', value);
  return localSet({
    [HS_EVERYWHERE_COPILOT_ENABLED_SETTINGS]: value,
  });
}

function informRuntimeAndTabs(method: string, value: any) {
  const message = { method, value };
  // Send message to runtime
  sendMessage(message).catch((ex: Error) => {
    console.error('sendMessage: informRuntimeAndTabs', ex);
  });

  // send messages to tabs. Many of these might not have listeners setup so we swallow the errors
  chrome.tabs
    .query({})
    .then((tabs: Tab[]) => {
      tabs.forEach((tab) => {
        chrome.tabs.sendMessage(tab.id, message).catch((ex: Error) => {
          console.info(`Error sending message to tab ${tab.id}:`, ex);
        });
      });
    })
    .catch((ex: Error) => {
      console.error(`Error querying tabs:`, ex);
    });
}
