import { useContext, createContext, ReactNode, useMemo, Context } from 'react';
import {
  useQuery as useApolloQuery,
  useMutation as useApolloMutation,
  useLazyQuery as useLazyApolloQuery,
  useSuspenseQuery as useApolloSuspenseQuery,
  useBackgroundQuery as useApolloBackgroundQuery,
  useReadQuery as useApolloReadQuery,
  useQueryRefHandlers,
  useLoadableQuery,
  ApolloLink,
  ApolloClient,
  InMemoryCache,
  fromPromise,
  QueryHookOptions,
  QueryResult,
  LazyQueryHookOptions,
  LazyQueryResultTuple,
  MutationHookOptions,
  MutationTuple,
  SuspenseQueryHookOptions,
  BackgroundQueryHookOptions,
  UseSuspenseQueryResult,
  UseBackgroundQueryResult,
  UseReadQueryResult,
  QueryRef,
  TypePolicy,
  Operation,
  DefaultOptions,
  TypedDocumentNode,
  OperationVariables,
  Observable,
} from '@apollo/client';
import { DocumentNode, OperationDefinitionNode } from 'graphql';
import { EntityController } from './entityManagement/controller/types';

export interface DataFetchingClientContextValue {
  client?: DataFetchingClient;
}

export interface DataFetchingClientLinkOptions {
  client: DataFetchingClient;
}

export type DataFetchingClientFetcherContext = {
  client: DataFetchingClient;
} & Record<string, unknown>;

export type KeysOfUnion<T> = T extends T ? keyof T : never;

export type AvailableKeys<T> = KeysOfUnion<T>;

export const contextKey = Symbol.for('__DATA_FETCHING_CLIENT_CONTEXT__');

// Memoized context creation using the `createContext` function as a cache. Following a pattern from Apollo Client
function getDataFetchingClientContext(): Context<DataFetchingClientContextValue> {
  let context: React.Context<DataFetchingClientContextValue> = (
    createContext as any
  )[contextKey];
  if (!context) {
    context = createContext({});
    Object.defineProperty(createContext, contextKey, {
      value: context,
      enumerable: false,
      writable: false,
      configurable: true,
    });
    context.displayName = 'DataFetchingClientContext';
  }
  return context;
}

function useDataFetchingClient() {
  const { client } = useContext(getDataFetchingClientContext());

  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore process is declared in webpack-env, but doesn't impact this module's type externally so we can ignore in downstream builds
  if (process.env.NODE_ENV !== 'production') {
    if (!client) {
      throw new Error(
        'Cannot find context. Did you forget to configure `DataFetchingClientProvider`'
      );
    }
  }
  return client!;
}

function DataFetchingClientProvider({
  client,
  children,
}: {
  client: DataFetchingClient;
  children: ReactNode | ReactNode[] | null;
}) {
  const clientValue = useMemo(() => ({ client }), [client]);
  const DataFetchingClientContext = getDataFetchingClientContext();

  return (
    <DataFetchingClientContext.Provider value={clientValue}>
      {children}
    </DataFetchingClientContext.Provider>
  );
}

// See https://spec.graphql.org/June2018/#sec-Names
const isValidGraphQLName = (str: any) =>
  typeof str === 'string' && /^[_A-Za-z][_0-9A-Za-z]+$/.test(str);

const registeredQueries = new WeakMap<
  DocumentNode,
  {
    fieldName: string;
    withResultAlias: boolean;
    fetcher?: (
      variables: any,
      context: DataFetchingClientFetcherContext
    ) => unknown | PromiseLike<unknown>;
  }
>();

const getFieldName = (operation: DocumentNode) => {
  const registeredOperation = registeredQueries.get(operation);
  if (!registeredOperation) {
    throw new Error(
      '`data-fetching-client` only supports queries created with `registerQuery` or `registerMutation`.'
    );
  }
  const fieldName = registeredOperation.fieldName;
  return fieldName;
};

class DataFetchingClientLink extends ApolloLink {
  client: DataFetchingClient;
  constructor({ client }: DataFetchingClientLinkOptions) {
    super();
    this.client = client;
  }
  request({ variables, query, getContext }: Operation) {
    const context = getContext();
    const fieldDef = registeredQueries.get(query);
    if (!fieldDef) {
      throw new Error(
        '`data-fetching-client` only supports queries created with `registerQuery` or `registerMutation`.'
      );
    }

    const { fetcher, fieldName, withResultAlias } = fieldDef;

    return fromPromise(
      new Promise((resolve, reject) => {
        try {
          resolve(fetcher!(variables, { ...context, client: this.client }));
        } catch (error) {
          reject(error);
        }
      })
    ).map((result) => ({
      data: {
        [withResultAlias ? 'result' : fieldName]: result,
      },
    }));
  }
}

export interface OperationInfo {
  fieldName: string;
  variables: Record<string, any>;
  operationType: 'query' | 'mutation';
}

interface FetcherRejectionHandler {
  (reason: any, operationInfo: OperationInfo): void;
}

function createFetcherRejectionLink(
  onFetcherRejection: FetcherRejectionHandler
): ApolloLink {
  return new ApolloLink((operation, forward) => {
    return new Observable((observer) => {
      const sub = forward(operation).subscribe({
        next(result) {
          observer.next(result);
        },
        error(err) {
          observer.error(err);
          onFetcherRejection(err, getOperationInfo(operation));
        },
        complete() {
          observer.complete();
        },
      });

      return () => {
        sub.unsubscribe();
      };
    });
  });
}

function getOperationInfo(operation: Operation): OperationInfo {
  const operationDefinition = operation.query.definitions.find(
    (def): def is OperationDefinitionNode => def.kind === 'OperationDefinition'
  );

  if (
    !operationDefinition ||
    !(
      operationDefinition.operation === 'query' ||
      operationDefinition.operation === 'mutation'
    )
  ) {
    throw new Error('Could not determine operation info');
  }

  return {
    operationType: operationDefinition.operation,
    fieldName: getFieldName(operation.query),
    variables: operation.variables,
  };
}

export interface DataFetchingClientOptions {
  queryFieldPolicies?: TypePolicy['fields'];
  mutationFieldPolicies?: TypePolicy['fields'];
  assumeImmutableResults?: boolean;
  defaultOptions?: DefaultOptions;
  connectToDevTools?: boolean;
  /**
   * A function that will be called when a fetcher rejects. This can be used to log errors.
   */
  onFetcherRejection?: FetcherRejectionHandler;

  /**
   * @deprecated Enable experimental entity management functionality
   *
   * Please ping `@devon` in slack before use  this api is not stable.
   *
   * If you use this without asking, at some point I will **personally** break your app.
   */
  unstable__createEntityController?: () => EntityController;
}

class DataFetchingClient extends ApolloClient<object> {
  entityController?: EntityController;

  constructor({
    queryFieldPolicies,
    mutationFieldPolicies,
    assumeImmutableResults,
    defaultOptions,
    connectToDevTools,
    onFetcherRejection,
    unstable__createEntityController,
  }: DataFetchingClientOptions = {}) {
    super({
      defaultOptions,
      cache: new InMemoryCache({
        typePolicies: {
          Query: {
            fields: {
              ...queryFieldPolicies,
            },
          },
          Mutation: {
            fields: {
              ...mutationFieldPolicies,
            },
          },
        },
        addTypename: false,
      }),
      assumeImmutableResults,
      connectToDevTools,
    });

    if (unstable__createEntityController) {
      this.entityController = unstable__createEntityController();
    }

    this.setLink(
      onFetcherRejection
        ? ApolloLink.from([
            createFetcherRejectionLink(onFetcherRejection),
            new DataFetchingClientLink({ client: this }),
          ])
        : new DataFetchingClientLink({ client: this })
    );

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore process is declared in webpack-env, but doesn't impact this module's type externally so we can ignore in downstream builds
    if (process.env.NODE_ENV !== 'production') {
      (window as any).__DATA_FETCHING_CLIENT__ = this;
    }
  }
}

export type RegisteredQuery<
  TFieldName extends string,
  TVariables extends OperationVariables = Record<string, unknown>,
  TResponse = unknown,
> = TypedDocumentNode<Record<TFieldName, TResponse>, TVariables>;

export interface RegisterQueryInput<
  TFieldName extends string,
  TVariables,
  TResponse,
> {
  fieldName: TFieldName;
  args?: AvailableKeys<TVariables>[];
  fetcher?: (
    variables: TVariables,
    ctx: DataFetchingClientFetcherContext
  ) => TResponse | PromiseLike<TResponse>;
}

function registerOperation<
  TFieldName extends string,
  TVariables extends OperationVariables,
  TResponse,
>(
  operation: 'query' | 'mutation',
  {
    fieldName,
    args = [],
    fetcher,
  }: RegisterQueryInput<TFieldName, TVariables, TResponse>,
  withResultAlias: boolean
) {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore process is declared in webpack-env, but doesn't impact this module's type externally so we can ignore in downstream builds
  if (process.env.NODE_ENV !== 'production') {
    if (!isValidGraphQLName(fieldName)) {
      throw new Error(
        `"${fieldName}" is not a valid GraphQL Name and cannot be used as \`fieldName\`. https://spec.graphql.org/June2018/#sec-Names`
      );
    }
    args.forEach((arg) => {
      if (!isValidGraphQLName(arg)) {
        throw new Error(
          `"${String(
            arg
          )}" is not a valid GraphQL Name and cannot be used in \`args\`. https://spec.graphql.org/June2018/#sec-Names`
        );
      }
    });
  }

  const query = {
    kind: 'Document',
    definitions: [
      {
        kind: 'OperationDefinition',
        operation,
        name: {
          kind: 'Name',
          value: fieldName,
        },
        variableDefinitions: args.map((arg) => ({
          kind: 'VariableDefinition',
          variable: {
            kind: 'Variable',
            name: {
              kind: 'Name',
              value: arg,
            },
          },
          type: {
            kind: 'NamedType',
            name: {
              kind: 'Name',
              value: 'JSON',
            },
          },
        })),
        selectionSet: {
          kind: 'SelectionSet',
          selections: [
            {
              kind: 'Field',
              name: {
                kind: 'Name',
                value: fieldName,
              },
              alias: withResultAlias
                ? { kind: 'Name', value: 'result' }
                : undefined,
              directives:
                typeof fetcher === 'undefined'
                  ? [
                      {
                        kind: 'Directive',
                        name: {
                          kind: 'Name',
                          value: 'client',
                        },
                      },
                    ]
                  : [],
              arguments: args.map((arg) => ({
                kind: 'Argument',
                name: {
                  kind: 'Name',
                  value: arg,
                },
                value: {
                  kind: 'Variable',
                  name: {
                    kind: 'Name',
                    value: arg,
                  },
                },
              })),
            },
          ],
        },
      },
    ],
  } as DocumentNode;

  registeredQueries.set(query, {
    fetcher,
    withResultAlias,
    fieldName,
  });

  return query as RegisteredQuery<TFieldName | 'result', TVariables, TResponse>;
}

function registerQuery<
  TFieldName extends string,
  TVariables extends Record<string, any>,
  TResponse,
>(
  options: RegisterQueryInput<TFieldName, TVariables, TResponse>
): RegisteredQuery<TFieldName, TVariables, TResponse> {
  return registerOperation('query', options, false);
}

function registerMutation<
  TFieldName extends string,
  TVariables extends OperationVariables,
  TResponse,
>(
  options: RegisterQueryInput<TFieldName, TVariables, TResponse>
): RegisteredQuery<TFieldName, TVariables, TResponse> {
  return registerOperation('mutation', options, false);
}

function useQuery<
  TFieldName extends string,
  TVariables extends OperationVariables,
  TResponse,
>(
  query: RegisteredQuery<TFieldName, TVariables, TResponse>,
  options?: QueryHookOptions<Record<TFieldName, TResponse>, TVariables>
): QueryResult<Record<TFieldName, TResponse>, TVariables> {
  const { client } = useContext(getDataFetchingClientContext());
  return useApolloQuery(query, {
    client,
    ...options,
  });
}

function useLazyQuery<
  TFieldName extends string,
  TVariables extends OperationVariables,
  TResponse,
>(
  query: RegisteredQuery<TFieldName, TVariables, TResponse>,
  options?: LazyQueryHookOptions<Record<TFieldName, TResponse>, TVariables>
): LazyQueryResultTuple<Record<TFieldName, TResponse>, TVariables> {
  const { client } = useContext(getDataFetchingClientContext());
  return useLazyApolloQuery(query, {
    client,
    ...options,
  });
}

function useMutation<
  TFieldName extends string,
  TVariables extends OperationVariables,
  TResponse,
>(
  query: RegisteredQuery<TFieldName, TVariables, TResponse>,
  options?: MutationHookOptions<Record<TFieldName, TResponse>, TVariables>
): MutationTuple<Record<TFieldName, TResponse>, TVariables> {
  const { client } = useContext(getDataFetchingClientContext());
  return useApolloMutation(query, {
    client,
    ...options,
  });
}

function useSuspenseQuery<
  TFieldName extends string,
  TVariables extends OperationVariables,
  TResponse,
>(
  query: RegisteredQuery<TFieldName, TVariables, TResponse>,
  options?: SuspenseQueryHookOptions<Record<TFieldName, TResponse>, TVariables>
): UseSuspenseQueryResult<Record<TFieldName, TResponse>, TVariables> {
  const client = useDataFetchingClient();
  return useApolloSuspenseQuery(query, {
    client,
    ...options,
  });
}

function useBackgroundQuery<
  TFieldName extends string,
  TVariables extends OperationVariables,
  TResponse,
>(
  query: RegisteredQuery<TFieldName, TVariables, TResponse>,
  options?: BackgroundQueryHookOptions<
    Record<TFieldName, TResponse>,
    TVariables
  >
): [
  QueryRef<Record<TFieldName, TResponse>> | undefined,
  UseBackgroundQueryResult<Record<TFieldName, TResponse>, TVariables>,
] {
  const client = useDataFetchingClient();
  return useApolloBackgroundQuery(query, {
    client,
    ...options,
  });
}

function useReadQuery<TData>(
  queryRef: QueryRef<TData>
): UseReadQueryResult<TData> {
  if (!queryRef) {
    throw new Error(
      'useReadQuery must be called with a query ref. If you are using `skip`, this component should be conditionally rendered.'
    );
  }
  return useApolloReadQuery(queryRef);
}

export {
  getDataFetchingClientContext,
  DataFetchingClientProvider,
  DataFetchingClient,
  registerQuery,
  registerMutation,
  registerOperation,
  useDataFetchingClient,
  useQuery,
  useLazyQuery,
  useMutation,
  useSuspenseQuery,
  useBackgroundQuery,
  useReadQuery,
  useQueryRefHandlers,
  useLoadableQuery,
  getFieldName,
};
