'use es6';

import HubApiHttpClientRemote from 'SignalsExtension/api/HubApiHttpClientRemote';
import HubApiWithoutCsrfHttpClientRemote from 'SignalsExtension/api/HubApiWithoutCsrfHttpClientRemote';
import { cacheHttpLogToChromeStorage } from 'sales-clients-common/utils/debugLogManagerChromeExt';
import { apiRetryQueue } from 'SignalsExtension/api/apiRetry';
import { setUserCookieExpired } from 'SignalsExtension/background/models/user';
import { LogMessage } from 'sales-clients-common/utils/reportError';
import { getBaseUrl } from 'SignalsExtension/utils/getBaseUrl';
import { DataFetchingClient, registerQuery } from 'data-fetching-client';
import { userUngatedFor } from 'SignalsExtension/data/salesGates';
import { gate } from 'hub-http/gates';

const URL_PROTOCOL_PATTERN = /^(https?|ftp):\/\//i;

const fetchRestOperation = ({ requestData }) => {
  const client = requestData.withoutCsrf
    ? HubApiWithoutCsrfHttpClientRemote
    : HubApiHttpClientRemote;

  return client[requestData.action](
    requestData.relativeUrl,
    requestData.options
  )
    .then((response) => {
      cacheHttpLogToChromeStorage({ message: requestData.relativeUrl });
      return Promise.resolve(response);
    })
    .catch((err) => {
      cacheHttpLogToChromeStorage({
        message: err.message || err,
        isError: true,
      });
      let coreRequest = null;

      if (
        requestData.options &&
        requestData.options.sigextApiRetry &&
        !requestData.options.sigextApiRetry.excludeStatus.includes(err.status)
      ) {
        apiRetryQueue(requestData, err);
        if (requestData.options.sigextApiRetry.coreRequest) {
          coreRequest = requestData.options.sigextApiRetry.coreRequest;
        }
      }

      // Handle expired cookie
      if (err.status === 401) {
        setUserCookieExpired();

        LogMessage({
          message: 'Cookie has expired.',
          stacktrace: true,
          tags: {
            method: requestData.action,
            relativeUrl: coreRequest || getBaseUrl(requestData.relativeUrl),
            source: 'expired_cookie',
            status: 'status' in err ? err.status : 'unknown status error',
          },
          extraData: {
            errorMessage: err.message || '',
            errorData: err.data || '',
            errorHeaders: err.headers || '',
            method: requestData.action,
            requestData: JSON.stringify(requestData),
            status: 'status' in err ? err.status : 'unknown status error',
            requestUrl: requestData.relativeUrl,
          },
        });
      }
      return Promise.reject(err);
    });
};

const dataFetchingClient = new DataFetchingClient({
  connectToDevTools: false,
});

const apolloQuery = registerQuery({
  fieldName: 'apolloQuery',
  args: ['requestData'],
  fetcher: fetchRestOperation,
});

export const clearDataFetchingClientCache = () => {
  dataFetchingClient.resetStore().catch((err) => {
    console.error('Error resetting Apollo Client cache: ', err);
  });
};

export const apiProxyMethodHandler = (requestData, sendResponse) => {
  try {
    // Filter out url protocol so we only execute under HubSpot domain
    if (URL_PROTOCOL_PATTERN.test(requestData.relativeUrl)) {
      sendResponse({
        success: false,
        data: {
          message: `Invalid param: relativeUrl: ${requestData.relativeUrl}`,
        },
      });
    } else {
      const isUngatedForExtApolloGraphQL = userUngatedFor(
        gate('Clients:SigExt:ApolloGraphQL')
      );
      if (isUngatedForExtApolloGraphQL) {
        dataFetchingClient
          .query({
            query: apolloQuery,
            variables: {
              requestData,
            },
            // We are doing no-cache by default to avoid any unintended cache issues and save memory
            // If we want to use cache, we can pass fetchPolicy as cache-first
            fetchPolicy: requestData.options?.fetchPolicy || 'no-cache',
          })
          .then((response) => {
            sendResponse({
              success: true,
              data: response.data.apolloQuery,
            });
          })
          .catch((err) => {
            sendResponse({ success: false, data: err?.cause });

            if (err.status === 401) {
              setUserCookieExpired();
            }
          });
      } else {
        fetchRestOperation({ requestData })
          .then((response) => {
            sendResponse({
              success: true,
              data: response,
            });
          })
          .catch((err) => {
            sendResponse({ success: false, data: err });
          });
      }
    }
  } catch (ex) {
    sendResponse({
      success: false,
      data: { message: `Error processing request: ${ex}` },
    });
  }
  return true;
};
