/* This is mostly copied from https://git.hubteam.com/HubSpot/quartz/blob/master/quartz/static/js/iplt-embed.ts
 * with the following changes:
   - Uses custom API client to send data to the server instead of sendBeacon (unavailable in the extension)
   - Updated sessionId (cru) logic for the long running extension sessions
   - Replaced origin url with the extension id
   - Disabled time out mechanism due to long running extension sessions
 */

import HubApiWithoutImmutableHttpClient from 'SignalsExtension/api/HubApiWithoutImmutableHttpClient';
import { getExtensionId } from 'sales-clients-common/chrome/safeRuntime';
import logger from 'SignalsExtension/utils/logger';
// @ts-expect-error missing types
import { sendToContentScripts } from 'SignalsExtension/background/backgroundMessaging';

if (!window.hubspot) {
  window.hubspot = {};
}

const MAX_REPORT_LOG_LENGTH = 50;
const MAX_LOGS_PER_SESSION = 1000;

const isSessionResourceTrackingEnabled = true;
let isTelemetryEnabled = true;

let resourcePO: PerformanceObserver | undefined;
let performanceTelemetryInitialized = false;
let _portalId: string;
let _sessionId: string | null;
let _sessionStartDay: number | null;

// Report number
let n = 1;

let logs: any[] = [];
let logsSent = 0;

// Get the current day as a number. This should be enough to detect a new day.
const getCurrentDay = () => {
  return new Date().getDay();
};

const setupSessionId = () => {
  try {
    _sessionId = crypto.randomUUID();
  } catch (e) {
    _sessionId = fallbackUUID();
  }
  (window as any).hubspot.sessionId = _sessionId;

  // Store the day when this session was created
  _sessionStartDay = getCurrentDay();

  sendToContentScripts('sessionId', {
    sessionId: _sessionId,
  });

  // Reset the counters
  logsSent = 0;
  n = 1;

  logger.debug('[PerformanceTelemetry]: setupSessionId', _sessionId);
};

const extensionId = getExtensionId();

const cartographerApiPath = 'api/cartographer/v1/ipl';

const performanceResourceTimingKeys = [
  'name',
  'responseStatus',
  'fetchStart',
  'responseEnd',
  'serverTiming',
  // 'entryType',
  'startTime',
  // derived from the responseEnd and the startTime properties
  // 'duration',
  'initiatorType',
  'nextHopProtocol',
  // 'workerStart',
  'redirectStart',
  'redirectEnd',
  'domainLookupStart',
  'domainLookupEnd',
  'connectStart',
  'connectEnd',
  'secureConnectionStart',
  'requestStart',
  'responseStart',
  'transferSize',
  'encodedBodySize',
  'decodedBodySize',
  'renderBlockingStatus',
  // only used for interim responses like 100 Continue or 103 Early Hints which we don't make use of
  // https://developer.mozilla.org/en-US/docs/Web/API/PerformanceResourceTiming/firstInterimResponseStart
  // 'firstInterimResponseStart',
];

interface ScriptInfo {
  firstSeen: number;
  timeout?: number;
}

// the TS types for the timing classes are missing a couple fields
interface CompletePerformanceResourceTiming extends PerformanceResourceTiming {
  responseStatus: number; // not in the native PerformanceResourceTiming interface pre TS 5.7 (https://developer.mozilla.org/en-US/docs/Web/API/PerformanceResourceTiming/responseStatus)
  renderBlockingStatus: 'blocking' | 'non-blocking' | null; // unsupported in Firefox (may be soon) & Safari (https://caniuse.com/mdn-api_performanceresourcetiming_renderblockingstatus)
}

const IPLTEvents = {
  TrackingStarted: 's',
  TrackingFinished: 'f',
  PerformanceResourceTiming: 'p',
  PerformanceNavigationTiming: 'n',
  ScriptLoaded: 'l',
  ScriptErrored: 'e',
  BeforeScriptTag: 'b',
  ScriptLoadTimeout: 't',
  DocumentReady: 'c',
  MaxLogOverflow: 'o',
  HitTimeBoundary: 'h',
  VisibilityChangeHidden: 'd',
  VisibilityChangeVisible: 'v',
  SessionLink: 'k',
} as const;

type Mark = [
  (
    | typeof IPLTEvents.TrackingStarted
    | typeof IPLTEvents.TrackingFinished
    | typeof IPLTEvents.DocumentReady
    | typeof IPLTEvents.HitTimeBoundary
    | typeof IPLTEvents.MaxLogOverflow
    | typeof IPLTEvents.VisibilityChangeHidden
    | typeof IPLTEvents.VisibilityChangeVisible
  ),
  number, // timestamp
];

type SessionLinkLog = [typeof IPLTEvents.SessionLink, string];

// From Performance API
type ServerTiming = [
  string | undefined,
  string | undefined,
  number | undefined,
];

type PerfLog =
  /**
   * 'p' - *p*erformance resource timing
   * 'n' - performance *n*avigation timing.
   */
  [
    (
      | typeof IPLTEvents.PerformanceResourceTiming
      | typeof IPLTEvents.PerformanceNavigationTiming
    ),
    ...(string | number | ServerTiming[] | null)[],
  ];

type ScriptElementEvent =
  | [
      typeof IPLTEvents.ScriptLoaded | typeof IPLTEvents.ScriptErrored,
      /* url: */ string,
      /* start: */ number,
      /* finish: */ number,
    ]
  | [typeof IPLTEvents.BeforeScriptTag, /* url: */ string, number];
type ScriptTimeoutEvent = [
  typeof IPLTEvents.ScriptLoadTimeout,
  /* url: */ string,
  number,
  number,
];

type Log =
  | Mark
  | PerfLog
  | ScriptElementEvent
  | ScriptTimeoutEvent
  | SessionLinkLog;

const ReportReasons = {
  AlwaysSent: 'a',
  SlowLoading: 's',
  VisibilityStateChanged: 'v',
  LogsFlushed: 'f',
  LogLimitReached: 'l',
} as const;

type ReportReasonType = (typeof ReportReasons)[keyof typeof ReportReasons];

type PendingScript = [/*url: */ string, /*firstSeen: */ number];

/** format for report sent to service */
interface IPLTReport {
  /** version */
  v: 0;
  /** time of report (performance.now()) */
  t: number;
  /** page url */
  u: string;
  /** crypto random uuid */
  cru: string | null;
  /** performance.timeOrigin */
  pto: number;
  /** Timestamp from Date.now() */
  dn: number;
  /** reason: see interface ReportReasons for all possible reasons. */
  r: ReportReasonType;
  /** report number */
  n: number;
  /** pending scripts */
  p: PendingScript[];
  /* log */
  l: Log[];
  /* portal id */
  pid?: number;
}

function now() {
  return Math.round(performance.now());
}

function serverTimingToJSON({
  serverTiming,
}: {
  serverTiming?: readonly PerformanceServerTiming[];
}): ServerTiming[] {
  return serverTiming
    ? // serverTiming is in the TS type, but only supported in safari 16.6
      serverTiming.map((st) => [st.name, st.description, st.duration])
    : [];
}

const pendingByUrl = new Map<string, ScriptInfo>();

const filterResourceTimingEntries = (
  entry: CompletePerformanceResourceTiming
) => {
  if (isSessionResourceTrackingEnabled) {
    // Don't self track cartographer requests
    return !(entry.name && entry.name.endsWith(cartographerApiPath));
  }

  return entry.initiatorType === 'script';
};

/** Fallback UUID
 * - Discussion in https://git.hubteam.com/HubSpot/quartz/pull/204#issuecomment-6340901
 * - HubSpot example usage, different format (no hyphens): https://git.hubteam.com/HubSpot/raven-hubspot/blob/master/raven/static/js/utils.js#L182
 * - StackOverflow, example implementation: https://stackoverflow.com/questions/105034/how-do-i-create-a-guid-uuid/2117523#2117523
 *
 * @returns { string | null } Page Load ID as UUID.
 **/
function fallbackUUID() {
  try {
    /* eslint-disable no-bitwise */
    return '10000000-1000-4000-8000-100000000000'.replace(/[018]/g, (c) => {
      const randomNumber =
        crypto && crypto.getRandomValues
          ? crypto.getRandomValues(new Uint8Array(1))[0] & 15
          : Math.random() * 16;
      const r = randomNumber | 0;
      const v = c === '0' ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
    /* eslint-enable no-bitwise */
  } catch (_) {
    return null;
  }
}

function isUrl(maybeUrl: string) {
  try {
    const url = new URL(maybeUrl);
    return !!url;
  } catch (e) {
    return false;
  }
}

// OAuth 2.0 and OpenID Connect parameters that should be redacted
const OAUTH_OIDC_PARAMS = new Set([
  // OAuth 2.0 core parameters
  'access_token',
  'token_type',
  'expires_in',
  'refresh_token',
  'scope',
  'state',
  'error',
  'error_description',
  'error_uri',
  'code',
  'grant_type',
  'redirect_uri',
  'client_id',
  'client_secret',

  // OpenID Connect parameters
  'id_token',
  'session_state',
  'nonce',
  'acr_values',
  'response_type',
  'response_mode',
  'display',
  'prompt',
  'max_age',
  'ui_locales',
  'claims_locales',
  'id_token_hint',
  'login_hint',
  'acr_values',
  'claims',
  'registration',
  'request',
  'request_uri',

  // Additional OAuth extensions
  'code_challenge',
  'code_challenge_method',
  'code_verifier',
  'token',
  'assertion',
  'client_assertion',
  'client_assertion_type',
]);

function sendData(payload: any) {
  const body = JSON.stringify(payload);
  HubApiWithoutImmutableHttpClient.post(cartographerApiPath, {
    headers: {
      'Content-Type': 'text/plain',
    },
    data: body,
  }).catch(() => {
    // Ignore errors.
  });
}

function redactUrl(url: string) {
  let urlObj: URL;
  try {
    urlObj = new URL(url);
  } catch (e) {
    // if the url is invalid, return the original string
    return url;
  }

  if (urlObj.hash) {
    const hash = urlObj.hash.slice(1);
    const hashEntries = hash.split('&');
    const redactedHashEntries = hashEntries.map((entry) => {
      const [key, value] = entry.split('=');
      if (value) {
        return `${key}=*REDACTED*`;
      }
      return key;
    });

    urlObj.hash = `#${redactedHashEntries.join('&')}`;
  }

  urlObj.searchParams.forEach((value, key) => {
    // Redact OAuth/OIDC parameters and URL values
    if (OAUTH_OIDC_PARAMS.has(key.toLowerCase()) || isUrl(value)) {
      urlObj.searchParams.set(key, '*REDACTED*');
    }
  });

  return urlObj.toString();
}

function sendReport(reason: ReportReasonType) {
  if (!isTelemetryEnabled) {
    return;
  }

  if (logs.length === 0 && pendingByUrl.size === 0) {
    return;
  }

  const payload: IPLTReport = {
    v: 0,
    r: reason,
    t: now(),
    n: n++,
    cru: _sessionId,
    // not supported in safari 11
    // eslint-disable-next-line compat/compat
    pto: performance.timeOrigin,
    dn: Date.now(),
    u: `chrome-extension://${extensionId}`,
    l: logs,
    pid: parseInt(_portalId, 10),
    p: [...pendingByUrl.entries()].map(([url, v]) => [url, v.firstSeen]),
  };

  sendData(payload);
}

const trySendReport = (reason: ReportReasonType) => {
  sendReport(reason);
  logsSent += logs.length;
  logs = [];
};

const tryAddLog = (log: Log) => {
  // TODO: Rate limit this and invoke stopTracking when logging excessively. Add the following events as markers:
  // logs.push([IPLTEvents.MaxLogOverflow, now()]);
  // logs.push([IPLTEvents.TrackingFinished, now()]);
  const currentDay = getCurrentDay();

  // Cycle to new session if either condition is met:
  // 1. Maximum logs per session reached
  // 2. A new day has started (using local datetime)
  if (
    logs.length + logsSent >= MAX_LOGS_PER_SESSION ||
    (_sessionStartDay && currentDay !== _sessionStartDay)
  ) {
    // Cycle to new session
    setupSessionId();
  }

  logs.push(log);

  // Send report when we have accumulated enough logs
  if (logs.length >= MAX_REPORT_LOG_LENGTH) {
    trySendReport(ReportReasons.LogsFlushed);
  }
  // else let the logs accumulate
};

const logTiming = (code: PerfLog[0], keys: string[], o: any) => {
  tryAddLog([
    code,
    ...keys.map((k) => {
      if (k === 'serverTiming') {
        return serverTimingToJSON(
          o as { serverTiming?: readonly PerformanceServerTiming[] }
        );
      }

      const v = o[k];

      if (k === 'name') {
        return redactUrl(v as string);
      }

      if (v === null) {
        return null;
      }

      /**
       * Note: All remaining values are of type string or number, but that cannot inferred because of the union type. Make sure that remains
       * the case (or amend this line) if types other than CompletePerformanceResourceTiming and CompletePerformanceNaviationTiming
       * are extended by T
       */
      return typeof v === 'number' ? Math.round(v) : (v as string);
    }),
  ]);
};

export const getSessionId = () => {
  return _sessionId;
};

// Force a new session to be started
export const startNewSession = () => {
  logger.debug('[PerformanceTelemetry]: starting new session');
  // Flush the logs when a new session is started
  trySendReport(ReportReasons.LogsFlushed);
  setupSessionId();
};

// Kill switch for performance telemetry. Once invoked, only initPerformanceTelemetry can restart it.
export const shutdownPerformanceTelemetry = () => {
  logger.debug('[PerformanceTelemetry]: shutting down');

  isTelemetryEnabled = false;
  if (resourcePO) {
    resourcePO.disconnect();
  }
};

export const initPerformanceTelemetry = (portalId: string) => {
  isTelemetryEnabled = true;

  // Update the portal id in case the extension is reinitialized
  if (_portalId !== portalId) {
    logger.debug('[PerformanceTelemetry]: portalId changed', portalId);
    // Flush the logs when the portal id changes
    startNewSession();
    _portalId = portalId;
  }

  if (performanceTelemetryInitialized) {
    logger.debug('[PerformanceTelemetry]: already initialized');
    return;
  }

  try {
    logger.debug('[PerformanceTelemetry]: setupPerformanceTelemetry', portalId);
    // eslint-disable-next-line compat/compat
    resourcePO = new PerformanceObserver((list) =>
      // this cast can be made because the observer is setup to only recieve resource entries in resourcePO.observe()
      (list.getEntries() as CompletePerformanceResourceTiming[])
        .filter(filterResourceTimingEntries)
        .forEach((v) => logTiming('p', performanceResourceTimingKeys, v))
    );
    resourcePO.observe({ type: 'resource', buffered: true });
    performanceTelemetryInitialized = true;
  } catch (e) {
    logger.error('[initPerformanceTelemetry]: error', e);
  }
};

export const getSessionIdMessageHandler = () => {
  sendToContentScripts('sessionId', {
    sessionId: _sessionId,
  });
};
