import { initErrorMetrics } from './daemon/initErrorMetrics';
import { initSpeculationRules } from './daemon/initSpeculationRules';
import { runMetricsDaemon } from './daemon/runMetricsDaemon';
import { Dimensions } from './metric/Metric';
import { MetricsFactory } from './MetricsFactory';

interface MetricsFactoryOptions {
  dimensions?: Dimensions;
  library?: string;
}

function createMetricsFactory(
  namespace: string,
  options: MetricsFactoryOptions = {}
): MetricsFactory {
  if (!namespace) {
    throw new Error(
      '[metrics-js] A namespace is required for a Metrics factory.'
    );
  }
  runMetricsDaemon();
  initErrorMetrics();
  initSpeculationRules();
  const dimensions = options.dimensions || {};
  if (options.library) {
    dimensions.fe_library = options.library;
  }
  return new MetricsFactory(namespace, dimensions);
}

export { createMetricsFactory };
