'use es6';

import { syncGet, syncSet } from 'sales-clients-common/chrome/safeStorage';
import { LogInfo } from 'sales-clients-common/utils/reportError';

let firstRun;

export const FIRST_TIME_EXT_ONBOARDING = 'first_time';

export const initFirstRunAndLoginTracker = () => {
  return syncGet(FIRST_TIME_EXT_ONBOARDING)
    .then((data) => {
      if (data.first_time != null) {
        firstRun = data.first_time;
      } else {
        firstRun = true;
      }
    })
    .catch((err) => {
      LogInfo({
        error: err,
        fingerprint: ['firstRunTracker/initFirstRunAndLoginTracker'],
        tags: { source: 'syncGet' },
        extraData: { params: [FIRST_TIME_EXT_ONBOARDING] },
      });
    });
};

export const isFirstRun = () => firstRun;

export const setNotFirstRun = () => {
  firstRun = false;
  return syncSet({ [FIRST_TIME_EXT_ONBOARDING]: false });
};
