const PORTALID = new RegExp(/\/\d+\//);

export const getBaseUrl = (url: string) => {
  try {
    if (!url) {
      return '';
    } else if (url.includes('?')) {
      return url.slice(0, url.indexOf('?'));
    } else if (url.includes('salesclients/v2/sidebar/contact')) {
      return 'salesclients/v2/sidebar/contact';
    } else if (url.includes('tracking/write/v1/trackers/app/email')) {
      return 'tracking/write/v1/trackers/app/email';
    } else if (PORTALID.test(url)) {
      const portalId = url.match(PORTALID)![0];
      return url.replace(portalId, '/');
    } else {
      return url;
    }
  } catch (err) {
    return url;
  }
};
