import { localGet, localSet } from '../chrome/safeStorage';
import {
  initCache,
  cacheHttpLog,
  cacheErrorEntry,
  cacheDebugLogsEntry,
} from './debugLogManager';
import { LogInfo } from 'sales-clients-common/utils/reportError';

type $tsMigrateAny = any;

const EMPTY_CACHED_OBJECT = { errors: [], httpLogs: [] };
const DEBUG_LOG_CACHE_HEADER = 'hs_debugLogCache';

export const initCacheWithChromeStorage = () => {
  initCache({}, true);
};

export const getDebugCacheFromChromeStorage = () =>
  localGet(DEBUG_LOG_CACHE_HEADER).then((values) => {
    //If DEBUG_LOG_CACHE_HEADER doesn't exist, return empty object.
    if (!Object.keys(values).length) {
      return EMPTY_CACHED_OBJECT;
    }
    return values[DEBUG_LOG_CACHE_HEADER];
  });

export const setDebugCacheInChromeStorage = (object: $tsMigrateAny) => {
  localSet({
    [DEBUG_LOG_CACHE_HEADER]: object,
  }).catch((err) => {
    LogInfo({
      error: err,
      fingerprint: ['debugLogManagerChromeExt/setDebugCacheInChromeStorage'],
      tags: { source: 'localSet' },
      extraData: {
        params: [
          {
            [DEBUG_LOG_CACHE_HEADER]: object,
          },
        ],
      },
    });
  });
};

export const cacheHttpLogToChromeStorage = (httpLog: $tsMigrateAny) => {
  cacheHttpLog(httpLog, true);
};

export const cacheErrorEntryToChromeStorage = (error: $tsMigrateAny) => {
  cacheErrorEntry(error, true);
};

export const cacheDebugEntryToChromeStorage = (error: $tsMigrateAny) => {
  cacheDebugLogsEntry(error, true);
};

/** @returns cached chrome extension error entries from chrome storage */
export const getCachedErrorsFromChromeStorage = () => {
  return getDebugCacheFromChromeStorage().then((object) => {
    return (object as $tsMigrateAny).errors;
  });
};

/** @returns cached chrome extension http logs entries from chrome storage */
export const getCachedHttpLogsFromChromeStorage = () => {
  return getDebugCacheFromChromeStorage().then((object) => {
    return (object as $tsMigrateAny).httpLogs;
  });
};

/** @returns cached chrome extension debug logs entries from chrome storage */
export const getCachedDebugLogsFromChromeStorage = () => {
  return getDebugCacheFromChromeStorage().then((object) => {
    return (object as $tsMigrateAny).debugLogs;
  });
};
