/* hs-eslint ignored failing-rules */
/* eslint-disable no-bitwise */

type $tsMigrateAny = any;

/**
 Based on Java implementation below:
 {@link  https://git.hubteam.com/HubSpot/JavaUtils/blob/master/src/main/java/com/hubspot/java/codec/Base54.java Base54}
 **/

const ALPHABET = '0123456789bcdfghjklmnpqrstvwxyzBCDFGHJKLMNPQRSTVWXYZ-_';
const BASE_54_POWERS = [1, 54, 2916, 157464, 8503056, 459165024];
const REVERSE_ALPHABET = Array.from(ALPHABET).reduce(
  (prev, curr, index) => ({
    /* eslint-disable-next-line hubspot-dev/no-reduce-accumulator-copy */
    ...prev,
    [curr]: index,
  }),
  {}
);

const reverse = (input: $tsMigrateAny) => {
  let str = '';
  for (let i = input.length - 1; i >= 0; i--) {
    str += input.charAt(i);
  }
  return str;
};

/**
 * Converts Integer into  corresponding Base54 value
 *  based on the ALPHABET charset defined below
 */
const encodeIntToBase54String = (() => {
  let chars: $tsMigrateAny;
  const reset = () => {
    chars = ALPHABET.split('').map((ch) => {
      return ch.charCodeAt(0);
    });
  };
  reset();
  const base54 = (num: $tsMigrateAny) => {
    let ret = '';
    const base = 54;
    do {
      ret += String.fromCharCode(chars[num % base]);
      num = Math.floor(num / base);
    } while (num > 0);
    return reverse(ret);
  };
  return base54;
})();

/**
 * Converts String to ArrayBuffer or byte array
 * @param {String} data - string to be transformed into ArrayBuffer
 * @returns {Promise<ArrayBuffer>}
 */
const getArrayBufferFromString = (data: $tsMigrateAny) =>
  new Promise((resolve, reject) => {
    const dataAsBlob = new Blob([data]);
    const fr = new FileReader();

    fr.onload = () => {
      resolve(fr.result);
    };
    fr.onerror = () => {
      fr.abort();
      reject(new Error('Error Generating Array buffer'));
    };

    fr.readAsArrayBuffer(dataAsBlob);
  });

/**
 * Converts String to ArrayBuffer or byte array
 * @param {String} str - string to be transformed into ArrayBuffer
 * @returns {ArrayBuffer}
 */
const getArrayBufferFromStringSync = (str: $tsMigrateAny) => {
  const utf8 = [];
  for (let i = 0; i < str.length; i++) {
    let charcode = str.charCodeAt(i);
    if (charcode < 0x80) utf8.push(charcode);
    else if (charcode < 0x800) {
      utf8.push(0xc0 | (charcode >> 6), 0x80 | (charcode & 0x3f));
    } else if (charcode < 0xd800 || charcode >= 0xe000) {
      utf8.push(
        0xe0 | (charcode >> 12),
        0x80 | ((charcode >> 6) & 0x3f),
        0x80 | (charcode & 0x3f)
      );
    }
    // surrogate pair
    else {
      i++;
      // UTF-16 encodes 0x10000-0x10FFFF by
      // subtracting 0x10000 and splitting the
      // 20 bits of 0x0-0xFFFFF into two halves
      charcode =
        0x10000 + (((charcode & 0x3ff) << 10) | (str.charCodeAt(i) & 0x3ff));
      utf8.push(
        0xf0 | (charcode >> 18),
        0x80 | ((charcode >> 12) & 0x3f),
        0x80 | ((charcode >> 6) & 0x3f),
        0x80 | (charcode & 0x3f)
      );
    }
  }
  const uInt8Array = new Uint8Array(utf8);
  const buffer = uInt8Array.buffer;
  return buffer;
};

/**
 * Helps compute size info of encoding
 * @param {Number} lowerSize
 * @param {Number} upperSize
 */
const packSizes = (lowerSize: $tsMigrateAny, upperSize: $tsMigrateAny) => {
  return lowerSize + 7 * upperSize;
};

/**
 * @param {number} number
 */
const unpackSizes = (number: $tsMigrateAny) => ({
  lowerSize: number % 7,
  upperSize: Math.floor(number / 7),
});

/**
 *
 * @param {DataView} buffer -- buffer holds the data to be encoded
 * @param {Number} offset -- offset, represents current position in buffer
 * @param {Number} padding -- padding, tracks left over bytes that are less than 4
 * @param {*} int -- int represents 32bit integer pulled from buffer or empty string
 */
const readOneInt = (
  buffer: $tsMigrateAny,
  offset: $tsMigrateAny,
  padding: $tsMigrateAny,
  int: $tsMigrateAny
) => {
  const result = {
    int: '',
    padding: 0,
    offset: 0,
  };
  const remaining = buffer.byteLength - offset;
  if (remaining < 4) {
    padding = 4 - remaining;
    const tempBuffer = new ArrayBuffer(4);
    const tempView = new DataView(tempBuffer);
    tempView.setUint32(0, 0);
    for (let i = 3; i >= padding; --i) {
      tempView.setUint8(i, buffer.getUint8(offset++));
    }
    int = tempView.getUint32(0, false);
  } else {
    padding = 0;
    int = buffer.getUint32(offset, false);
    offset += 4;
  }
  result.int = int;
  result.padding = padding;
  result.offset = offset;
  return result;
};

/**
 *
 * @param {ArrayBuffer} data -- buffer representation of data
 */
const encode = (data: $tsMigrateAny) => {
  const view = new DataView(data);
  let padding = 0;
  let offset = 0;
  let finalResult = '';
  while (offset < view.byteLength) {
    const result = readOneInt(view, offset, padding, '');
    const lowInt = encodeIntToBase54String(result.int);
    padding = result.padding;
    offset = result.offset;
    let highInt = '';
    if (padding === 0) {
      const res = readOneInt(view, offset, padding, '');
      highInt = encodeIntToBase54String(res.int);
      offset = res.offset;
      padding = res.padding;
    }
    const sizeData = packSizes(lowInt.length, highInt.length);
    const encodeSizeData = encodeIntToBase54String(sizeData);
    finalResult += `${encodeSizeData}${lowInt}${highInt}`;
  }
  const paddingStr = encodeIntToBase54String(padding);
  const encoded = `${finalResult}${paddingStr}`;
  return encoded;
};

/**
 * @param {String} dataStr -- string to be encoded
 */
export const encodeToBase54 = (dataStr: $tsMigrateAny) =>
  getArrayBufferFromString(dataStr).then((data) => {
    return encode(data);
  });

/**
 * @param {string} stringValue Base54 substring representing an integer
 */
const decodeInt = (stringValue: $tsMigrateAny) => {
  const length = stringValue.length;
  if (length - 1 >= BASE_54_POWERS.length) {
    throw new Error('Input too large to decode to integer without overflow');
  }

  let result = 0;

  for (let i = 0; i < length; ++i) {
    const power = length - i - 1;
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    result += REVERSE_ALPHABET[stringValue.charAt(i)] * BASE_54_POWERS[power];
  }

  return result;
};

/**
 * Decodes B54 encoded input into an array of integers
 * @param {string} input B54 encoded data string
 * @returns {number[]}
 */
const decodeIntegers = (input: $tsMigrateAny) => {
  const integers: $tsMigrateAny = [];
  let curPos = 0;

  const consumeNextInt = (size: $tsMigrateAny) => {
    const decoded = decodeInt(input.substr(curPos, size));
    integers.push(decoded);
    curPos += size;
  };

  while (curPos < input.length - 1) {
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    const rawSizeInfo = REVERSE_ALPHABET[input.charAt(curPos++)];
    const { lowerSize, upperSize } = unpackSizes(rawSizeInfo);

    consumeNextInt(lowerSize);
    if (upperSize > 0) {
      consumeNextInt(upperSize);
    }
  }

  return integers;
};

const intToBytes = (val: $tsMigrateAny) => {
  const bufferDataView = new DataView(new ArrayBuffer(4));
  bufferDataView.setUint32(0, val);
  return bufferDataView;
};

/**
 * At the end of writing data to the destination buffer, we need to fit an integer
 * into a space less than the standard 32bits. This function takes care of that by
 * allowing an int to be forced into a certain size.
 * @param {DataView} dest
 * @param {number} destOffset Where in the destination data view to start writing
 * @param {number} srcInt Integer to write into dest
 * @param {number} partialIntSize Size of the integer to write
 */
const setPartialInt = (
  dest: $tsMigrateAny,
  destOffset: $tsMigrateAny,
  srcInt: $tsMigrateAny,
  partialIntSize: $tsMigrateAny
) => {
  const bytes = intToBytes(srcInt);
  for (
    let i = 0, j = bytes.byteLength - 1;
    i < partialIntSize && j >= 0;
    ++i, --j
  ) {
    dest.setUint8(destOffset + i, bytes.getUint8(j));
  }
};

const Utf8ArrayToStr = (data: $tsMigrateAny) => {
  const extraByteMap = [1, 1, 1, 1, 2, 2, 3, 0];
  const count = data.length;
  let str = '';

  for (let index = 0; index < count; ) {
    let ch = data[index++];
    if (ch & 0x80) {
      let extra = extraByteMap[(ch >> 3) & 0x07];
      if (!(ch & 0x40) || !extra || index + extra > count) return null;

      ch = ch & (0x3f >> extra);
      for (; extra > 0; extra -= 1) {
        const chx = data[index++];
        if ((chx & 0xc0) !== 0x80) return null;

        ch = (ch << 6) | (chx & 0x3f);
      }
    }

    str += String.fromCharCode(ch);
  }

  return str;
};

/**
 * @param {string} encodedData B54 encoded data string
 * @returns {string} decoded string
 */
export const decodeBase54 = (encodedData: $tsMigrateAny) => {
  const HYPHEN = '-';
  const OLD_HYPHEN_CHAR = '.';

  // Legacy replace - from Java code
  encodedData = encodedData.replace(OLD_HYPHEN_CHAR, HYPHEN);

  const resultIntegers = decodeIntegers(encodedData);
  const finalPadding =
    // @ts-expect-error ts-migrate(7053) FIXME: Element implicitly has an 'any' type because expre... Remove this comment to see the full error message
    REVERSE_ALPHABET[encodedData.charAt(encodedData.length - 1)];

  const outBuffer = new ArrayBuffer(
    (resultIntegers.length << 2) - finalPadding
  );
  const bufferView = new DataView(outBuffer);
  let bufferWritePos = 0;

  let idx = 0;
  for (const val of resultIntegers) {
    if (idx++ === resultIntegers.length - 1 && finalPadding > 0) {
      const dataLeftToWrite = 4 - finalPadding;
      setPartialInt(bufferView, bufferWritePos, val, dataLeftToWrite);
      bufferWritePos += dataLeftToWrite;
    } else {
      bufferView.setUint32(bufferWritePos, val);
      bufferWritePos += 4;
    }
  }

  return Utf8ArrayToStr(new Uint8Array(outBuffer));
};

/**
 * @param {String} dataStr -- string to be encoded
 */
export const encodeToBase54Sync = (dataStr: $tsMigrateAny) => {
  const bufferFromString = getArrayBufferFromStringSync(dataStr);
  return encode(bufferFromString);
};
