import { getHubSpot } from '../getGlobal';
import { MetricsFactory } from '../MetricsFactory';

let evtTarget: EventTarget = window;
let factory: MetricsFactory | undefined;

function setSpeculationSetupComplete(newValue: boolean) {
  getHubSpot().__speculationSetupComplete = newValue;
}

function getSpeculationSetupComplete(): boolean {
  return getHubSpot().__speculationSetupComplete;
}

export function initSpeculationRules(customTarget?: EventTarget) {
  // for testing - actually dispatching errors to the window during tests
  // causes the tests to fail
  evtTarget = customTarget || evtTarget;

  if (
    factory ||
    getSpeculationSetupComplete() ||
    // @ts-expect-error prerendering is experimental, only in Chromium
    evtTarget?.document?.prerendering === undefined
  ) {
    return;
  }

  // Only setup listeners once, otherwise we get multiple errors per asynchronously loaded bundle (Navigation, Zorse, etc).
  setSpeculationSetupComplete(true);

  // Need to construct a factory directly to avoid a circular dependency in
  // createMetricsFactory. Do not copy this into app/library code.
  factory = new MetricsFactory('js', { name: 'speculationrules' });

  try {
    if (
      // @ts-expect-error prerendering is experimental, only in Chromium
      evtTarget?.document?.prerendering ||
      // @ts-expect-error activationStart is part of Speculation Rules API, only in Chromium
      evtTarget?.performance?.getEntriesByType?.('navigation')[0]
        ?.activationStart
    ) {
      // @ts-expect-error activationStart is part of Speculation Rules API, only in Chromium
      evtTarget?.document?.addEventListener(
        'prerenderingchange',
        trackPrerenderingTime,
        {
          once: true,
        }
      );

      factory.counter('prerendering').increment();
    }
  } catch (__err) {
    // ignore
  }
}

export function getMetricsFactoryForTesting() {
  return factory;
}

export function resetSpeculationRulesForTesting() {
  try {
    factory = undefined;
    if (getHubSpot()) delete getHubSpot().__speculationSetupComplete;
  } catch (__err) {
    // ignore, this is an unrecoverable failure
  }
}

function trackPrerenderingTime() {
  if (!factory) return;

  const activationStart: number | undefined =
    // @ts-expect-error activationStart is part of Speculation Rules API, only in Chromium
    evtTarget?.performance?.getEntriesByType?.('navigation')[0]
      ?.activationStart;

  if (activationStart && activationStart > 0) {
    factory.histogram('activationStart').update(activationStart);
    factory
      .counter('prerendered-activation-start', { activationStart: 'nonzero' })
      .increment();
  } else {
    factory
      .counter('prerendered-activation-start', { activationStart: 'zero' })
      .increment();
  }
}
