import { header } from 'hub-http/middlewares/core';
import { localGet } from '../chrome/safeStorage';

type $tsMigrateAny = any;

const HEADER_NAME = 'X-HubSpot-CSRF-hubspotapi';

export default (options: $tsMigrateAny) =>
  localGet(HEADER_NAME).then((values) => {
    const csrfToken = values[HEADER_NAME];
    if (!csrfToken) {
      return options;
    } else {
      return header(HEADER_NAME, csrfToken)(options);
    }
  });
