import * as core from 'hub-http/middlewares/core';

type $tsMigrateAny = any;

// The clients require special handling for logout above and beyond the standard HubSpot redirect.

export const onUnauthorized =
  (onUnauthed: $tsMigrateAny) => (options: $tsMigrateAny) =>
    core.onResponse((response) => {
      if (!options.ignoreRedirect && response.status === 401) {
        onUnauthed();
      }
      return response;
    })(options);
