'use es6';

import logger from 'SignalsExtension/utils/logger';
import * as user from 'SignalsExtension/background/models/user';
import {
  EVENT_TRACKER_SECONDARY_ID,
  EVENT_TRACKER_TARGET,
  GOOGLE_CONTENT_PROXY,
  V2_LINK_TRACKER,
} from 'SignalsExtension/constants/matchers';
import chrome from '../globals/chrome';
import { decodeBase54 } from 'sales-clients-common/utils/Base54';
import { addParamsToUrl } from 'sales-clients-common/utils/url';
import getDeclarativeRules from 'SignalsExtension/constants/declarativeRules';
import { LogInfo } from 'sales-clients-common/utils/reportError';

export class RequestBlocker {
  static BLOCKING_RESPONSES = {
    allow: { cancel: false },
    block: { cancel: true },
  };

  static TRACKER_TYPES = {
    OPEN: 'open',
    CLICK: 'click',
  };

  static urls = [
    '*://*.googleusercontent.com/proxy/*',
    '*://*/e1t/o/*',
    '*://*/e1t/c/*',
    '*://*/e2t/o/*',
    '*://*/e2t/c/*',
    '*://*/s1t/c/*',
    '*://*/s1t/o/*',
    '*://*/s2t/c/*',
    '*://*/s2t/o/*',
    '*://*/*to/*',
    '*://*/*tc/*',
  ];

  static LINK_TRACKER_TYPES = {
    URI_ENCODED: 't',
    BASE_54_ENCODED: 'te',
  };

  /* Google sometimes doesn't include a redirectUrl in their proxied links, so we don't have a way
   * to recognize that the proxy URL is pointing towards a tracker link. So, we store a list of the
   * proxied URLs we have blocked (that did contain redirect URLs), and we block those in the future.
   * More details here: https://git.hubteam.com/HubSpot/SignalsExtension/pull/1519
   */
  blockedGoogleUrlCache = [];
  static CACHE_MAX_SIZE = 1000;

  handleOwnTrackerRequest = (type, target, encodingType) => {
    switch (type) {
      case RequestBlocker.TRACKER_TYPES.OPEN:
        return RequestBlocker.BLOCKING_RESPONSES.block;
      case RequestBlocker.TRACKER_TYPES.CLICK:
        if (
          encodingType === RequestBlocker.LINK_TRACKER_TYPES.BASE_54_ENCODED
        ) {
          target = decodeBase54(target);
        } else {
          target = decodeURIComponent(target);
        }
        return { redirectUrl: target };
      default:
        return RequestBlocker.BLOCKING_RESPONSES.block;
    }
  };

  // Inserts element at end of cache array, or moves them to the end if they already exist
  // (In order to keep track of least recently used items)
  insertIntoCache = (googleProxyUrl) => {
    const existingUrlIndex = this.blockedGoogleUrlCache.findIndex(
      (u) => u === googleProxyUrl
    );
    if (existingUrlIndex < 0) {
      this.blockedGoogleUrlCache.push(googleProxyUrl);
    } else {
      // splice implicitly removes existingUrl so we can re-add it at the end
      const existingUrl = this.blockedGoogleUrlCache
        .splice(existingUrlIndex, 1)
        .pop();
      this.blockedGoogleUrlCache.push(existingUrl);
    }
  };

  handleTrackerRequest = (
    type,
    url,
    userId,
    secondaryId,
    target,
    encodingType
  ) => {
    const isProxied = url.match(GOOGLE_CONTENT_PROXY);
    const isOwn = +secondaryId === userId;
    const [googleProxyUrl, redirectUrl] = url.split('#');
    if (isOwn) {
      if (isProxied) {
        this.insertIntoCache(googleProxyUrl);
      }
      return this.handleOwnTrackerRequest(type, target, encodingType);
    } else if (isProxied) {
      return { redirectUrl: decodeURIComponent(redirectUrl) };
    } else {
      return RequestBlocker.BLOCKING_RESPONSES.allow;
    }
  };

  clearCacheIfNecessary = () => {
    if (this.blockedGoogleUrlCache.length > RequestBlocker.CACHE_MAX_SIZE) {
      // Removes first half of cache (least recently used items)
      this.blockedGoogleUrlCache = this.blockedGoogleUrlCache.slice(
        RequestBlocker.CACHE_MAX_SIZE / 2
      );
    }
  };

  isLinkTracker = (url) => {
    const eventTracker = url.match(EVENT_TRACKER_SECONDARY_ID);
    const linkTracker = url.match(EVENT_TRACKER_TARGET);
    const v2LinkTracker = url.match(V2_LINK_TRACKER);
    return eventTracker || linkTracker || v2LinkTracker;
  };

  blockRequest = ({ url }) => {
    this.clearCacheIfNecessary();
    const userId = user.getSidekickId();

    const eventTracker = url.match(EVENT_TRACKER_SECONDARY_ID);
    const linkTracker = url.match(EVENT_TRACKER_TARGET);
    const [googleProxyUrl] = url.split('#');

    if (this.isLinkTracker(url)) {
      const isProxied = url.match(GOOGLE_CONTENT_PROXY);
      let trackerUrl = url;

      if (isProxied) {
        trackerUrl = url.split('#')[1];
      }

      const updated = addParamsToUrl(trackerUrl, {
        checkOwner: user.getUserId(),
      });

      return {
        redirectUrl: updated,
      };
    }

    if (eventTracker && !linkTracker) {
      const [, secondaryId] = eventTracker;
      return this.handleTrackerRequest(
        RequestBlocker.TRACKER_TYPES.OPEN,
        url,
        userId,
        secondaryId
      );
    } else if (eventTracker && linkTracker) {
      const [, secondaryId] = eventTracker;
      const [, type, target] = linkTracker;

      return this.handleTrackerRequest(
        RequestBlocker.TRACKER_TYPES.CLICK,
        url,
        userId,
        secondaryId,
        target,
        type
      );
    } else if (this.blockedGoogleUrlCache.includes(googleProxyUrl)) {
      return RequestBlocker.BLOCKING_RESPONSES.block;
    } else {
      return RequestBlocker.BLOCKING_RESPONSES.allow;
    }
  };

  adjustDynamicRules = () => {
    if (chrome.declarativeNetRequest) {
      const userID = user.getUserId();
      const updatedRules = getDeclarativeRules(userID);

      chrome.declarativeNetRequest
        .getDynamicRules()
        .then((existingRules) => {
          const existingRuleIds = existingRules.map((r) => r.id);

          chrome.declarativeNetRequest
            .updateDynamicRules({
              removeRuleIds: existingRuleIds,
              addRules: updatedRules,
            })
            .catch((err) => {
              LogInfo({
                error: err,
                fingerprint: ['requestBlocker/updateDynamicRules'],
                tags: {
                  source: 'chrome.declarativeNetRequest.updateDynamicRules',
                },
              });
            });
        })
        .catch((err) => {
          LogInfo({
            error: err,
            fingerprint: ['requestBlocker/adjustDynamicRules'],
            tags: { source: 'chrome.declarativeNetRequest.getDynamicRules' },
          });
        });
    } else {
      logger.debug('declarativeNetRequest API not available');
    }
  };

  startBlocking = () => {
    logger.log('Start blocking self-opens');

    try {
      this.adjustDynamicRules();
    } catch (err) {
      logger.log(`Unable to work with declarativeNetRequest: ${err}`);
    }

    return null;
  };
}
