/* hs-eslint ignored failing-rules */
/* eslint-disable hubspot-dev/no-async-await */

'use es6';

import {
  hasCooldownElapsed,
  setCooldown,
} from 'sales-clients-common/utils/Cooldown';
import { LogError } from 'sales-clients-common/utils/reportError';

import { restartExtension } from '../../utils/tabs';

const RELOAD_KEY = 'sales-extension_last-reload';
const RELOAD_COOLDOWN = 60 * 60 * 6 * 1000; // 6hrs in ms

const reload = () => {
  setCooldown(RELOAD_KEY);
  restartExtension();
};

export const reloadIfNeeded = async () => {
  try {
    const elapsed = await hasCooldownElapsed(RELOAD_KEY, RELOAD_COOLDOWN);
    if (elapsed) {
      reload();
    }
  } catch (err) {
    LogError({ error: err, fingerprint: ['Runtime.js reloadIfNeeded error'] });
  }
};
