'use es6';

import chrome from 'SignalsExtension/globals/chrome';
import { LogMessage } from 'sales-clients-common/utils/reportError';
import { cacheHttpLogToChromeStorage } from 'sales-clients-common/utils/debugLogManagerChromeExt';
import HubApiHttpClientRemoteNoRetry from 'SignalsExtension/api/HubApiHttpClientRemoteNoRetry';
import HubApiWithoutCsrfHttpClientRemoteNoRetry from 'SignalsExtension/api/HubApiWithoutCsrfHttpClientRemoteNoRetry';
import logger from 'SignalsExtension/utils/logger';
import {
  syncSet,
  syncGet,
  syncRemove,
} from 'sales-clients-common/chrome/safeStorage';
import { LogInfo } from 'sales-clients-common/utils/reportError';

const BASE_TIME = 300000; // 5 minute minimum
const MAX_TIME = 60000 * 60 * 11; // 11 hours
const RETRY_KEY = 'API_Retry_';

const processRetry = ({
  error,
  attempt,
  sigextApiRetry,
  startTimestamp,
  requestData,
  alarmName,
}) => {
  const incrementTime = BASE_TIME * 2 ** attempt; // WIll retry in 5, 10, 20, 40, 80, 160, 320 and 640 minutes

  if (attempt < sigextApiRetry.count && incrementTime < MAX_TIME) {
    const nextScheduledTime = incrementTime + startTimestamp;
    const nextAlarmName = `API_Retry_${nextScheduledTime}`;
    syncSet({
      [nextAlarmName]: {
        attempt: attempt + 1,
        requestData,
        nextScheduledTime,
        startTimestamp,
      },
    });
    syncRemove(alarmName);

    chrome.alarms.create(nextAlarmName, {
      when: nextScheduledTime,
    });
  } else {
    const message = `Retry process terminated unsuccessfully.`;

    syncRemove(alarmName);

    cacheHttpLogToChromeStorage({
      message,
      relativeUrl: requestData.relativeUrl,
      method: requestData.action,
      attempt,
    });
    LogMessage({
      logger: logger.error,
      message,
      stacktrace: true,
      tags: {
        attempt,
        method: requestData.action,
        relativeUrl: requestData.relativeUrl,
        source: 'apiRetry_terminal_failure',
        status: 'status' in error ? error.status : 'unknown status error',
      },
      extraData: {
        attempt,
        errorMessage: error.message || '',
        errorData: error.data || '',
        errorHeaders: error.headers || '',
        method: requestData.action,
        requestData: JSON.stringify(requestData),
        status: 'status' in error ? error.status : 'unknown status error',
        url: requestData.relativeUrl,
      },
    });
  }
};

const apiRetryEvent = (alarmName, __scheduledTime) => {
  syncGet(alarmName)
    .then((result) => {
      if (result[alarmName]) {
        const { attempt, requestData, startTimestamp } = result[alarmName];

        const client = requestData.withoutCsrf
          ? HubApiWithoutCsrfHttpClientRemoteNoRetry
          : HubApiHttpClientRemoteNoRetry;

        requestData.options.query = {
          ...requestData.options.query,
          retryAttempt: attempt,
        };

        client[requestData.action](requestData.relativeUrl, requestData.options)
          .then((response) => {
            const message = `Retry of API call succeeded`;
            cacheHttpLogToChromeStorage({
              message,
              relativeUrl: requestData.relativeUrl,
              method: requestData.action,
              attempt,
            });

            syncRemove(alarmName);

            LogMessage({
              logger: logger.debug,
              message,
              tags: {
                attempt,
                method: requestData.action,
                relativeUrl: requestData.relativeUrl,
                source: 'apiRetry_success',
              },
              extraData: {
                attempt,
                method: requestData.action,
                requestData: JSON.stringify(requestData),
                response: response ? JSON.stringify(response) : null,
                url: requestData.relativeUrl,
              },
            });
          })
          .catch((error) => {
            const { sigextApiRetry } = requestData.options;

            processRetry({
              error,
              attempt,
              sigextApiRetry,
              startTimestamp,
              requestData,
              alarmName,
            });
          });
      }
    })
    .catch((err) => {
      LogInfo({
        error: err,
        fingerprint: ['apiRetry/apiRetryEvent'],
        tags: { source: 'syncGet' },
        extraData: { params: [alarmName] },
      });
    });
};

export const apiRetryHandleAlarm = (alarm) => {
  const { name, scheduledTime } = alarm;
  apiRetryEvent(name, scheduledTime);
};

export const apiRetryQueue = (requestData, error = null) => {
  const startTimestamp = Date.now();
  const nextScheduledTime = Date.now() + BASE_TIME;
  const alarmName = `API_Retry_${nextScheduledTime}`;

  syncSet({
    [alarmName]: {
      attempt: 1,
      nextScheduledTime,
      requestData,
      startTimestamp,
    },
  });

  chrome.alarms.create(alarmName, {
    when: nextScheduledTime,
  });

  LogMessage({
    logger: error ? logger.error : console.log,
    message: error
      ? `API call submitted for retry`
      : `API Queue Item Restarted`,
    stacktrace: true,
    tags: {
      method: requestData.action,
      relativeUrl: requestData.relativeUrl,
      source: 'apiRetryQueue_start',
      status:
        error && 'status' in error ? error.status : 'unknown status error',
    },
    extraData: {
      errorMessage: (error && error.message) || '',
      errorData: (error && error.data) || '',
      errorHeaders: (error && error.headers) || '',
      method: requestData.action,
      requestData: JSON.stringify(requestData),
      status:
        error && 'status' in error ? error.status : 'unknown status error',
      url: requestData.relativeUrl,
    },
  });
};

export const apiRetryAlarm = () => {
  chrome.alarms.onAlarm.addListener(() => apiRetryHandleAlarm);
};

export const reloadApiRetryQueue = () => {
  syncGet(null)
    .then((data) => {
      const dataKeys = Object.keys(data).filter((key) =>
        key.includes(RETRY_KEY)
      );
      if (dataKeys.length) {
        chrome.alarms
          .getAll()
          .then((alarms) => {
            const alarmMap = alarms.reduce((foundAlarms, alarm) => {
              if (alarm.name.includes(RETRY_KEY))
                /* eslint-disable-next-line hubspot-dev/no-reduce-accumulator-copy */
                return { ...foundAlarms, [alarm.name]: alarm };
              return foundAlarms;
            }, {});

            for (const key of dataKeys) {
              if (!alarmMap[key]) {
                const retryRequest = data[key];

                retryRequest.requestData.options.sigextApiRetry.count =
                  retryRequest.requestData.options.sigextApiRetry.count -
                  retryRequest.attempt;

                apiRetryQueue(retryRequest.requestData);

                syncRemove(key);
              }
            }
          })
          .catch((err) => {
            LogInfo({
              error: err,
              fingerprint: ['apiRetry/reloadApiRetryQueue'],
              tags: { source: 'chrome.alarms.getAll' },
            });
          });
      }
    })
    .catch((err) => {
      LogInfo({
        error: err,
        fingerprint: ['apiRetry/reloadApiRetryQueue'],
        tags: { source: 'syncGet' },
        extraData: { params: [null] },
      });
    });
};
