import enviro from 'enviro';
import { cacheErrorEntryToChromeStorage } from 'sales-clients-common/utils/debugLogManagerChromeExt';
// @ts-expect-error untyped
import context from 'SignalsExtension/config/context';
import { emptyFunction } from 'SignalsExtension/utils/emptyfunction';

class Logger {
  log: (...args: any[]) => void;
  debug: (...args: any[]) => void;
  info: (...args: any[]) => void;
  warn: (...args: any[]) => void;
  error: (...args: any[]) => void;

  static LEVELS = {
    log: 0,
    debug: 1,
    info: 2,
    warn: 3,
    error: 4,
  };
  static LEVELS_MAP: { [level: number]: string } = {
    0: 'LOG',
    1: 'DEBUG',
    2: 'INFO',
    3: 'WARN',
    4: 'ERROR',
  };
  // eslint-disable-next-line hubspot-dev/no-confusing-browser-globals
  static CONSOLE_FN = self.console || {};

  constructor() {
    this.log = this._getLogFn('log', Logger.LEVELS.log);
    this.debug = this._getLogFn('debug', Logger.LEVELS.debug);
    this.info = this._getLogFn('info', Logger.LEVELS.info);
    this.warn = this._getLogFn('warn', Logger.LEVELS.warn);
    // Only cache errors for now.
    this.error = this._getLogFn('error', Logger.LEVELS.error, true);
  }

  _getLogFn(method: string, level: number, cache = false) {
    if (!this._shouldLog(method, level)) {
      return emptyFunction;
    }

    const logFn =
      // @ts-expect-error string cannot be used to index into Console
      Logger.CONSOLE_FN[method] || Logger.CONSOLE_FN.log || emptyFunction;

    return function (...args: any[]) {
      const updatedArgs = new Array(args.length + 1);

      updatedArgs[0] = `%c[HubSpot Sales] ${Logger.LEVELS_MAP[level].padEnd(
        6
      )} - %c${new Date().toISOString().slice(11, -1)}%c:`;

      for (let i = 0; i < args.length; i++) {
        const arg = args[i];
        // @ts-expect-error this has any type
        updatedArgs[i + 1] = this._formatError(arg);
      }

      // Cache first arg from the error for now. Revise as needed.
      if (cache && updatedArgs.length > 1) {
        cacheErrorEntryToChromeStorage({ message: updatedArgs[1] });
      }

      // @ts-expect-error this has any type
      return this._callLogFn(logFn, [
        updatedArgs.join(' '),
        'color:grey',
        `color:grey`,
        'color:orangered',
      ]);
    }.bind(this);
  }

  _callLogFn(logFn: Function, args: any[]) {
    return logFn.apply(Logger.CONSOLE_FN, args);
  }

  _shouldLog(method: string, level: number) {
    if (context === 'background') {
      return true;
    }

    return this._aboveLevelThreshold(level);
  }

  _aboveLevelThreshold(level: number) {
    if (enviro.deployed('console')) {
      return level >= Logger.LEVELS.error;
    } else {
      return level >= Logger.LEVELS.log;
    }
  }

  _formatError(arg: Error) {
    if (!(arg instanceof Error)) {
      return arg;
    }

    if (arg.stack) {
      if (arg.message && arg.stack.indexOf(arg.message) === -1) {
        return `Error: ${arg.message}\n${arg.stack}`;
      } else {
        return arg.stack;
      }
      // @ts-expect-error sourceURL is not on Error type
    } else if (arg.sourceURL) {
      // @ts-expect-error sourceURL and line are not on Error type
      return `${arg.message}\n${arg.sourceURL}:${arg.line}`;
    } else {
      return arg;
    }
  }
}

export default new Logger();
