'use es6';
import chrome from 'SignalsExtension/globals/chrome';
import {
  LogMessage,
  LogInfo,
  LogError,
} from 'sales-clients-common/utils/reportError';
import { forAllTabs } from 'SignalsExtension/utils/tabs';
import { addMessageListener } from 'sales-clients-common/chrome/safeRuntime';

const URL_PROTOCOL_PATTERN = /^(https?|ftp):\/\//i;

const EVERYWHERE_URL_PATTERN = /^https:\/\/.*\.force.com\/.*/i;

const EVERYWHERE_DEMO_URL_PATTERN =
  /^https:\/\/.*\.hubspot(qa)*.com\/everywhere-onboarding/i;

const openCommunicatorWindow = ({
  height,
  width,
  offsetWidth = 0,
  onCreateCommunicatorWindow,
  url,
}) => {
  chrome.windows
    .getCurrent()
    .then((current) => {
      const top = Math.round(current.top + current.height / 2 - height / 2);
      const left = Math.round(
        current.left + current.width / 2 - width / 2 - offsetWidth
      );
      return chrome.windows
        .create({
          width,
          height,
          top,
          left,
          url,
          focused: true,
          type: 'popup',
        })
        .then((windowInfo) => {
          if (onCreateCommunicatorWindow) {
            onCreateCommunicatorWindow(windowInfo);
          }
        });
    })
    .catch((err) => {
      LogInfo({
        error: err,
        fingerprint: ['hubspotEverywhereMethodHandler/openCommunicatorWindow'],
        tags: { source: 'chrome.windows.getCurrent' },
      });
    });
};

const startEverywhereDemoHelper = () => {
  let maxAttempts = 10;
  // Signals the sidePanel content to start demo flow.
  // Since sidePanel can take some time to load, we retry a few times.
  // The receiving end will only handle the first message after it is ready.
  const intervalID = setInterval(() => {
    chrome.runtime
      .sendMessage({ method: 'HS_START_EVERYWHERE_DEMO' })
      .catch(() => {
        // Expected error when sidePanel is not ready or already received this.
      });
    if (--maxAttempts <= 0) {
      clearInterval(intervalID);
    }
  }, 500);
};

export const hubspotEverywhereMethodHandler = (requestData, sendResponse) => {
  try {
    // Filter out url protocol so we only execute under HubSpot domain
    if (URL_PROTOCOL_PATTERN.test(requestData.relativeUrl)) {
      sendResponse({
        success: false,
        data: { message: 'Invalid param: relativeUrl' },
      });
    } else {
      const { type } = requestData;
      switch (type) {
        case 'START_EVERYWHERE_DEMO':
          // Relay message to sidePanel
          startEverywhereDemoHelper();
          break;
        case 'CONTINUE_EVERYWHERE_DEMO':
          // Relay message to everywhere demo tab
          forAllTabs(null, (tab) => {
            if (EVERYWHERE_DEMO_URL_PATTERN.test(tab.url)) {
              chrome.tabs.sendMessage(tab.id, {
                method: 'HS_CONTINUE_EVERYWHERE_DEMO',
              });
            }
          });
          break;
        default:
          break;
      }
      return null;
    }
  } catch (ex) {
    LogError({
      error: ex,
      fingerprint: ['hubspotEverywhereMethodHandler'],
      tags: { source: 'hubspotEverywhereMethodHandler' },
      extraData: { requestData },
      stacktrace: true,
    });
    sendResponse({
      success: false,
      data: { message: `Error processing request: ${ex}` },
    });
  }
  return null;
};

addMessageListener((message) => {
  if (message.method === 'openCommunicator') {
    const { width, height, offsetWidth, url } = message.data;
    const onCreateCommunicatorWindow = (windowInfo) => {
      chrome.windows.onRemoved.addListener((wIndex) => {
        if (wIndex === windowInfo.id) {
          forAllTabs(null, (tab) => {
            if (EVERYWHERE_URL_PATTERN.test(tab.url)) {
              chrome.tabs
                .sendMessage(tab.id, { method: 'HS_RELOAD_CRM' })
                .catch((error) => {
                  LogMessage({
                    error,
                    fingerprint: ['hubspotEverywhereMethodHandler'],
                    tags: { source: 'hubspotEverywhereMethodHandler' },
                    stacktrace: true,
                  });
                });
            }
          });
        }
      });
    };

    openCommunicatorWindow({
      height,
      offsetWidth,
      onCreateCommunicatorWindow,
      url,
      width,
    });
  }
});
