export interface Enviro {
  createEnviro(location: typeof window.location): Enviro;
  debug(service: string, defaultVal?: boolean): boolean | string;
  denormalize(env: string): string;
  deployed(service?: string): boolean;
  enabled(service: string, defaultVal?: boolean): boolean;
  get(service: string): string;
  getHublet(): string;
  getInternal(service?: string): string;
  getShort(service?: string): string;
  isProd(service?: string): boolean;
  isQa(service?: string): boolean;
  normalize(env: string): string;
  set(key: string, env: string): string | boolean | undefined;
  setDebug(service: string, val?: boolean): void;

  /**
   * Executes callback based on matching against current or provided hublet.
   *
   * @template T
   * @template P
   * @param {Object} hubletCallbacks
   * @param {function(string): T} hubletCallbacks.isNa1 - Callback for NA1 hublet.
   * @param {function(string): P} hubletCallbacks.isNa1 - Callback for non-NA1 hublets.
   * @param hublet - (Optional) The hublet to check against. If not provided, the current hublet is used.
   * @returns {T | P} - The result of the matching callback function.
   */
  ifHublet<T, P>(hubletCallbacks: HubletCallback<T, P>, hublet?: string): T | P;

  /**
   * Executes callback based on matching against current or provided hublet.
   *
   * @template T
   * @param {Object} hubletCallbacks
   * @param {function(string): T} hubletCallbacks.isNa1 - Callback for NA1 hublet.
   * @param hublet - (Optional) The hublet to check against. If not provided, the current hublet is used.
   * @returns {T | undefined} The result of the matching callback function, or `undefined` if no match is found.
   */
  ifHublet<T>(
    hubletCallbacks:
      | { isNa1?: (hublet: string) => T }
      | { isNonNa1?: (hublet: string) => T },
    hublet?: string
  ): T | undefined;
}

interface Window {
  [key: string]: any;
}

interface EnvMap {
  [key: string]: string;
}

type HubletCallback<T = unknown, P = unknown> = {
  isNa1: (hublet: string) => T;
  isNonNa1: (hublet: string) => P;
};

const SUBDOMAINS = [
  'api',
  'local',
  'app',
  'private',
  'platform',
  'tools',
  'meetings',
  'payments',
  'mcp',
];
const COM_DOMAINS = [
  'hubspot',
  'hubteam',
  'grader',
  'getsignals',
  'getsidekick',
  'gettally',
  'hubspotemail',
  'customer-hub',
  'hubspotservicehub',
  'hubspotquote',
  'hubspotdocuments',
  'hs-data-privacy',

  // connect.com and wthubspot.com intentionally not included here due to mismatching qa tld
];
const NET_DOMAINS = ['hubspotstarter', 'hubspotfree', 'hubspotemail'];
const ORG_DOMAINS = ['growth'];
const DOMAINS = {
  com: COM_DOMAINS.join('|'),
  net: NET_DOMAINS.join('|'),
  org: ORG_DOMAINS.join('|'),
};

const createEnviro = function (location: typeof window.location): Enviro {
  const deployedRe = new RegExp(
    `^(?!local|test|selenium)(.*\\.)?(${Object.entries(DOMAINS)
      .map(([tld, regexList]) => `(?:${regexList})(qa)?\\.${tld}`)
      .join(
        '|'
      )}|(?:connect)\\.com|(?:connect)(qa)\\.co|wthubspot\\.(com|de|es|fr|jp))$`
  );
  const isDeployed = deployedRe.test(location.hostname);
  const qaRe = new RegExp(
    `${Object.entries(DOMAINS)
      .map(([tld, regexList]) => `(?:${regexList})qa\\.${tld}`)
      .join(
        '|'
      )}|(?:connect)qa\\.co|wthubspot\\.(com|de|es|fr|jp)|hsqa-sales(?:crm)?-sub\\.com|(?:hubspotstarter|hubspotfree|hubspotemail)(qa)(?:-.*)\\.net|(?:hubspotemail)(qa)(?:-.*)\\.com`
  );
  const hubletRe = new RegExp(
    `^(?:${SUBDOMAINS.join('|')})-(.*).(?:hubspot|hubteam)(?:qa)?.com`
  );
  const hubspotQuoteHubletRe = new RegExp(`^(.*).(?:hubspotquote)(?:qa)?.com`);
  const hubspotDocumentsHubletRe = new RegExp(
    `^app-(.*).(?:hubspotdocuments)(?:qa)?.com`
  );
  const hubspotSalesSubRe = new RegExp(
    '^(?:[0-9]+).(.*).hs(?:qa)?-sales(?:crm)?-sub.com'
  );
  const hubspotFreeStarterHubletRe = new RegExp(
    '^hs-(?:[0-9]+).s.(?:hubspotfree|hubspotstarter|hubspotemail)(?:qa)?-(.*).net'
  );
  const hubspotEmailHubletRe = new RegExp(
    '^hs-(?:[0-9]+).s.(?:hubspotemail)(?:qa)?-(.*).com'
  );

  const sidekickOpenHubletRe = new RegExp(
    '^t.sidekickopen(?:\\d)+-([a-z]+[0-9]).com'
  );
  const hsDataPrivacyHubletRe = new RegExp(
    '^([a-z]+[0-9]).hs-data-privacy(?:qa)?.com'
  );

  const HUBLET_REGEXPS = [
    hubletRe,
    hubspotQuoteHubletRe,
    hubspotDocumentsHubletRe,
    hubspotSalesSubRe,
    hubspotFreeStarterHubletRe,
    hubspotEmailHubletRe,
    sidekickOpenHubletRe,
    hsDataPrivacyHubletRe,
  ];

  const defaultKey = 'ENV';
  const DEFAULT_NOT_SUPPORTED_ERROR_MSG =
    'Enviro error: the default argument for .get and .getShort is no longer supported';

  const getEnv = (key: string): string => {
    let result = (window as Window)[key];

    if (result == null) {
      try {
        result = (window as Window).sessionStorage.getItem(key);
      } catch (e) {}
    }

    if (result == null) {
      try {
        result = (window as Window).localStorage.getItem(key);
      } catch (e) {}
    }

    return result;
  };

  const getDefaultEnv = (): string => {
    const env = getEnv(defaultKey);
    if (env) {
      return env;
    } else if (qaRe.test(location.host)) {
      return 'qa';
    } else {
      return 'prod';
    }
  };

  const setEnv = (key: string, env: string | boolean | undefined) => {
    (window as Window)[key] = env;
    return env;
  };

  const MAP = {
    prod: 'production',
    qa: 'development',
  };

  const normalize = (env: string): string => {
    if (typeof env === 'string') {
      const lower = env.toLowerCase();
      return (MAP as EnvMap)[lower] || lower;
    }

    return env;
  };

  const denormalize = (env: any): string => {
    env = typeof env === 'string' ? env.toLowerCase() : undefined;

    return (
      Object.keys(MAP).find((ours: string) => env === (MAP as EnvMap)[ours]) ||
      env
    );
  };

  const get = (service?: string, defaultVal?: boolean): string => {
    if (defaultVal != null) {
      throw new Error(DEFAULT_NOT_SUPPORTED_ERROR_MSG);
    }
    let env = null;
    if (service) {
      const parts = service.split('.').reverse();
      for (let i = 0; i < parts.length; i++) {
        const pathPart = parts[i];
        env = getEnv(`${pathPart.toUpperCase()}_ENV`);
        if (env) {
          break;
        }
      }
    }
    if (env == null) {
      const defaultEnv = getDefaultEnv();
      env = defaultEnv != null ? defaultEnv : 'qa';
    }
    return normalize(env);
  };

  const set = function (
    key: string,
    env: string
  ): string | boolean | undefined {
    if (arguments.length === 1) {
      env = key;
      key = defaultKey;
    }
    return setEnv(key, env);
  };

  const getInternal = (service?: string, defaultVal?: boolean): string => {
    if (defaultVal != null) {
      throw new Error(DEFAULT_NOT_SUPPORTED_ERROR_MSG);
    }
    return denormalize(get(service));
  };

  const getShort = getInternal;

  const isProd = (service?: string): boolean => getShort(service) === 'prod';

  const isQa = (service?: string): boolean => getShort(service) === 'qa';

  const deployed = (service?: string): boolean => {
    let result;
    if (typeof service === 'string') {
      result = getEnv(`${service.toUpperCase()}_DEPLOYED`);
    }

    if (result == null) {
      result = getEnv('DEPLOYED');
    }

    return result == null ? isDeployed : !!result;
  };

  const debug = (service: string, defaultVal = false): boolean | string => {
    let result;
    if (typeof service === 'string') {
      result = getEnv(`${service.toUpperCase()}_DEBUG`);
    }

    if (result == null) {
      result = getEnv('DEBUG');
    }

    return result == null ? defaultVal : result;
  };

  const setDebug = (service: string, val = true): void => {
    if (typeof service === 'string') {
      try {
        if (val) {
          localStorage.setItem(
            `${service.toUpperCase()}_DEBUG`,
            JSON.stringify(true)
          );
        } else {
          localStorage.removeItem(`${service.toUpperCase()}_DEBUG`);
        }
      } catch (e) {
        setEnv(`${service.toUpperCase()}_DEBUG`, val || undefined);
      }
    } else {
      val = service != null ? service : true;
      try {
        if (val) {
          localStorage.setItem('DEBUG', JSON.stringify(val));
        } else {
          localStorage.removeItem('DEBUG');
        }
      } catch (e) {
        setEnv('DEBUG', val || undefined);
      }
    }
  };

  const enabled = (service: string, defaultVal = false): boolean => {
    let result = getEnv(`${service.toUpperCase()}_ENABLED`);
    if (result == null) {
      result = JSON.stringify(defaultVal);
    }
    return `${result}`.toLowerCase() === 'true';
  };

  const getHublet = (): string => {
    const hubletOverride = getEnv('HUBLET');

    if (hubletOverride) {
      return hubletOverride;
    }

    for (const regexp of HUBLET_REGEXPS) {
      if (regexp.test(location.hostname)) {
        return regexp.exec(location.hostname)![1];
      }
    }

    return 'na1';
  };

  function ifHublet<T, P>(
    { isNa1, isNonNa1 }: Partial<HubletCallback<T, P>>,
    hublet?: string
  ): T | P | undefined {
    if (!hublet) {
      hublet = getHublet();
    }

    if (hublet === 'na1') {
      return typeof isNa1 === 'function' ? isNa1(hublet) : undefined;
    } else if (typeof isNonNa1 === 'function') {
      return isNonNa1(hublet);
    }
  }

  return {
    createEnviro,
    debug,
    denormalize,
    deployed,
    enabled,
    get,
    getHublet,
    getInternal,
    getShort,
    isProd,
    isQa,
    ifHublet,
    normalize,
    set,
    setDebug,
  };
};

export default createEnviro(document.location);
