'use es6';

import toJS from 'transmute/toJS';
import { fetchUserInfo } from 'sales-ext-common/apis/LoginVerifyApi';

// UserInfo transformation got v3 gates
// Copied and modified from https://git.hubteam.com/HubSpot/hub-http/blob/2f1533d05e3acf7244d0512c07cf0e32ee4d94ce/hub-http/static/js/userInfo-internal.ts
export function transformGates(v2Gates, v3Gates) {
  return [
    ...(v2Gates || []),
    ...(v3Gates || []).map(
      (gate) =>
        // eslint-disable-next-line hubspot-dev/no-bare-gate-strings
        `g8:${gate}`
    ),
  ];
}

let memoizedInfo;

// Match the shape of the response from `hub-http/userInfo`
// See https://git.hubteam.com/HubSpot/hub-http/blob/ae08d511b31d69e7344fb48a8cb7a2d14a5500e0/hub-http/static/js/userInfo.js#L115
export function userInfoSafe() {
  return fetchUserInfo().then((response) => {
    const { auth, portal, user } = toJS(response);

    const info = {
      user,
      gates: transformGates(portal.enabled_gates, portal.enabled_v3_gates),
      portal,
    };

    if (auth) {
      info.auth = auth;
    }

    memoizedInfo = info;

    return info;
  });
}

export const userInfoSync = () => {
  if (!memoizedInfo) {
    throw new Error('User info has not been loaded yet');
  }

  return memoizedInfo;
};

export default userInfoSafe;
