/* hs-eslint ignored failing-rules */
/* eslint-disable hubspot-dev/no-async-await, hubspot-dev/no-confusing-browser-globals */

'use es6';

import { updateIcon } from 'SignalsExtension/background/browserAction';
import { initializeFireAlarms } from 'SignalsExtension/background/models/firealarm';
import logger from 'SignalsExtension/utils/logger';
import { LogError, LogInfo } from 'sales-clients-common/utils/reportError';
import * as user from 'SignalsExtension/background/models/user';
import Raven from 'raven-js';
import {
  isFirstRun,
  setNotFirstRun,
} from 'SignalsExtension/data/firstRunTracker';
import { setSessionMonitor } from 'SignalsExtension/data/sessionMonitorContainer';
import { setPortalId } from 'SignalsExtension/data/PortalIdContainer';
import {
  fetchAttributes,
  clearAttributes,
  getAttribute,
  setAttribute,
} from 'SignalsExtension/background/models/attributes';
import { initAuthedTracker } from '../../utils/usageTracker';
import { USER_REGISTRATION_KEY } from 'sales-clients-common/constants/ChromeAttributeKeys';
import getExtensionVersion from 'sales-clients-common/utils/getExtensionVersion';
import { RequestBlocker } from '../requestBlocker';
import { reloadGmailTabs, reloadSFDCTabs } from '../../utils/tabs';
import { SessionMonitor } from '../SessionMonitor';
import { startOnboardingFlow } from '../../utils/onboarding';
import { SHORTLINK_OPEN } from '../../constants/StorageKeys';
import localStorage from 'SignalsExtension/globals/localStorage';
import {
  initFeatureToggleManager,
  isFeatureEnabledForSigExt,
} from 'SignalsExtension/background/managers/featureToggleManager';
import { Feature_SelfOpenBlocking } from 'sales-ext-common/managers/featureToggles';
import { setUpEverywhereListener } from '../../init/everywhereListener';
import { clearGates } from 'SignalsExtension/data/salesGates';
import {
  applyEverywhereToAllTabs,
  removeEverywhereFromAllTabs,
} from 'SignalsExtension/init/everywhereListener';

export const ONE_HOUR = 60 * 60 * 1000;

let hasInitializedUserRefresh = false;

const requestBlocker = new RequestBlocker();

const initAnalyticsTracker = (portalId) => {
  try {
    if (portalId) {
      initAuthedTracker(user.getEmail(), portalId);
    }
  } catch (error) {
    Raven.captureException((error && error.message) || error);
  }
};

const sendRegisterEventIfNecessary = async () => {
  const registrationAttr = getAttribute(USER_REGISTRATION_KEY);
  if (!registrationAttr) {
    try {
      await setAttribute(USER_REGISTRATION_KEY, getExtensionVersion());
    } catch (error) {
      LogInfo({
        error,
        fingerprint: ['accountManager::sendRegisterEventIfNecessary'],
      });
    }
  }
};

const onLoginInit = async (portalId, reloadGmail) => {
  try {
    Raven.setUserContext({
      email: user.getEmail(),
      id: user.getUserId(),
      sidekickId: user.getSidekickId(),
    });
    initAnalyticsTracker(portalId);
    if (isFeatureEnabledForSigExt(Feature_SelfOpenBlocking)) {
      requestBlocker.startBlocking();
    }
    if (!self.window.sessionStorage.popupInit && reloadGmail) {
      reloadGmailTabs(); // async, but we don't need to block
    }
    updateIcon();
    await applyEverywhereToAllTabs();
    await sendRegisterEventIfNecessary();
  } catch (err) {
    LogError({
      error: err,
      fingerprint: ['accountManager:onLoginInit'],
      tags: { source: 'accountManager:onLoginInit' },
      extraData: { portalId },
      stacktrace: true,
    });
  }
};

const onLogin = async (loggedinUser, reloadGmail) => {
  let userInfo;
  try {
    userInfo = loggedinUser || (await user.fetchUser()); // Side effect: sets user model in models/user
    const portalId = userInfo ? userInfo.get('portal_id') : null;
    setPortalId(portalId);
    if (portalId) {
      logger.log('User is Logged In');
      updateIcon();
      await fetchAttributes(); // Side effect: sets attributes model in models/attributes
      await onLoginInit(portalId, reloadGmail);
      setUpEverywhereListener();
    }
  } catch (err) {
    LogError({
      error: err,
      fingerprint: ['accountManager:onLogin'],
      tags: { source: 'accountManager:onLogin' },
      extraData: { userInfo },
      stacktrace: true,
    });
  }
};

const handleFirstRun = () => {
  if (!isFirstRun()) {
    return;
  }
  setNotFirstRun();

  startOnboardingFlow();
};

const onReady = async (loggedIn, loggedinUser, reloadGmail) => {
  logger.log(`Extension Version: ${getExtensionVersion()}`);
  updateIcon();
  initializeFireAlarms();

  // Reset value to false
  try {
    localStorage.setItem(SHORTLINK_OPEN, 'false');
  } catch (error) {
    logger.error(`Error setting ${SHORTLINK_OPEN} in localStorage`, error);
  }

  if (!loggedIn && reloadGmail) {
    logger.debug('User not logged in on startup');
    reloadGmailTabs();
    reloadSFDCTabs();
  } else {
    await onLogin(loggedinUser, reloadGmail);
  }

  handleFirstRun();
};

const onLogout = () => {
  updateIcon();
  user.clearUser();
  clearGates();
  clearAttributes();
  reloadGmailTabs(); // async, but we don't need to block
  reloadSFDCTabs();
  removeEverywhereFromAllTabs();
};

const initUserInfoRefresh = () => {
  if (!hasInitializedUserRefresh) {
    setInterval(async () => {
      try {
        await user.fetchUser();
      } catch (error) {
        logger.debug('fetchUser failed', error);
      }
    }, ONE_HOUR);
    hasInitializedUserRefresh = true;
  }
};

const init = async (reloadGmail, hasCsrfCookie) => {
  // Initialize feature toggles
  await initFeatureToggleManager();

  // Init usage tracker without email or user data. Will reinit with user onLogin
  initAnalyticsTracker();

  // Init user info refresh to update gates
  initUserInfoRefresh();

  const sessionMonitor = new SessionMonitor(onReady, onLogin, onLogout);
  setSessionMonitor(sessionMonitor); // Must be set before init- used in onReady
  await sessionMonitor.init(reloadGmail, hasCsrfCookie);
  return sessionMonitor;
};

export default {
  init,
};
