import { isLocalService, shouldUseLocalBackend } from './localRequestUtils';
import {
  ChirpAvroRpcDetails,
  ChirpAvroRpcInputs,
  PostOnlyHubHttpClient,
  ChirpAvroRpcResult,
  ChirpAvroRpcError,
  HUBSPOT_CORRELATION_ID_KEY,
} from './types';
import { createCombinedAbortSignal } from './abortSignalUtils';
import {
  FetchType,
  HeadersInitType,
  AbortSignalType,
} from './apiClients/global';
export class ChirpInternalError extends Error {}
export class ChirpError extends Error {}

export interface GetAvroGatewayOptions {
  auth: string;
  timeout?: number;
  isLocal?: boolean;
  na1Only?: boolean;
  isHublessAppAuth?: boolean;
  isStreaming?: boolean;
}

export function getAvroGateway({
  auth,
  timeout,
  isLocal = false,
  na1Only = false,
  isHublessAppAuth = false,
  isStreaming = false,
}: GetAvroGatewayOptions) {
  const extraAppAuthPath = isHublessAppAuth && auth === 'app' ? '/hubless' : '';

  if (isLocal) {
    return getLocalAvroGateway(auth) + extraAppAuthPath;
  }

  if (na1Only) {
    return `/chirp-frontend-${auth}-na1-proxy/v1/gateway${extraAppAuthPath}`;
  }

  if (auth === 'app' && isStreaming) {
    return `/chirp-frontend-app-stream/v1/gateway${extraAppAuthPath}`;
  }

  if (auth === 'internal' && isStreaming) {
    return `/chirp-frontend-internal-stream/v1/gateway${extraAppAuthPath}`;
  }

  // Handle external streaming endpoint
  if (auth === 'external' && isStreaming) {
    return '/chirp-frontend-external-stream/v1/gateway';
  }

  if (auth === 'app' && timeout && timeout > 5000) {
    return `/chirp-frontend-app-slow/v1/gateway${extraAppAuthPath}`;
  }

  return `/chirp-frontend-${auth}/v1/gateway${extraAppAuthPath}`;
}

export function getLocalAvroGateway(auth: string) {
  const domain =
    auth === 'app' || auth === 'external' ? 'hubspotqa' : 'hubteamqa';
  return `https://local.${domain}.com/chirp-frontend-local/v1/gateway`;
}

export type DataResult<TResult> = {
  type: 'data';
  data: TResult;
};
export type RpcErrorResult<TError> = {
  type: 'rpcError';
  rpcError: TError;
  correlationId?: string;
};
export type InternalErrorResult = {
  type: 'internalError';
  internalError: Record<string, unknown>;
  correlationId?: string;
};

/**
 * @deprecated This was used only from code generation which has been removed. This will be removed when usage moves to 0
 */
export type DfcObject<T, U, V> = {
  fieldName: string;
  args: T;
  fetcher(request: U): Promise<V>;
};

/**
 * @deprecated This was used only from code generation which has been removed. This will be removed when usage moves to 0
 */
export type DfcObjectNoArgs<T, __U, V> = {
  fieldName: string;
  args: T;
  fetcher(): Promise<V>;
};

/**
 * @deprecated This was used only from code generation which has been removed. This will be removed when usage moves to 0
 */
export type ClientCall = <Details extends ChirpAvroRpcDetails<any, any, any>>(
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>
) => Promise<ChirpAvroRpcResult<Details>>;

export const defaultTag: unique symbol = Symbol('defaultTag');
/**
 *
 * Represents an object property marked as `@Default` in Java.
 * These properties are always present on responses, but are optional on requests (empty values will fall back to the default specified on the model).
 * To be able to assign a value to this field, wrap the type in Requestify
 */
export type Default<T> = T & { [defaultTag]?: 'default' };

export type AlgebraResult<T, U> =
  | { type: 'ok'; value: T }
  | { type: 'err'; value: U };

export type Requestify<T> = T extends Array<infer U>
  ? Array<Requestify<U>>
  : {
      [Property in keyof T as T[Property] extends Default<unknown>
        ? Property
        : never]?: Requestify<T[Property] extends Default<infer U> ? U : never>;
    } & {
      [Property in keyof T as T[Property] extends Default<unknown>
        ? never
        : Property]: Requestify<T[Property]>;
    };

export type ResponseUnion<
  Details extends ChirpAvroRpcDetails<any, any, any, any>,
> =
  | DataResult<ChirpAvroRpcResult<Details>>
  | RpcErrorResult<ChirpAvroRpcError<Details>>
  | InternalErrorResult;

export type ResultUnion<
  Details extends ChirpAvroRpcDetails<any, any, any, any>,
> =
  | DataResult<ChirpAvroRpcResult<Details>>
  | RpcErrorResult<ChirpAvroRpcError<Details>>;

export function spreadPropertiesIntoError(
  error: Error,
  properties: Record<string, unknown>
) {
  const {
    message: __message,
    stack: __stack,
    cause: __cause,
    ...spreadableProperties
  } = properties;
  return Object.assign(error, spreadableProperties);
}

export function parseRejectingChirpErrors<
  Details extends ChirpAvroRpcDetails<any, any, any>,
>(response: ResponseUnion<Details>): ChirpAvroRpcResult<Details> {
  if (response.type === 'data') {
    if ('correlationId' in response) {
      // We don't want to surface this for successful responses yet
      delete response.correlationId;
    }
    return response.data;
  } else if (response.type === 'rpcError') {
    if ('correlationId' in response && response.rpcError) {
      response.rpcError[HUBSPOT_CORRELATION_ID_KEY] = response.correlationId;
      delete response.correlationId;
    }
    const error = new ChirpError('CHIRP RPC failed with a known error', {
      cause: response.rpcError,
    });
    throw spreadPropertiesIntoError(error, response.rpcError);
  } else {
    if ('correlationId' in response && response.internalError) {
      response.internalError[HUBSPOT_CORRELATION_ID_KEY] =
        response.correlationId;

      delete response.correlationId;
    }
    const error = new ChirpInternalError(
      'CHIRP RPC failed with a CHIRP internal error',
      { cause: response.internalError }
    );
    throw spreadPropertiesIntoError(error, response.internalError);
  }
}

export function parseIntoUnion<
  Details extends ChirpAvroRpcDetails<any, any, any, any>,
>(
  response: ResponseUnion<Details>
):
  | DataResult<ChirpAvroRpcResult<Details>>
  | RpcErrorResult<ChirpAvroRpcError<Details>> {
  if (response.type === 'internalError') {
    if ('correlationId' in response && response.internalError) {
      response.internalError[HUBSPOT_CORRELATION_ID_KEY] =
        response.correlationId;
      delete response.correlationId;
    }
    const error = new ChirpInternalError(
      'CHIRP RPC failed with a CHIRP internal error',
      { cause: response.internalError }
    );
    throw spreadPropertiesIntoError(error, response.internalError);
  }

  if (response.type === 'rpcError') {
    if ('correlationId' in response && response.rpcError) {
      response.rpcError[HUBSPOT_CORRELATION_ID_KEY] = response.correlationId;
    }
  }

  if ('correlationId' in response) {
    delete response.correlationId;
  }
  return response;
}

export async function makeChirpAvroCall<
  Details extends ChirpAvroRpcDetails<any, any, any, any>,
  TReturn,
>({
  hubHttpClient,
  details,
  responseParser,
  inputs,
  alternateData,
  calledFromNpmShim = false,
}: {
  hubHttpClient: PostOnlyHubHttpClient;
  details: Details;
  responseParser: (response: ResponseUnion<Details>) => TReturn;
  inputs: ChirpAvroRpcInputs<Details>;
  alternateData?: Promise<unknown>;
  calledFromNpmShim?: boolean;
}): Promise<TReturn> {
  const { timeout, portalId, userId, environment, hublet } = inputs ?? {};
  const request = inputs?.request ?? {};
  const {
    auth,
    serviceName,
    rpcName,
    fingerprint,
    na1Only,
    isHublessAppAuth,
    isLocal,
  } = details;
  const syncStackError = new Error(`CHIRP RPC failed for ${rpcName}`);
  const clientTimeout = typeof timeout === 'number' ? timeout : 5000;

  const shouldMakeLocalRequest =
    isLocal || (shouldUseLocalBackend() && (await isLocalService(serviceName)));

  const headers: Record<string, string> = {};

  if (fingerprint !== undefined) {
    headers['x-hs-fingerprint'] = fingerprint;
  }

  if (portalId !== undefined && (auth === 'internal' || calledFromNpmShim)) {
    headers['X-Origin-HubId'] = String(portalId);
  }

  if (userId !== undefined) {
    headers['X-Origin-UserId'] = String(userId);
  }

  const data =
    alternateData ??
    hubHttpClient
      .post(
        `${getAvroGateway({
          auth,
          timeout: clientTimeout,
          isLocal: shouldMakeLocalRequest,
          na1Only,
          isHublessAppAuth,
        })}/${serviceName}/${rpcName}`,
        {
          data: request,
          headers,
          timeout: clientTimeout,
          query: { clienttimeout: String(clientTimeout) },
          environment,
          hublet,
          ...(portalId && auth === 'app' && !calledFromNpmShim
            ? { portalId }
            : {}),
        }
      )
      .catch((errorResponse) => {
        if (errorResponse.status !== 0 && 'responseJSON' in errorResponse) {
          return errorResponse.responseJSON;
        }
        throw errorResponse;
      });

  return data
    .then((response) => responseParser(response as ResponseUnion<Details>))
    .catch((error: Error) => {
      syncStackError.cause = error;
      throw syncStackError;
    });
}

/**
 * Makes a CHIRP Avro call using the Fetch API.
 * For streaming RPCs, this allows consuming the response as a stream of Server-Sent Events.
 *
 * @param fetcher The fetch function to use
 * @param details The details of the CHIRP RPC
 * @param inputs The inputs for the RPC call
 * @param shouldMakeLocalRequest Whether to use local request handling
 * @param abortSignal Optional AbortSignal to cancel the request
 * @returns A Response object that can be processed as a stream for streaming RPCs
 */
export async function makeChirpAvroCallWithFetch<
  Details extends ChirpAvroRpcDetails<any, any, any, any>,
>(
  fetcher: FetchType,
  details: Details,
  inputs: ChirpAvroRpcInputs<Details>,
  shouldMakeLocalRequest: boolean,
  abortSignal?: AbortSignalType
) {
  const { request, timeout, portalId, userId } = inputs ?? { request: {} };
  const {
    auth,
    serviceName,
    rpcName,
    fingerprint,
    isStreaming,
    na1Only,
    isLocal,
  } = details;
  const clientTimeout = isStreaming ? 60000 : timeout ?? 5000;

  const url = getFetchUrlPath({
    isLocal: isLocal || shouldMakeLocalRequest,
    auth,
    serviceName,
    rpcName,
    isStreaming,
    clientTimeout,
    na1Only,
  });

  const headers: HeadersInitType = {
    'Content-Type': 'application/json',
  };

  const isInternalAuth = auth === 'internal';

  if (fingerprint !== undefined) headers['x-hs-fingerprint'] = fingerprint;
  if (isStreaming) headers['Accept'] = 'text/event-stream';
  if (portalId !== undefined && isInternalAuth)
    headers['X-Origin-HubId'] = String(portalId);
  if (userId !== undefined && isInternalAuth)
    headers['X-Origin-UserId'] = String(userId);

  // Create a composite abort signal that combines the timeout and user abort signal
  const signal = createCombinedAbortSignal(clientTimeout, abortSignal);

  const response = await fetcher(url, {
    method: 'POST',
    headers,
    signal,
    body: JSON.stringify(request),
  });

  return response;
}

export function formatChirpAvroMSWSuccessResponse<TResult>(
  mockResponse: TResult
): DataResult<TResult> {
  return {
    type: 'data',
    data: mockResponse,
  };
}

export function formatChirpAvroMSWUserErrorResponse<TError>(
  mockErrorResponse: TError
): RpcErrorResult<TError> {
  return {
    type: 'rpcError',
    rpcError: mockErrorResponse,
  };
}

export function formatChirpAvroMSWInternalErrorResponse(
  mockErrorResponse: Record<string, unknown>
): InternalErrorResult {
  return {
    type: 'internalError',
    internalError: mockErrorResponse,
  };
}

function getFetchUrlPath({
  isLocal,
  auth,
  serviceName,
  rpcName,
  isStreaming,
  clientTimeout,
  na1Only = false,
}: {
  isLocal: boolean;
  auth: string;
  serviceName: string;
  rpcName: string;
  isStreaming: boolean;
  clientTimeout: number;
  na1Only?: boolean;
}): string {
  const gateway = getAvroGateway({
    auth,
    timeout: clientTimeout,
    isLocal,
    na1Only,
    isStreaming,
  });
  let path = `${gateway}/${serviceName}/${rpcName}`;
  if (isStreaming) {
    path += '/stream';
  }
  path += `?clienttimeout=${clientTimeout}`;
  return path;
}
