import { QFWindow, QuickFetch } from './internal/index';

let quickFetch =
  typeof window !== 'undefined'
    ? (window as any as QFWindow).quickFetch
    : undefined;

if (typeof window === 'undefined' || !(window as any as QFWindow).quickFetch) {
  console.warn(
    '[quick-fetch] quickFetchScript was not included or running in Node.js environment. Falling back to stubbed implementation.'
  );
  quickFetch = {
    afterAuth() {},
    clearAllRequests() {},
    getApiUrl() {},
    getPortalId() {},
    getRequestStateByName() {
      return null;
    },
    makeEarlyRequest() {},
    makeLoginVerifyRequest() {},
    removeEarlyRequest() {},
    getCookie() {
      return null;
    },
    hasEarlyRequests() {
      return false;
    },
  } as any;
}

export type { AjaxOptions, RequestState } from './internal/index';
export default quickFetch as QuickFetch;
