'use es6';

import enviro from 'SignalsExtension/shims/enviro';
import * as core from 'hub-http/middlewares/core';
import { XMLHttpRequestWithFetch } from 'SignalsExtension/utils/XMLHttpRequestWithFetch';

export const withFetch = (options) =>
  core.withOptions(options, {
    Request: XMLHttpRequestWithFetch,
  });

const addApiPathPrefix = (url) => {
  let path = url.path;
  if (path.startsWith('/api/') || path.startsWith('api/')) {
    return url;
  } else if (path.startsWith('/')) {
    path = `/api${path}`;
  } else {
    path = `/api/${path}`;
  }
  url.path = path;
  return url;
};

export const dynamicHubletEnvironmentUrl = (
  name,
  domainPrefix,
  hubletOverride
) => {
  let lastCalculatedHublet = null;
  let lastCalculatedApi = null;
  const getDefaultApiMemoized = () => {
    const currentHublet = enviro.getHublet();
    if (currentHublet === lastCalculatedHublet && lastCalculatedApi !== null) {
      return lastCalculatedApi;
    }

    lastCalculatedHublet = currentHublet;
    lastCalculatedApi = core.hubletApi(name, domainPrefix, hubletOverride);
    return lastCalculatedApi;
  };

  return (options) =>
    core.withUrl((url) => {
      if (!url.protocol && options.location) {
        url.protocol = options.location.protocol.slice(0, -1);
      }

      if (!url.hostname) {
        const api = options.api || getDefaultApiMemoized();
        url.hostname = core.resolveApi(api);
      }
      if (name === 'app') {
        url = addApiPathPrefix(url);
      }

      return url;
    })(options);
};

const onMaxAttemptsReached = core.onResponseError((response) =>
  Promise.reject(response)
);

export const customRetryOnError = core.retry(
  (response) => {
    // If 488 (wrong hublet) update the hublet and retry
    if (response.status === 488) {
      const correctHublet = response.responseJSON?.correctHublet;
      if (correctHublet) {
        enviro.setHublet(correctHublet);
        return true;
      }
    }

    // Carry over default retry logic from https://git.hubteam.com/HubSpot/hub-http/blob/master/hub-http/static/js/middlewares/hubapi.js#L177
    if (
      response.options.method === 'GET' &&
      (response.status >= 500 ||
        (response.status === 0 && response.errorCode === 'NETWORKERROR'))
    ) {
      return true;
    }

    return false;
  },
  {
    maxRetries: 1,
    onMaxAttemptsReached,
  }
);
