'use es6';

import enviro from 'SignalsExtension/shims/enviro';
import chrome from 'SignalsExtension/globals/chrome';
import logger from 'SignalsExtension/utils/logger';
import {
  HUBSPOT_API_COOKIE,
  HUBSPOTAPI_COOKIE_NAME,
  HUBSPOTAPI_CSRF_COOKIE_NAME,
} from 'SignalsExtension/constants/cookieDescriptors';
import { waitForChromeCookies } from 'SignalsExtension/data/chrome/Cookies';
import { localSet } from 'sales-clients-common/chrome/safeStorage';

const HEADER_NAME = 'X-HubSpot-CSRF-hubspotapi';
const HUBSPOT_API_EXPIRATION_DATE_KEY = 'hubspotapi-expiration-date';

/**
 * Truncates cookie value to obscure for logging
 * @param {cookieObj||cookieChangeObj} cookieObj
 * @returns string
 */
export const truncateCookieValue = (cookieObj) => {
  let returnStr = 'Could not process cookie';
  try {
    let returnObj = {};
    if (cookieObj && cookieObj.cookie) {
      const { value } = cookieObj.cookie;
      if (value && typeof value === 'string') {
        returnObj.cookie = { ...cookieObj.cookie };
        returnObj.cookie.value = `${value.slice(0, 8)}...`;
        returnObj.cause = cookieObj.cause;
        returnObj.removed = cookieObj.removed;
        returnStr = JSON.stringify(returnObj);
      }
    } else if (
      cookieObj &&
      cookieObj.value &&
      typeof cookieObj.value === 'string'
    ) {
      returnObj = { ...cookieObj };
      returnObj.value = `${returnObj.value.slice(0, 8)}...`;
      returnStr = JSON.stringify(returnObj);
    } else if (!cookieObj) {
      returnStr = 'CookieObj is null';
    }
  } catch (err) {
    console.error(err);
    returnStr = `${returnStr}: ${err}`;
  }
  return returnStr;
};

const setCsrfForContentScript = (cookie) => {
  localSet({ [HEADER_NAME]: cookie.value }).catch((err) => {
    if (err) {
      logger.log(`crsf cookie local set failed: ${err}`);
    }
  });
};

const setHubspotApiExpirationDate = (cookie) => {
  const expirationDateMs =
    cookie && typeof cookie.expirationDate === 'number'
      ? Math.trunc(parseFloat(cookie.expirationDate) * 1000)
      : null;
  localSet({
    [HUBSPOT_API_EXPIRATION_DATE_KEY]: expirationDateMs,
  }).catch((err) => {
    if (err) {
      logger.log(`hubspotapi cookie local set failed: ${err}`);
    }
  });
};

const isTargetCookie = (cookieName, cookie) => {
  if (
    cookie &&
    cookie.domain.includes(HUBSPOT_API_COOKIE[enviro.getShort('api')].domain) &&
    cookie.name === cookieName &&
    cookie.value
  ) {
    return true;
  }
  return false;
};

const hubspotCookiesListener = (changeInfo) => {
  const { cookie } = changeInfo;
  if (isTargetCookie(HUBSPOTAPI_CSRF_COOKIE_NAME, cookie)) {
    setCsrfForContentScript(cookie);
    logger.log(
      `Detected HUBSPOTAPI_CSRF_COOKIE_NAME cookie change \nCause: ${
        changeInfo.cause || 'No cause provided'
      } Removed:  ${changeInfo.removed} Cookie: ${
        (changeInfo.cookie && truncateCookieValue(changeInfo.cookie)) ||
        'No cookie provided'
      }`
    );
    return;
  }
  if (isTargetCookie(HUBSPOTAPI_COOKIE_NAME, cookie)) {
    setHubspotApiExpirationDate(cookie);
    logger.log(
      `Detected HUBSPOTAPI_COOKIE_NAME cookie change \nCause: ${
        changeInfo.cause || 'No cause provided'
      } Removed:  ${changeInfo.removed} Cookie: ${
        (changeInfo.cookie && truncateCookieValue(changeInfo.cookie)) ||
        'No cookie provided'
      }`
    );
  }
};

// We keep a copy of the csrf token in chrome.storage.local synced with the actual cookie.
// SERA uses this value because it does not have access to all cookies in chrome.

export const getAndSetCsrfForContentScript = () =>
  waitForChromeCookies()
    .then(() =>
      chrome.cookies
        .get({
          url: HUBSPOT_API_COOKIE[enviro.getShort('api')].url,
          name: HUBSPOTAPI_CSRF_COOKIE_NAME,
        })
        .then((cookie) => {
          if (cookie && cookie.value) {
            logger.debug(`Detected csrf cookie on startup: `);
            setCsrfForContentScript(cookie);
            return cookie.value;
          }
          logger.warn(`User logged out- No CSRF cookie on found on startup`);
          return null;
        })
        .catch((err) => {
          logger.error(
            `csrfCookieMonitor: Unable to get csrf cookie: ${err.message}`
          );
        })
    )
    .catch((error) => {
      throw new Error(`csrfCookieMonitor failure: ${error}`);
    });

export const getAndSetHubspotApiExpirationDate = () =>
  waitForChromeCookies()
    .then(() =>
      chrome.cookies
        .get({
          url: HUBSPOT_API_COOKIE[enviro.getShort('api')].url,
          name: HUBSPOTAPI_COOKIE_NAME,
        })
        .then((cookie) => {
          if (cookie) {
            logger.debug(`Detected hubspotapi cookie on startup`);
            setHubspotApiExpirationDate(cookie);
            return true;
          }
          logger.warn(`No hubspotapi cookie found on startup`);
          return false;
        })
        .catch((err) => {
          logger.error(
            `csrfCookieMonitor: Unable to get hubspotapi cookie: ${err.message}`
          );
        })
    )
    .catch((error) => {
      throw new Error(`hubspotapi cookie monitor failure: ${error}`);
    });

export const initCsrfCookieMonitor = async () => {
  let csrfCookie = null;
  try {
    csrfCookie = await getAndSetCsrfForContentScript();
  } catch (err) {
    logger.error('No hubspotapi-csrf Cookie found: ', err);
  }

  // set crsf listeners
  if (!chrome.cookies.onChanged.hasListener(hubspotCookiesListener)) {
    chrome.cookies.onChanged.addListener(hubspotCookiesListener);
  }
  // seed and listen for hubspotapi cookie expiration
  try {
    await getAndSetHubspotApiExpirationDate();
  } catch (err) {
    logger.error('No hubspotapi Cookie found: ', err);
  }
  return !!csrfCookie;
};
