'use es6';

import chrome from 'SignalsExtension/globals/chrome';
import { forAllTabs } from 'SignalsExtension/utils/tabs';
import {
  addMessageListener,
  sendMessage,
} from 'sales-clients-common/chrome/safeRuntime';
import logToCache from 'SignalsExtension/utils/logToCache';
import logger from 'SignalsExtension/utils/logger';
import Raven from 'raven-js';

export const handleSendMessageError = (
  sendMessageError,
  caller,
  method,
  data,
  tab
) => {
  if (
    sendMessageError.message &&
    !sendMessageError.message.includes(
      'Could not establish connection. Receiving end does not exist.'
    ) &&
    !sendMessageError.message.includes(
      'The message port closed before a response was received.'
    )
  ) {
    logToCache(
      `[CHROME SEND MESSAGE ERROR]
      from ${caller}:  ${JSON.stringify(sendMessageError)}
      method: ${method}
      data: ${JSON.stringify(data)}
      tab: ${JSON.stringify(tab)}`,
      true
    );
  }
};

export const sendToContentScripts = (method, data) => {
  forAllTabs(null, (tab) => {
    chrome.tabs
      .sendMessage(tab.id, { method, data })
      .catch((sendMessageError) =>
        handleSendMessageError(
          sendMessageError,
          'sendToContentScripts',
          method,
          data,
          tab
        )
      );
    return null;
  });
};

export const listenToContentScriptMethodsBatch = (methodHandlers) => {
  const messageListener = (request, sender, sendResponse) => {
    const handler = methodHandlers[request.method];
    if (handler) {
      try {
        handler(request.data, sendResponse, sender);
      } catch (err) {
        logger.error(err);
        logger.info(request.method);
        logger.debug(request);
        Raven.captureBreadcrumb({
          message: `Handler for ${request.method} failed`,
          data: { err, request },
        });
      }
      return true;
    }
    return false;
  };

  addMessageListener(messageListener);
};

export const sendToPopup = (method, data) => {
  sendMessage({ method, data }).catch((sendMessageError) => {
    logger.error('sendMessage: sendToPopup', sendMessageError);
    if (
      sendMessageError.message &&
      !sendMessageError.message.includes(
        'The message port closed before a response was received'
      ) &&
      !sendMessageError.message.includes(
        'Could not establish connection. Receiving end does not exist.'
      )
    ) {
      logToCache(
        `[CHROME SEND MESSAGE ERROR] from sendToPopup:  ${JSON.stringify(
          sendMessageError
        )}`,
        true
      );
    }
  });
};
