'use es6';

import location from 'SignalsExtension/globals/location';

const EXTENSION_PROTOCOL = 'chrome-extension:';
const { pathname, protocol } = location;

const isBGPage =
  protocol === EXTENSION_PROTOCOL && pathname.indexOf('Background-sw') !== -1;

const isPopup =
  protocol === EXTENSION_PROTOCOL && pathname.indexOf('popup.html') !== -1;

let context = null;

if (isBGPage) {
  context = 'background';
} else if (isPopup) {
  context = 'popup';
} else {
  context = 'content_script';
}

export default context;
