import { LogMessage } from 'sales-clients-common/utils/reportError';
import { openInActiveTab } from './tabs';
import { getFullUrl } from 'hubspot-url-utils';
import { localGet } from 'sales-clients-common/chrome/safeStorage';

const ENV_KEY = 'ENV';
export const FIRST_RUN_LOGGED_OUT = 'FIRST_RUN_LOGGED_OUT';
export const FIRST_RUN = 'FIRST_RUN';

export const getOnboardingURL = () => {
  return localGet([ENV_KEY])
    .then((result) => {
      const envOverride = result[ENV_KEY] === 'qa' ? 'qa' : 'prod';
      const baseUrl = getFullUrl('app', {
        envOverride,
      });

      return `${baseUrl}/everywhere-onboarding`;
    })
    .catch(() => `${getFullUrl('app')}/everywhere-onboarding`);
};

export const startOnboardingFlow = () => {
  getOnboardingURL()
    .then((url: string) => openInActiveTab(url))
    .catch((error: Error) => {
      LogMessage({
        error,
        fingerprint: ['startOnboardingFlow'],
        tags: { source: 'startOnboardingFlow' },
        stacktrace: true,
      });
    });
};
