/* hs-eslint ignored failing-rules */

'use es6';

import delay from 'hs-promise-utils/delay';
import logger from 'SignalsExtension/utils/logger';
import chrome from 'SignalsExtension/globals/chrome';
import { reloadIfNeeded } from './Runtime';

let loaded = false;
const MAX_RETRIES = 5;

const isCookieStoreAvailable = (resolve, reject, retries = 0) => {
  logger.log('Check for chrome cookie store context available');
  return chrome.cookies
    .getAllCookieStores()
    .then((cookieStores) => {
      if (cookieStores.length) {
        loaded = true;
        resolve();
      } else {
        if (++retries < MAX_RETRIES) {
          delay(1000)
            .then(() =>
              isCookieStoreAvailable(resolve, reject, retries).catch(
                (error) => {
                  console.error(error);
                }
              )
            )
            .catch(reject);
        } else {
          logger.error('Chrome cookie store unavailable');
          reject(new Error('Unable to fetch chrome cookies'));
          reloadIfNeeded();
        }
      }
    })
    .catch(reject);
};

export const waitForChromeCookies = () =>
  new Promise((resolve, reject) => {
    if (loaded) {
      logger.log('Chrome cookie store available');
      resolve();
      return;
    }
    isCookieStoreAvailable(resolve, reject);
  });
