'use es6';

import HubAppHttpClient from 'SignalsExtension/api/HubAppHttpClient';
import HubApiWithoutImmutableHttpClient from 'SignalsExtension/api/HubApiWithoutImmutableHttpClient';

export const fetchUserInfo = () => HubAppHttpClient.get('/cookie-echo/v1/user');

export const fetchUserPortalInfo = (portalId) =>
  HubApiWithoutImmutableHttpClient.get(`login-verify/hub-user-info`, {
    query: { portalId },
  });
