/* eslint-disable hubspot-dev/no-async-await */

'use es6';
import { LogMessage } from 'sales-clients-common/utils/reportError';
import {
  fetchAlarms,
  fetchExtensionAlarms,
} from 'SignalsExtension/api/FireAlarmApi';
import localStorage from 'SignalsExtension/globals/localStorage';
import Raven from 'raven-js';
import {
  sendToContentScripts,
  sendToPopup,
} from 'SignalsExtension/background/backgroundMessaging';
import logger from 'SignalsExtension/utils/logger';
import chrome from 'SignalsExtension/globals/chrome';
import { getManifestVersion } from 'sales-clients-common/chrome/safeRuntime';
import { listenToContentScriptMethodsBatch } from 'SignalsExtension/background/backgroundMessaging';
import fireAlarmInterval from 'SignalsExtension/constants/fireAlarmInterval';

const REQUEST_FIRE_ALARMS_METHOD = 'requestFireAlarms';
const REQUEST_NEW_ALARMS_METHOD = 'requestNewAlarms';
const MARK_ALARMS_AS_SEEN_METHOD = 'markAlarmsAsSeen';
const DISMISS_ALARM_METHOD = 'dismissAlarm';

let alarms = [];
let fireAlarmWindowId;
let initialized = false;

const broadcast = (message, payload) => {
  sendToPopup(message, payload);
  sendToContentScripts(message, payload);
};

const fetchFireAlarms = async () => {
  const apiAlarms = await fetchAlarms();
  const dismissed = JSON.parse(localStorage.getItem('dismissedAlarms'));
  alarms = apiAlarms.map((alarm) => {
    const id = alarm.get('id');
    const existingAlarm = alarms.find((a) => a.id === id);
    if (existingAlarm) {
      return existingAlarm;
    } else {
      return {
        id,
        alarm,
        seenByUser: false || dismissed.includes(id),
        dismissed: dismissed.includes(id),
      };
    }
  });

  const newDismissed = dismissed.filter(
    (dId) => !!alarms.find((alarm) => alarm.id === dId)
  );

  localStorage.setItem('dismissedAlarms', JSON.stringify(newDismissed));
  return alarms;
};

// This fetches alarms targeting specific versions of the extension for the purpose of
// expediting saturation in case of a critical bug.
const fetchTargetedExtensionFireAlarms = async () => {
  const apiAlarms = await fetchExtensionAlarms();
  const dismissed = JSON.parse(localStorage.getItem('dismissedAlarms'));
  const crxVersionTarget = `/crx-version/${getManifestVersion()}`;
  alarms = apiAlarms
    .filter((alarm) => {
      const urlRegexPattern = alarm.get('urlRegexPattern');
      return (
        urlRegexPattern && new RegExp(urlRegexPattern).test(crxVersionTarget)
      );
    })
    .map((alarm) => {
      const id = alarm.get('id');
      const existingAlarm = alarms.find((a) => a.id === id);
      if (existingAlarm) {
        return existingAlarm;
      } else {
        return {
          id,
          alarm,
          seenByUser: false || dismissed.includes(id),
          dismissed: dismissed.includes(id),
        };
      }
    });

  const newDismissed = dismissed.filter(
    (dId) => !!alarms.find((alarm) => alarm.id === dId)
  );

  localStorage.setItem('dismissedAlarms', JSON.stringify(newDismissed));
  return alarms;
};

const markAllAsSeen = () =>
  alarms.forEach((alarm) => {
    alarm.seenByUser = true;
  });

const dismissAlarm = (id) => {
  const dismissed = JSON.parse(localStorage.getItem('dismissedAlarms'));
  dismissed.push(id);
  localStorage.setItem('dismissedAlarms', JSON.stringify(dismissed));
  alarms.get(alarms.findIndex((alarm) => alarm.id === id)).dismissed = true;
};

export const getHasNewAlarms = () =>
  !!alarms.find((alarm) => !alarm.seenByUser);

const checkForGmailAlarms = async () => {
  try {
    const nextAlarms = await fetchFireAlarms();
    const payload = { fireAlarms: nextAlarms };
    broadcast('backgroundFireAlarms', payload);
  } catch (err) {
    logger.error(`Fetch gmail alarms failed with error: ${err}`);
  }
};

const FIRE_ALARM_WINDOW_TOP = 100;
const FIRE_ALARM_WINDOW_WIDTH = 360;
const FIRE_ALARM_WINDOW_HEIGHT = 300;

const checkForExtensionAlarms = async () => {
  try {
    const targetedAlarms = await fetchTargetedExtensionFireAlarms();
    if (targetedAlarms.length) {
      chrome.action.setBadgeText({ text: '!' });
      chrome.action.setBadgeBackgroundColor({ color: '#d94c53' });

      if (fireAlarmWindowId) {
        chrome.windows
          .update(fireAlarmWindowId, { focused: true })
          .catch((error) => {
            LogMessage({
              error,
              fingerprint: ['checkForExtensionAlarms'],
              tags: { source: 'checkForExtensionAlarms' },
              stacktrace: true,
            });
          });
      } else {
        const currentWindow = await chrome.windows.getCurrent();
        const windowInstance = await chrome.windows.create({
          url: chrome.runtime.getURL('pages/firealarm.html'),
          type: 'panel',
          focused: true,
          top: FIRE_ALARM_WINDOW_TOP,
          left: Math.round((currentWindow.width + FIRE_ALARM_WINDOW_WIDTH) / 2), // center
          width: FIRE_ALARM_WINDOW_WIDTH,
          height: FIRE_ALARM_WINDOW_HEIGHT,
        });

        fireAlarmWindowId = windowInstance.id;
      }
    }
  } catch (err) {
    logger.error(`Fetch extension alarms failed with error: ${err}`);
  }
};

export const checkForAlarms = async () => {
  await checkForExtensionAlarms();
  await checkForGmailAlarms();
};

const initAlarmListeners = () => {
  if (!localStorage.getItem('dismissedAlarms')) {
    localStorage.setItem('dismissedAlarms', JSON.stringify([]));
  }
  listenToContentScriptMethodsBatch({
    [REQUEST_FIRE_ALARMS_METHOD]: () => {
      Raven.captureBreadcrumb({
        message: REQUEST_FIRE_ALARMS_METHOD,
      });
      checkForAlarms();
    },
    [REQUEST_NEW_ALARMS_METHOD]: () => {
      Raven.captureBreadcrumb({
        message: REQUEST_NEW_ALARMS_METHOD,
      });
      checkForAlarms();
    },
    [MARK_ALARMS_AS_SEEN_METHOD]: () => {
      Raven.captureBreadcrumb({
        message: MARK_ALARMS_AS_SEEN_METHOD,
      });
      markAllAsSeen();
      const payload = { fireAlarms: alarms };
      broadcast('backgroundFireAlarms', payload);
    },
    [DISMISS_ALARM_METHOD]: (request) => {
      Raven.captureBreadcrumb({
        message: DISMISS_ALARM_METHOD,
      });
      dismissAlarm(request.id);
      const payload = { fireAlarms: alarms };
      broadcast('backgroundFireAlarms', payload);
    },
  });
};

export const initializeFireAlarms = async () => {
  if (!initialized) {
    initialized = true;
    initAlarmListeners();
    setInterval(async () => await checkForAlarms(), fireAlarmInterval);
  }
  await checkForAlarms();
};
