import { MessageSender } from 'sales-clients-common/chrome/types';
// @ts-expect-error untyped
import chrome from 'SignalsExtension/globals/chrome';
import { LogWarn } from 'sales-clients-common/utils/reportError';
import { getSidePanel } from 'SignalsExtension/data/sidePanelContainer';
import { Port } from 'sales-clients-common/chrome/types';

function closeSidePanel(
  tabId: number,
  sendResponse: (response: { isOpen: boolean }) => void
) {
  chrome.runtime
    .sendMessage({ method: 'closeSidePanelMethod', tabId })
    .catch((err: Error) => {
      console.error('sendMessage: closeSidePanelMethod', err);
    });
  sendResponse({ isOpen: false });

  return true;
}

function toggleSidePanel(
  tabId: number,
  sendResponse: (response: {
    isOpen: boolean;
    errorMessage?: string;
  }) => void = () => {},
  openOnly = false,
  sidePanelParameters?: object
) {
  // We can check for the context on the fly and remove all the event handler logic when
  //   we are on a chrome standard version of v129 (or probably v128). Using a callback or
  //   promise here makes the api think that opening the side panel did not come from a user
  //   gesture, which causes an error and won't open the side panel at all.
  const sidePanel = getSidePanel(`${tabId}`);
  if (sidePanel) {
    if (!openOnly) {
      closeSidePanel(tabId, sendResponse);
    }
  } else {
    let path = `pages/sidePanel.html?tabId=${tabId}`;
    Object.entries(sidePanelParameters || {}).forEach(([key, value]) => {
      if (value) {
        path += `&${encodeURIComponent(key)}=${encodeURIComponent(value)}`;
      }
    });
    chrome.sidePanel.setOptions({
      tabId,
      path,
    });
    chrome.sidePanel
      .open({ tabId })
      .then(() => sendResponse({ isOpen: true }))
      .catch((err: Error) => {
        console.error('Error opening side panel: ', err);
        LogWarn({
          error: err,
          fingerprint: ['controlSidePanelMethodHandler:toggleSidePanel'],
          tags: { source: 'controlSidePanelMethodHandler:toggleSidePanel' },
          extraData: {
            additionalMessage: 'Error opening side panel on user click',
          },
          stacktrace: true,
        });
        sendResponse({ isOpen: false, errorMessage: err.message });
      });
  }

  return true;
}

function closeCopilot(tabId: number) {
  const sidePanelForTab = getSidePanel(`${tabId}`);
  if (sidePanelForTab) {
    chrome.sidePanel.setOptions({
      tabId,
      path: `pages/sidePanel.html?tabId=${tabId}`,
    });
  }
}

function toggleCopilot(
  tabId: number,
  sendResponse: (response: { isOpen: boolean }) => void = () => {}
) {
  // We can check for the context on the fly and remove all the event handler logic when
  //   we are on a chrome standard version of v129 (or probably v128). Using a callback or
  //   promise here makes the api think that opening the side panel did not come from a user
  //   gesture, which causes an error and won't open the side panel at all.
  const sidePanelForTab = getSidePanel(`${tabId}`);
  let sidePanelOpenPromise = Promise.resolve();
  if (!sidePanelForTab) {
    chrome.sidePanel.setOptions({
      tabId,
      path: `pages/sidePanel.html?tabId=${tabId}&copilotOpen=true`,
    });
    sidePanelOpenPromise = chrome.sidePanel.open({ tabId });
  }

  sidePanelOpenPromise
    .then(() => {
      return chrome.runtime
        .sendMessage({
          method: 'sidePanelToggleCopilot',
          tabId,
        })
        .then(({ isOpen: isCopilotOpen }: { isOpen: boolean }) => {
          sendResponse({ isOpen: isCopilotOpen });
        })
        .catch((err: Error) => {
          console.error('sendMessage: sidePanelToggleCopilot', err);
        });
    })
    .catch((err: Error) => {
      LogWarn({
        error: err,
        fingerprint: ['controlSidePanelMethodHandler:toggleCopilot'],
        tags: {
          source: 'controlSidePanelMethodHandler:toggleCopilot',
        },
        extraData: {
          additionalMessage: 'Unable to toggle Copilot',
        },
        stacktrace: true,
      });
    });

  return true;
}

type ControlSidePanelRequest =
  | {
      request:
        | 'toggleCopilot'
        | 'closeCopilot'
        | 'closeSidePanel'
        | 'openSidePanel';
      requestId?: string;
    }
  | {
      request: 'toggleSidePanel';
      sidePanelParameters?: object;
      requestId?: string;
    };

export const controlSidePanelMethodHandler = (
  data: ControlSidePanelRequest,
  sendResponse: (response: { isOpen: boolean; errorMsg?: string }) => void,
  sender: MessageSender
) => {
  const tabId = (sender.tab && sender.tab.id) || -1;

  switch (data.request) {
    case 'toggleSidePanel':
      return toggleSidePanel(
        tabId,
        sendResponse,
        false,
        data.sidePanelParameters
      );
    case 'openSidePanel':
      return toggleSidePanel(tabId, sendResponse, true);
    case 'closeSidePanel':
      return closeSidePanel(tabId, sendResponse);
    case 'toggleCopilot':
      return toggleCopilot(tabId, sendResponse);
    case 'closeCopilot':
      return closeCopilot(tabId);
    default:
      return false;
  }
};

export const controlSidePanelMethodHandlerPort = (
  data: ControlSidePanelRequest,
  tabId: number,
  connection: Port
) => {
  const portSendResponse = (response: {
    isOpen: boolean;
    errorMessage?: string;
  }) => {
    connection.postMessage({
      method: 'controlSidePanelMethodResponse',
      data: response,
    });
  };
  switch (data.request) {
    case 'toggleSidePanel':
      return toggleSidePanel(
        tabId,
        portSendResponse,
        undefined,
        data.sidePanelParameters
      );
    case 'openSidePanel':
      return toggleSidePanel(tabId, portSendResponse, true);
    case 'closeSidePanel':
      return closeSidePanel(tabId, portSendResponse);
    case 'toggleCopilot':
      return toggleCopilot(tabId, portSendResponse);
    case 'closeCopilot':
      return closeCopilot(tabId);
    default:
      return false;
  }
};
