/* hs-eslint ignored failing-rules */
/* eslint-disable hubspot-dev/no-async-await,  hubspot-dev/no-confusing-browser-globals */
'use es6';

//
// These item must be imported in this order
//
// modifySelf adds window object mocks for enviro self-init
import { initSelf } from '../shims/initSelf';
import '../shims/bender-fill';
// End ordered import

import 'I18n/init/data/allTimezones';
import raven from 'SignalsExtension/init/raven';
import chrome from 'SignalsExtension/globals/chrome';
import logger from 'SignalsExtension/utils/logger';
import { reloadGmailTabs } from 'SignalsExtension/utils/tabs';
import { enableRejectionTracking } from 'hs-promise-rejection-tracking';
import { EXTENSION_VERSION } from 'SignalsExtension/background/managers/updateManager';
import {
  portListener,
  messageHandler,
} from 'SignalsExtension/background/contentScriptListeners';
import { sendToContentScripts } from 'SignalsExtension/background/backgroundMessaging';
import { setupWebsocketListener } from './websocketListener';
import { initExtension } from './initServiceWorker';
import { apiRetryHandleAlarm } from 'SignalsExtension/api/apiRetry';
import getExtensionVersion from 'sales-clients-common/utils/getExtensionVersion';
import { LogError } from 'sales-clients-common/utils/reportError';
import {
  GMAIL_VISIBILITY_CHANGE,
  RESTART_OFFSCREEN_DOCUMENT,
} from 'sales-ext-common/constants/backgroundMessagingMethods';
import { setUpEverywhereListener } from './everywhereListener';
import { Extension_SW_Keep_Alive } from 'sales-ext-common/managers/featureToggles';
import {
  initFeatureToggleManager,
  isFeatureEnabledForSigExt,
} from 'SignalsExtension/background/managers/featureToggleManager';
import { userUngatedFor } from 'SignalsExtension/data/salesGates';
import { gate } from 'hub-http/gates';

logger.log(`Extension Version: ${getExtensionVersion()}`);
logger.log('[BACKGROUND SW]: loaded');

self.window.sessionStorage.serviceWorkerActive = false;

let isKeepAliveRunning = false;
let isExtensionInitialized = false;
let isOffscreenRestarting = false;

const offscreenPath = 'pages/offscreen.html';

const closeOffscreenDocuments = async () => {
  const existingContexts = await chrome.runtime.getContexts({
    contextTypes: ['OFFSCREEN_DOCUMENT'],
  });

  return new Promise((resolve) => {
    if (existingContexts.length > 0) {
      chrome.offscreen.closeDocument(resolve);
    } else {
      resolve();
    }
  });
};

const launchOffscreen = async () => {
  await closeOffscreenDocuments();
  await initExtension();

  const isUngatedForCallingChromeExtension = userUngatedFor(
    gate('Omnichannel:CallingChromeExtension')
  );

  if (isUngatedForCallingChromeExtension) {
    await chrome.offscreen.createDocument({
      url: offscreenPath,
      reasons: ['AUDIO_PLAYBACK', 'USER_MEDIA', 'WEB_RTC'],
      justification: 'Make WebRTC Calls',
    });
  }
};

const keepAlive = () =>
  setInterval(() => {
    try {
      chrome.runtime.getPlatformInfo();
    } catch {
      // Ignore error
    }
  }, 20e3);

try {
  // Service Worker event handlers
  const onStartupHandler = async () => {
    logger.debug(`[SW EVENT] onStartup`);
    await initExtension(true);
  };

  const onInstalledHandler = async (param = {}) => {
    logger.debug(`[SW EVENT] onInstalled `);
    if (param.reason === 'update') {
      if (param.previousVersion !== EXTENSION_VERSION) {
        logger.debug(
          `[SW EVENT] onInstalled: Updated from ${param.previousVersion} to ${EXTENSION_VERSION}`
        );
      } else {
        logger.debug(
          `[SW EVENT] onInstalled: Reload by user ${EXTENSION_VERSION}`
        );
      }
    }
    await initExtension(true);
  };

  const onUpdateAvailableHandler = () => {
    logger.debug(
      `[SW EVENT] onUpdateAvailable - serviceWorkerActive: ${
        self.window.sessionStorage
          ? !!self.window.sessionStorage.serviceWorkerActive
          : false
      }`
    );
    // send message to SERA indicating an update is ready.
    // If user does not update, the extension will update
    // the next time the service worker unloads

    sendToContentScripts('extensionUpdate', true);
  };

  const onConnectHandler = async (port) => {
    logger.debug(
      `[SW EVENT] onConnect - serviceWorkerActive: ${
        self.window.sessionStorage
          ? !!self.window.sessionStorage.serviceWorkerActive
          : false
      }`
    );
    if (!self.window.sessionStorage.serviceWorkerActive) {
      await initExtension();
    }
    portListener(port);
  };

  const onMessageListener = (request, sender, sendResponse) => {
    // Handle event for when Gmail page becoming visible separately as we just want to re-init the extension but don't need to send a response
    const isGmailVisibilityChange =
      request && request.method === GMAIL_VISIBILITY_CHANGE;

    if (!self.window.sessionStorage.serviceWorkerActive) {
      logger.debug(
        `[SW EVENT] onMessageListener - serviceWorkerActive: ${false}, method: ${
          request.method
        }`
      );
      // TODO: We will repeatedly call initExtension if the service worker is not active. Can we prevent this?
      initExtension()
        .then(() => {
          if (!isGmailVisibilityChange)
            return messageHandler(request, sender, sendResponse);
          return undefined;
        })
        .catch((error) => {
          console.error(error);
        });
      return true;
    } else if (!isGmailVisibilityChange) {
      return messageHandler(request, sender, sendResponse);
    }
    return undefined;
  };

  const onMessageExternalListener = (request) => {
    const isOffscreenRestartMessage =
      request && request.method === RESTART_OFFSCREEN_DOCUMENT;

    if (isOffscreenRestarting || !isOffscreenRestartMessage) {
      return;
    }
    isOffscreenRestarting = true;
    setTimeout(() => {
      isOffscreenRestarting = false;
    }, 20000);
    launchOffscreen();
  };

  // Set up Listeners
  //
  // Fired when chrome starts up and chrome profile has this extension
  if (!chrome.runtime.onStartup.hasListener(onStartupHandler))
    chrome.runtime.onStartup.addListener(onStartupHandler);

  //Fired when the extension is first installed, when the extension is updated to a new version, and when Chrome is updated to a new version.
  if (!chrome.runtime.onInstalled.hasListener(onInstalledHandler))
    chrome.runtime.onInstalled.addListener(onInstalledHandler);

  // MV3 TODO add user interaction?
  // Gets triggered on new version available
  // Update will be installed on next time the service worker unloads
  // The restartExtension below will restart the extension immediately on this event
  // regardless of what the user is doing
  if (!chrome.runtime.onUpdateAvailable.hasListener(onUpdateAvailableHandler))
    chrome.runtime.onUpdateAvailable.addListener(onUpdateAvailableHandler);

  //Fired when a connection is made from either an extension process or a content script (by runtime.connect).
  if (!chrome.runtime.onConnect.hasListener(onConnectHandler))
    chrome.runtime.onConnect.addListener(onConnectHandler);

  // Fired when a message is received from a content script
  if (!chrome.runtime.onMessage.hasListener(onMessageListener))
    chrome.runtime.onMessage.addListener(onMessageListener);

  if (!chrome.runtime.onMessageExternal.hasListener(onMessageExternalListener))
    chrome.runtime.onMessageExternal.addListener(onMessageExternalListener);

  // Fired when chrome alarm triggers
  chrome.alarms.onAlarm.addListener(apiRetryHandleAlarm);
} catch (error) {
  LogError({
    error,
    fingerprint: ['background-sw-listeners'],
    tags: { source: 'background-sw-listeners' },
    stacktrace: true,
  });
}

// Start Setup
try {
  if (!self.window) {
    initSelf();
  }
  raven.init();
  launchOffscreen();
  setupWebsocketListener();
  setUpEverywhereListener();
  enableRejectionTracking();
  reloadGmailTabs();

  // eslint-disable-next-line promise/catch-or-return
  initFeatureToggleManager().then(() => {
    if (!isExtensionInitialized) {
      initExtension();
      isExtensionInitialized = true;
    }
    if (
      isFeatureEnabledForSigExt(Extension_SW_Keep_Alive) &&
      !isKeepAliveRunning
    ) {
      isKeepAliveRunning = true;
      keepAlive();
    }
  });
} catch (error) {
  LogError({
    error,
    fingerprint: ['background-sw-startup'],
    tags: { source: 'background-sw-startup' },
    stacktrace: true,
  });
}
