import { SENSITIVE_PROPERTIES } from '../constants';
import type { JsonValue, PropertyResolver } from './types';

// This defines a local safe reference to the global.Window object
// We use an exhaustion method approach, where we check on a priority-based
// approach to get the `global` context for the given environment
export const lWindow = (function () {
  // eslint-disable-next-line
  if (typeof window !== 'undefined') {
    // eslint-disable-next-line
    return window;
  }

  if (typeof globalThis !== 'undefined') {
    return globalThis;
  }

  // eslint-disable-next-line
  if (typeof self !== 'undefined') {
    // eslint-disable-next-line
    return self;
  }

  throw new Error('[usage-tracker-js] Unable to determine the global context');
})();

export const ensureFn = <T extends Function>(
  fn: T | undefined,
  fallback: Function = () => {}
): T => (typeof fn === 'function' ? fn : (fallback as unknown as T));

export const getObjectKeys = Object.keys as <T extends {}>(
  obj: T
) => Array<keyof T & string>;

export const isPromise = (subject: any): subject is Promise<any> =>
  (Boolean(subject) &&
    typeof subject === 'object' &&
    typeof subject.then === 'function') ||
  subject instanceof Promise;

export const promiseHasDone = (promise: any): promise is { done: () => void } =>
  Boolean(promise) &&
  typeof promise === 'object' &&
  typeof promise.done === 'function';

export const reduceObject =
  <T extends {}>(obj: Partial<T> | T) =>
  (fn: (accumulator: T, key: keyof T) => T) =>
    getObjectKeys(obj).reduce(fn, {} as T);

export const between = (str = '', left = '', right = '') => {
  const leftIndex = str.indexOf(left);
  const rightIndex = str.indexOf(right);

  return str.substr(
    leftIndex + left.length,
    rightIndex - leftIndex - right.length
  );
};

// Note this is a naive approach and should not be used outside this library
export const debounce = (fn: (...args: any[]) => any, wait: number) => {
  let timeout = 0;
  let result: any;

  const debounced = (...args: any[]) => {
    if (timeout) {
      clearTimeout(timeout);
    }

    timeout = setTimeout(() => {
      timeout = 0;
      result = fn.apply(null, args);
    }, wait);

    return result;
  };

  return debounced;
};

export const defaults = <
  Source extends
    | Partial<Record<keyof Blueprint, any>>
    | Record<keyof Blueprint, any>,
  Blueprint extends Record<
    string,
    Source[keyof Blueprint] | Blueprint[keyof Blueprint]
  >,
>(
  source: Source = {} as Source,
  blueprint: Blueprint = {} as Blueprint
) => {
  const withDefaults = reduceObject(blueprint)((accumulator, key) => {
    const value = source[key];

    if (value === undefined && blueprint[key] !== undefined) {
      accumulator[key] = blueprint[key];
    }

    return accumulator;
  });

  return {
    ...source,
    ...withDefaults,
  };
};

export const isArray = (thing: any): thing is Array<any> => {
  if ('isArray' in Array) {
    return Array.isArray(thing);
  }

  return Object.prototype.toString.call(thing) === '[object Array]';
};

export const getRealTypeOf = <T extends any>(thing: T) => {
  let type = typeof thing as
    | 'string'
    | 'number'
    | 'boolean'
    | 'array'
    | 'object'
    | 'function'
    | 'undefined';

  if (isArray(thing)) {
    type = 'array';
  }

  if (thing === null) {
    return 'null';
  }

  return type;
};

export const makeUuid = () => {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
    let v;
    const r = (Math.random() * 16) | 0; // eslint-disable-line no-bitwise
    if (c === 'x') {
      v = r;
    } else {
      v = (r & 0x3) | 0x8; // eslint-disable-line no-bitwise
    }
    return v.toString(16);
  });
};

export const mapObject = <T extends Record<string, any>>(
  source: T = {} as T,
  iteratee: (key: keyof T, value: T[keyof T]) => any
) => {
  return reduceObject<Record<keyof T, any>>(source)((accumulator, key) => {
    accumulator[key] = iteratee(key, source[key]);
    return accumulator;
  });
};

export const omit = <T extends {}, K extends Extract<keyof T, string>>(
  source: T = {} as T,
  list: K[] = []
): Omit<T, K> => {
  return reduceObject(source || ({} as T))((accumulator, key) => {
    const includes = list.includes(key as any);

    if (!includes) {
      accumulator[key] = source[key];
    }

    return accumulator;
  });
};

export const pick = <T extends {}, K extends keyof T>(
  source: T = {} as T,
  list: K[] | readonly K[] = []
): Pick<T, K> =>
  Object.assign({}, ...list.map((key) => ({ [key]: source[key] })));

export const once = <ReturnType extends any>(
  fn: (...args: any[]) => ReturnType
) => {
  let isCached: boolean;
  let result: any;

  return (...args: any[]): ReturnType => {
    if (!isCached) {
      isCached = true;
      result = fn(...args);
    }
    return result;
  };
};

export const pluck = <
  Dictionary extends Record<string, any>,
  Collection extends Record<string, Dictionary>,
>(
  subject: keyof Dictionary,
  collection: Collection
) => {
  type PluckedCollection = Record<
    keyof Collection,
    Dictionary[keyof Dictionary]
  >;

  // We cannot use reduceObject here since the return types are intriniscally different
  return getObjectKeys(collection).reduce((accumulator, key) => {
    const entry = collection[key];
    accumulator[key] = entry[subject];
    return accumulator;
  }, {} as PluckedCollection) as PluckedCollection;
};

export const trim = (str = '', outer = '') => {
  str = str.replace(/^\s+|\s+$/g, '');

  if (str.indexOf(outer) === 0) {
    str = str.substr(outer.length);
  }

  if (str.indexOf(outer) === str.length - outer.length) {
    str = str.substr(0, str.indexOf(outer));
  }

  return str;
};

export const shallowCopy = <T extends {}>(source: T = {} as T) => {
  return reduceObject<T>(source)((accumulator, key) => {
    accumulator[key] = source[key];
    return accumulator;
  });
};

export const truncate = (str = '', limit = 256) => {
  let truncated = str;
  if (truncated.length > limit) {
    truncated = truncated.substr(0, limit);
    truncated = `${truncated}[..]`;
  }
  return truncated;
};

export const createQueue = <QueueItem extends any>() => {
  const queue: QueueItem[] = [];

  return {
    enqueue: (entry: QueueItem) => queue.unshift(entry),
    dequeue: () => queue.shift(),
    peek: () => queue[0],
  };
};

export const safeGetOrDefault = <Default extends PropertyResolver<JsonValue>>(
  path: string[] = [],
  defaultValue: Default,
  root: {} = lWindow
): Default => {
  let result: any = root;

  path.forEach((current) => {
    const hasNode = result && current in result;

    result = hasNode ? result[current] : undefined;
  });

  return result === undefined ? defaultValue : result;
};

export const prettyPrint = (str = '') => {
  const snakeCase = str
    .toString()
    .replace(/(?:^|\.?)([A-Z]+)/g, (x, y: string) => `_${y.toLowerCase()}`)
    .replace(/^_/, '');

  const titleCase = snakeCase.replace(
    /\w\S*/g,
    (txt) => txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase()
  );

  return titleCase.replace(/-/g, ' ').replace(/_/g, ' ').replace(/\s{2}/g, ' ');
};

export const convertEventNameToEventKey = (str = '') =>
  str
    .replace(/[_|-]/g, ' ')
    .replace(/(?:^\w|[A-Z]|\b\w)/g, (letter, index) =>
      letter[index === 0 ? 'toLowerCase' : 'toUpperCase']()
    )
    .replace(/\s+/g, '');

export const deepFreeze = <T extends any>(source: T): Readonly<T> => {
  if (source && typeof source === 'object' && !Object.isFrozen(source)) {
    Object.freeze(source);
    Object.getOwnPropertyNames(source).forEach((prop) =>
      deepFreeze(source[prop as keyof T])
    );
  }

  return source;
};

export const proxyLogger = (tags: {}) => (c?: Function) => (m: any, e: any) => {
  const decoratedData = { fingerprint: ['usage-tracker-js'], tags };

  return ensureFn(c)(m, { ...decoratedData, ...e });
};

export const mask = <T extends string[]>(
  properties: T,
  _mask: string | null = '**********'
) =>
  properties.reduce(
    (acc, key) => ({
      /* eslint-disable-next-line hubspot-dev/no-reduce-accumulator-copy */
      ...acc,
      [key]: _mask,
    }),
    {} as Record<keyof T, typeof _mask>
  );

export const maskEmail = (input: JsonValue) =>
  input && typeof input === 'string'
    ? input.replace(/^(.{0})[^@]+/, '$1*****')
    : input;

export const replaceSentryValues = (input: Record<string, any> = {}) => {
  const sensitiveProps = Object.entries(pick(input, SENSITIVE_PROPERTIES))
    .filter(([, value]) => Boolean(value))
    .map(([key]) => key);

  const parsedValues = Object.entries(input)
    .map(([k, v]) => [k, typeof v === 'function' ? 'Function' : v])
    .map(([k, v]) => [k, typeof v === 'undefined' ? null : v]);

  return { ...Object.fromEntries(parsedValues), ...mask(sensitiveProps) };
};

export const dispatchFunctionAsync = <T extends Function>(f: T) => {
  // Makes a synchronous function non-blocking, in case we want to dispatch things
  // without waiting for the result or for their call to resolve
  new Promise((resolve) => resolve(f())).catch((e) => {
    throw new Error(e);
  });
};

// Better (and more modern) approach for getting the screen size
// this is used as a metaProperty for each Event
export const getScreenWidthSize = (size: number) => {
  if (size > 1280) {
    return 'xlarge (> 1280)';
  }

  if (size >= 1024) {
    return 'large (1024 - 1280)';
  }

  if (size >= 680) {
    return 'medium (680 - 1024)';
  }

  if (size > 0) {
    return 'small (< 680)';
  }

  return 'unknown';
};
