import enviro from 'enviro';
import * as PureUrlUtils from './pure-url-utils';

type Environment = 'prod' | 'qa';
type HubletPostfixLocation = 'subdomain' | 'domain';

export interface OverrideConfig {
  domainOverride?: string;
  envOverride?: Environment;
  hubletOverride?: string;
  hubletPostfixLocation?: HubletPostfixLocation;
  tldOverride?: string;
}

function getHubletToUse(overrideConfig?: OverrideConfig) {
  return overrideConfig && overrideConfig.hubletOverride
    ? overrideConfig.hubletOverride
    : enviro.getHublet();
}

function getEnvToUse(overrideConfig?: OverrideConfig): Environment {
  return overrideConfig && overrideConfig.envOverride
    ? overrideConfig.envOverride
    : (enviro.getShort() as Environment);
}

export function getHubletPostfix(overrideConfig?: OverrideConfig): string {
  const hubletToUse = getHubletToUse(overrideConfig);
  return PureUrlUtils.getHubletPostfix(hubletToUse, overrideConfig);
}

export function getSubDomain(
  prefix: string,
  overrideConfig?: OverrideConfig
): string {
  const hubletToUse = getHubletToUse(overrideConfig);
  return PureUrlUtils.getSubDomain(hubletToUse, prefix, overrideConfig);
}

export function getDomain(overrideConfig?: OverrideConfig) {
  const hublet = getHubletToUse(overrideConfig);
  const short = getEnvToUse(overrideConfig);

  return PureUrlUtils.getDomain(hublet, short, overrideConfig);
}

export function getEnvPostfix(overrideConfig?: OverrideConfig) {
  const envToUse = getEnvToUse(overrideConfig);
  return PureUrlUtils.getEnvPostfix(envToUse, overrideConfig);
}

export function getDomainPrefix(overrideConfig?: OverrideConfig) {
  return PureUrlUtils.getDomainPrefix(overrideConfig);
}

export function getHubletDomainPostfix(overrideConfig?: OverrideConfig) {
  const hubletToUse = getHubletToUse(overrideConfig);
  return PureUrlUtils.getHubletDomainPostfix(hubletToUse, overrideConfig);
}

export function getTld(overrideConfig?: OverrideConfig) {
  return PureUrlUtils.getTld(overrideConfig);
}

export function getPathPrefix(subDomainPrefix: string) {
  return PureUrlUtils.getPathPrefix(subDomainPrefix);
}
