export const getInstalledExtensions = () =>
  new Promise((resolve) => {
    // This is using the callback intentionally because it surfaces the user gesture error
    chrome.permissions.request({ permissions: ['management'] }, (granted) => {
      if (granted) {
        // This is using the callback intentionally because it surfaces the user gesture error
        chrome.management.getAll((extensions) =>
          resolve(
            extensions.map((extension) => ({
              id: extension.id,
              name: extension.name,
              enabled: extension.enabled,
              version: extension.version,
            }))
          )
        );
      } else {
        resolve([]);
      }
    });
  });

export const extensionCheckerHandler = () => {
  chrome.permissions
    .request({ permissions: ['management'] })
    .then((granted) => {
      if (granted) {
        chrome.management
          .getAll()
          .then((extensions) => {
            sendToContentScripts('blacklistedExtensions', {
              blacklistedExtensions: extensions
                .filter(
                  (extension) =>
                    conflictingExtensionsIds[extension.id] && extension.enabled
                )
                .map((extension) => conflictingExtensionsIds[extension.id]),
            });
          })
          .catch((err) => {
            LogInfo({
              error: err,
              fingerprint: ['extensionChecker/extensionCheckerHandler'],
              tags: { source: 'chrome.management.getAll' },
            });
          });
      }
    })
    .catch((err) => {
      LogInfo({
        error: err,
        fingerprint: ['extensionChecker/extensionCheckerHandler'],
        tags: { source: 'chrome.permissions.request' },
        extraData: { params: [{ permissions: ['management'] }] },
      });
    });
  return null;
};
