export type MetricType = 'COUNTER' | 'HISTOGRAM' | 'TIMER';

export type Dimensions = Record<string, string>;

export interface MetricReport {
  name: string;
  type: MetricType;
  values: number[];
  dimensions: Dimensions;
}

export interface MetricConstructor {
  new (name: string, dimensions: Dimensions): Metric;
}

abstract class Metric {
  private readonly name: string;
  private dimensions: Dimensions;

  constructor(name: string, dimensions: Dimensions) {
    this.name = name;
    this.dimensions = this.filterStringDimensions(dimensions);
  }

  private filterStringDimensions(dimensions: Dimensions): Dimensions {
    const filtered: Dimensions = {};

    for (const [key, value] of Object.entries(dimensions)) {
      if (typeof value === 'string') {
        filtered[key] = value;
      } else {
        if (process.env.NODE_ENV !== 'production') {
          console.error(
            `[metrics-js] Metric ${
              this.name
            } received a non-string dimension value for key "${key}" (${typeof value}: ${value}).\n\nDimensions must be string values only. Non-string values have been filtered out.\nIf you need to track non-string data, please convert it to a string first.\nIf you have further questions please reach out in #frontend-platform-support.`
          );
        }
      }
    }

    return filtered;
  }

  getDimensions() {
    return this.dimensions;
  }

  abstract canFlush(endOfSession: boolean): boolean;

  abstract flush(): MetricReport;

  getName() {
    return this.name;
  }

  getSeries(qualifier: string) {
    return [this.name, qualifier].join('.');
  }

  toString() {
    return JSON.stringify(
      {
        name: this.getName(),
        dimensions: this.getDimensions(),
      },
      null,
      2
    );
  }
}

export { Metric };
