import { pixelRegexStr } from 'sales-clients-common/emailTracking/PixelGenerators';

/*
 * Due to Critsit (#3719), please do not modify our declarativeNetRequest rules without reading.
 * See doc: https://developer.chrome.com/docs/extensions/reference/api/declarativeNetRequest#rule-prioritization-within-an-extension
 * When adding a rule, we need to exercise caution for 'redirect' rules
 * Depending on the content type, redirect rules can self trigger
 * These can potentailly trigger a redirect loop if not accompanied by special handling
 * At some level, this requires being cognizant in the regex we use for the redirect
 * But, for extra protection, we can accompany redirect rules with a base/exit case
 * Whatever content we're adding for the direct, we can add a higher priority rule scanning for
 * the intended URL, and add it as an 'allow' rule to prevent the redirect from triggering
 * For an example of this, see either redirect rule 1 or 2 and their accompanied exit case in rule 3
 * Once we see the `checkOwner` param is in the URL, we know our redirect has been successful
 * So we can 'allow', and ensure the cycle ends
 */

const getDeclarativeRules = (userId: number) => [
  {
    id: 1,
    action: {
      type: 'redirect',
      redirect: {
        regexSubstitution: `\\1&checkOwner=${userId}`,
      },
    },
    condition: {
      isUrlFilterCaseSensitive: false,
      regexFilter:
        '(?:https?://ci[0-9]\\.googleusercontent\\.com/[^/]+/[^/]+#)?(https?://.*/(?:s|e)[1-9]t/o/.*\\?(?:si=([0-9a-z]+)&(?:amp;)?pi=[0-9a-z\\-]+).*)',
    },
    priority: 1,
  },
  {
    id: 2,
    action: {
      type: 'redirect',
      redirect: { regexSubstitution: `\\1?checkOwner=${userId}` },
    },
    condition: {
      isUrlFilterCaseSensitive: false,
      regexFilter: pixelRegexStr,
    },
    priority: 1,
  },
  {
    id: 3,
    action: {
      type: 'allow',
    },
    condition: {
      isUrlFilterCaseSensitive: false,
      regexFilter: '[?&]checkOwner=\\d+',
    },
    priority: 2,
  },
];

export default getDeclarativeRules;
