// @ts-expect-error untyped
import { chrome } from 'sales-clients-common/globals/window';
import { LogError, LogInfo } from 'sales-clients-common/utils/reportError';
import { RUNTIME_ERROR_THRESHOLD } from '../constants/chrome';
import { handleInternalCallback } from './utils';
import { isMv3 } from './safeRuntime';

export const SYNC_GET = 'SYNC_GET';
export const SYNC_SET = 'SYNC_SET';
export const SYNC_USAGE = 'SYNC_USAGE';
export const SYNC_REMOVE = 'SYNC_REMOVE';
export const LOCAL_GET = 'LOCAL_GET';
export const LOCAL_SET = 'LOCAL_SET';
export const LOCAL_USAGE = 'LOCAL_USAGE';
export const LOCAL_REMOVE = 'LOCAL_REMOVE';
export const SESSION_GET = 'SESSION_GET';
export const SESSION_SET = 'SESSION_SET';
export const SESSION_USAGE = 'SESSION_USAGE';
export const SESSION_REMOVE = 'SESSION_REMOVE';
type Method =
  | typeof SYNC_GET
  | typeof SYNC_SET
  | typeof SYNC_USAGE
  | typeof SYNC_REMOVE
  | typeof LOCAL_GET
  | typeof LOCAL_SET
  | typeof LOCAL_USAGE
  | typeof LOCAL_REMOVE
  | typeof SESSION_GET
  | typeof SESSION_SET
  | typeof SESSION_USAGE
  | typeof SESSION_REMOVE;

// This method attempts to call the chrome.storage methods directly, but logs a generic sentry error on failure
// in order to allow us to track how often this fails, without getting individual sentries

export const callStorageMethodSafe = (method: Method, ...args: any[]) => {
  try {
    switch (method) {
      case SYNC_GET:
        return chrome.storage.sync.get(...args);
      case SYNC_SET:
        return chrome.storage.sync.set(...args);
      case SYNC_USAGE:
        return chrome.storage.sync.getBytesInUse(...args);
      case SYNC_REMOVE:
        return chrome.storage.sync.remove(...args);
      case LOCAL_GET:
        return chrome.storage.local.get(...args);
      case LOCAL_SET:
        return chrome.storage.local.set(...args);
      case LOCAL_USAGE:
        return chrome.storage.local.getBytesInUse(...args);
      case LOCAL_REMOVE:
        return chrome.storage.local.remove(...args);
      case SESSION_GET:
        return chrome.storage.session.get(...args);
      case SESSION_SET:
        return chrome.storage.session.set(...args);
      case SESSION_USAGE:
        return chrome.storage.session.getBytesInUse(...args);
      case SESSION_REMOVE:
        return chrome.storage.session.remove(...args);
      default:
        return null;
    }
  } catch (error) {
    // segment missing chrome storage errors into a new Sentry error.
    if (
      !chrome.storage ||
      !chrome.storage.sync ||
      !chrome.storage.local ||
      !chrome.storage.session
    ) {
      if (!sessionStorage.storageError) {
        sessionStorage.storageError = 1;
      } else if (sessionStorage.storageError > RUNTIME_ERROR_THRESHOLD) {
        LogInfo({
          error,
          fingerprint: ['chrome runtime/storage is undefined'],
          tags: { storageUndefined: method },
          extraData: {
            storageUndefined: method,
            messageString: 'SafeStorage chrome.storage is undefined',
          },
        });
      } else {
        sessionStorage.runtimeError =
          parseInt(sessionStorage.runtimeError, 10) + 1;
      }
    } else {
      LogError({
        error,
        fingerprint: [`Error in chrome safeStorage`, method],
        tags: { failingStorageMethod: method },
        extraData: {
          failingStorageMethod: method,
          messageString: 'Error in chrome safeStorage',
        },
      });
    }
    return null;
  }
};

export function syncGet(keys?: string | string[] | object): Promise<any>;
/**
 * @deprecated Please use promises instead of callbacks
 */
export function syncGet(
  keys?: string | string[] | object,
  callback?: (items: any) => void
): Promise<any>;
export function syncGet(
  keys?: string | string[] | object,
  callback?: (items: any) => void
): Promise<any> {
  if (callback || !isMv3()) {
    return new Promise((resolve, reject) => {
      const internalCallback = (response: any): void =>
        handleInternalCallback(callback, response, resolve, reject);
      callStorageMethodSafe(SYNC_GET, keys, internalCallback);
    });
  }
  return callStorageMethodSafe(SYNC_GET, keys);
}

export function syncSet(items: object): Promise<void>;
/**
 * @deprecated Please use promises instead of callbacks
 */
export function syncSet(items: object, callback?: () => void): Promise<void>;
export function syncSet(items: object, callback?: () => void): Promise<void> {
  if (callback || !isMv3()) {
    return new Promise((resolve, reject) => {
      const internalCallback = (): void =>
        handleInternalCallback(callback, undefined, resolve, reject);
      callStorageMethodSafe(SYNC_SET, items, internalCallback);
    });
  }
  return callStorageMethodSafe(SYNC_SET, items);
}

