import { ExtensionContext } from 'sales-clients-common/chrome/types';
import { LogWarn, LogInfo } from 'sales-clients-common/utils/reportError';
// @ts-expect-error untyped
import chrome from 'sales-ext-common/globals/chrome';

type onContextChangeHandler = (contexts: ExtensionContext[]) => void;

class ContextChangeEventEmitter {
  private intervalId?: number;
  private currentContexts: ExtensionContext[] = [];
  private interval = 1e3;
  private listeners = new Set<onContextChangeHandler>();

  constructor() {
    this.startWatching();
  }

  onContextChange(handler: onContextChangeHandler) {
    this.listeners.add(handler);
  }

  startWatching() {
    // TODO: temporary patch. We need to find a better alterantive to handle sidePanel state.
    // We should at least update the context when the sidePanel is opened/closed explicitly.
    if (chrome.runtime.getContexts) {
      this.intervalId = setInterval(() => {
        chrome.runtime
          .getContexts({ contextTypes: ['SIDE_PANEL'] })
          .then((contexts: ExtensionContext[]) => {
            const shouldEmit = this.updateContexts(contexts);
            if (shouldEmit) {
              this.listeners.forEach((handler) => handler(contexts));
            }
          })
          .catch((err: Error) => {
            LogWarn({
              error: err,
              fingerprint: ['contextEventEmitter.startWatching'],
              tags: { source: 'contextEventEmitter.startWatching' },
            });
          });
      }, this.interval);
    } else {
      LogInfo({
        message: 'ContextChangeEventEmitter: getContexts unavailable',
        extraData: {
          userAgent: navigator ? navigator.userAgent : null,
        },
        fingerprint: ['ContextChangeEventEmitter'],
        tags: { source: 'ContextChangeEventEmitter' },
      });
    }
  }

  stopWatching() {
    clearInterval(this.intervalId);
  }

  // returns boolean of whether there were changes
  updateContexts(newContexts: ExtensionContext[]): boolean {
    const currentContextIds = this.currentContexts.map(
      (context) => context.contextId
    );
    const contextsAreEqual =
      newContexts.length === currentContextIds.length &&
      newContexts.every(({ contextId }) =>
        currentContextIds.includes(contextId)
      );
    if (contextsAreEqual) {
      return false;
    }

    this.currentContexts = newContexts;
    return true;
  }
}

export default new ContextChangeEventEmitter();
