'use es6';

import { createStack } from 'hub-http';
import promiseClient from 'hub-http/adapters/promiseClient';
import * as core from 'hub-http/middlewares/core';
import { rewriteUrl } from 'hub-http/middlewares/debug';
import sidekickDefaults from 'hub-http-contrib/middlewares/sidekickDefaults';
import { immutableResponse } from 'hub-http-contrib/middlewares/immutable';
import withCsrfFromBackground from 'sales-clients-common/utils/withCsrfFromBackground';
import { reloadOnUnauthorized } from 'sales-clients-common/utils/reloadOnUnauthorized';
import { restartExtension } from '../utils/tabs';
import logger from 'SignalsExtension/utils/logger';
import { dynamicHubletEnvironmentUrl, customRetryOnError } from './middleware';
import withFetch from 'SignalsExtension/api/withFetch';
import withPortalId from 'SignalsExtension/api/withPortalId';

const reloadOnUnauthorizedSuccess = () => {
  logger.warn(`SignalsExtension HubApiHttpClient: reloading on unauthorized`);
  restartExtension();
};

const reloadOnUnauthorizedFail = (e) =>
  logger.warn(`Error reloading on unauthed: ${e && e.message}`);

export default promiseClient(
  createStack(
    core.services,
    withFetch,
    sidekickDefaults,
    core.jsonBody,
    core.httpsOnly,
    dynamicHubletEnvironmentUrl('app', 'hubspot'),
    withCsrfFromBackground,
    reloadOnUnauthorized(reloadOnUnauthorizedSuccess, reloadOnUnauthorizedFail),
    withPortalId,
    core.withQuery,
    rewriteUrl,
    customRetryOnError,
    core.validateStatus,
    immutableResponse
  )
);
