import { addParamsToUrl } from 'sales-clients-common/utils/url';

export const constructTrackingUrl = (
  trackingUrl: string,
  sidekickUserId: number,
  primaryId: string,
  templateId: number | null
) => {
  return addParamsToUrl(trackingUrl, {
    si: sidekickUserId,
    pi: primaryId,
    ti: templateId,
  });
};

export const constructTrackingPixel = (src: string) =>
  `<img src='${src}' alt="" style='display:none!important' height='1' width='1'>`;

// Should match output from constructTrackingPixel
export const constructTrackingPixelNode = (
  src: string,
  pixelDataAttrName?: string
) => {
  const pixelNode = Object.assign(document.createElement('img'), {
    src,
    alt: '',
    style: 'display:none!important',
    height: '1',
    width: '1',
  });
  if (pixelDataAttrName) pixelNode.setAttribute(pixelDataAttrName, '');
  return pixelNode;
};

// These strings are used as fallback values in case no regex is provided from the configuration. If you are changing these values,
//   please also change the values in email-tracking.json in SERA as well.
const googleUserContentRegexString = `(?:https?://ci[0-9]\\.googleusercontent\\.com/[^/]+/[^/]+#)?`;
export const pixelRegexStr = `${googleUserContentRegexString}(https?://.*/[0-9A-Z]{0,2}(to)/([0-9A-Za-z]{1,2}\\+[0-9]+)/.*/.*)`;
export const legacyPixelRegexStr = `${googleUserContentRegexString}(https?://.*/((e1t)|(e2t)|(s[0-9]{1,2}t))/o/.*\\?si=([0-9]+)&(amp;)?pi=([0-9a-z]{8}-([0-9a-z]{4}-){3}[0-9a-z]{12}|[0-9a-z]{32}))`;

export const generatePixelRegex = () => new RegExp(pixelRegexStr);
export const generatePixelMatcher = (
  trackingPixelRegex = generatePixelRegex()
) => {
  return (str: string) => !!str.match(trackingPixelRegex);
};

export const generateLegacyPixelRegex = () => new RegExp(legacyPixelRegexStr);
export const generateLegacyPixelMatcher = (
  trackingPixelRegex = generateLegacyPixelRegex()
) => {
  return (str: string) => !!str.match(trackingPixelRegex);
};
