import { restartExtension } from 'SignalsExtension/utils/tabs';
import logger from 'SignalsExtension/utils/logger';
import { SHORTLINK_OPEN } from 'SignalsExtension/constants/StorageKeys';
// @ts-expect-error ts-migrate(7016) js file missing types
import localStorage from 'SignalsExtension/globals/localStorage';

export const shortlinkMethodHandler = (isShortlinkLoginWindowOpen: boolean) => {
  try {
    localStorage.setItem(SHORTLINK_OPEN, `${isShortlinkLoginWindowOpen}`);
    // restart after user picks portal from shortlink popup
    if (!isShortlinkLoginWindowOpen) {
      restartExtension();
    }
  } catch (error) {
    logger.log(`Failed to update SHORTLINK_OPEN : ${error}`);
  }
};

export const shortlinkResetMethodHandler = () => {
  localStorage.setItem(SHORTLINK_OPEN, false);
};
