/* eslint-disable hubspot-dev/no-confusing-browser-globals */
/* eslint-disable hubspot-dev/no-async-await */
/* global G_BUILD_ENVIRONMENT, G_IS_DEPLOYED, G_SALES_MODAL_IFRAME_DEPLOYED */

'use es6';

import storage from '../shims/synchronousStorage';
import logger from 'SignalsExtension/utils/logger';

const IFRAME_DEPLOYED_KEY = 'IFRAME_DEPLOYED';
const ENV_KEY = 'ENV';
const DEPLOYED_KEY = 'DEPLOYED';
const HUBLET_KEY = 'HUBLET';

const setAllEnvs = (environment, { iframeDeployed, hublet, isDeployed }) => {
  self.window = {
    ...self.window,
    ...environment,
    [IFRAME_DEPLOYED_KEY]: iframeDeployed === 'true',
    [DEPLOYED_KEY]: isDeployed === 'true',
  };

  storage.setItem(environment);
  storage.setItem({
    [DEPLOYED_KEY]: isDeployed === 'true',
  });
  storage.setItem({
    [IFRAME_DEPLOYED_KEY]: iframeDeployed === 'true',
  });

  if (hublet) {
    self.window[HUBLET_KEY] = hublet;
    storage.setItem({ [HUBLET_KEY]: hublet });
  }

  logger.debug(
    `[Enviro Shim] ENV: ${environment.ENV} ${DEPLOYED_KEY}: ${
      isDeployed === 'true'
    } ${IFRAME_DEPLOYED_KEY}: ${iframeDeployed === 'true'} hublet: ${hublet}`
  );

  return;
};

/* This function pulls the environments for ENV from the globals set in the webpack config.
   However, if the corresponding key is set in chrome.storage.local, that will override
   whatever was set in the build script. If anything fails with getting chrome.storage.local,
   we default to whatever the build script originally set.
   Use chrome.storage.local.clear() to clear
*/
export default async function initEnviro() {
  let hublet = null;
  let environment = {
    [ENV_KEY]: G_BUILD_ENVIRONMENT,
  };
  let isDeployed = G_IS_DEPLOYED;
  let iframeDeployed = G_SALES_MODAL_IFRAME_DEPLOYED;

  try {
    const result = await storage.getItem([
      ENV_KEY,
      IFRAME_DEPLOYED_KEY,
      HUBLET_KEY,
    ]);

    if (result) {
      if (result[ENV_KEY] !== undefined) {
        environment = {
          [ENV_KEY]: result[ENV_KEY],
        };
      }
      if (result[IFRAME_DEPLOYED_KEY] !== undefined) {
        iframeDeployed = result[IFRAME_DEPLOYED_KEY];
      }
      if (result[HUBLET_KEY] !== undefined) {
        hublet = result[HUBLET_KEY];
      }
      if (result[DEPLOYED_KEY] !== undefined) {
        isDeployed = result[DEPLOYED_KEY];
      }
    }
    setAllEnvs(environment, {
      iframeDeployed,
      hublet,
      isDeployed,
    });
    return;
  } catch (err) {
    console.error('Error fetching from chrome.storage.local: ', err);
    setAllEnvs(environment, {
      iframeDeployed,
      hublet,
      isDeployed,
    });
    return;
  }
}
