import { LogError } from 'sales-clients-common/utils/reportError';
import {
  fetchFeatureOverrideConfig,
  featureOverrideConfigType,
} from 'sales-ext-common/apis/configApi';
import { PromiseClient } from 'hub-http/adapters/promiseClient';

// Add features here
export const GmailExtension = 'GmailExtension'; // If disabled, will prevent extension components from loading into Gmail page aside from the sprocket menu
export const Feature_Everywhere = 'Feature_Everywhere'; // HubSpot Everywhere scripts
export const Feature_SelfOpenBlocking = 'Feature_SelfOpenBlocking';
export const Feature_SalesSidebar = 'Feature_SalesSidebar';
export const Feature_LogTrackIndicator = 'Feature_LogTrackIndicator';
export const ComposeToolbarLogTrackCheckBox = 'ComposeToolbarLogCheckBox';
export const Extension_SW_Keep_Alive = 'Extension_SW_Keep_Alive'; // Extension background service worker keep alive mechanism
export const SidebarApolloProvider = 'SidebarApolloProvider'; // ApolloProvider for the sidebar
export const Winter2024DomStyles = 'Winter2024DomStyles'; // Toggle whether to use new styles for the Winter 2024 Gmail update
export const AssociationAuditElement = 'AssociationAuditElement'; // Toggle whether to add an audit element to the association data node
export const Feature_PerformanceTelemetry = 'Feature_PerformanceTelemetry'; // Toggle whether to log performance telemetry to cartography API

export const ONE_HOURS_MS = 3600000;

let featureOverrides: featureOverrideConfigType = {};

let initialized = false;

export const getFeatureOverrides = () => {
  return featureOverrides;
};

export const fetchFeatureOverrides = async (http: PromiseClient) => {
  featureOverrides = await fetchFeatureOverrideConfig(http);
};

export const initFeatureToggles = async (http: PromiseClient) => {
  if (!initialized) {
    setInterval(() => {
      fetchFeatureOverrides(http);
    }, ONE_HOURS_MS);

    initialized = true;
  }

  await fetchFeatureOverrides(http);
};

// To minimize error path impact, feature override returns true by default, unless disabled explicitly.
export const isFeatureEnabled = (
  featureName: string,
  userUngatedFor?: (gate: string) => boolean
) => {
  try {
    const featureOverride = featureOverrides[featureName];
    if (featureOverride) {
      if (featureOverride.status === 'disabled') {
        return false;
      } else if (featureOverride.status === 'gated') {
        if (featureOverride.gates && userUngatedFor) {
          const gatedGates = featureOverride.gates.filter((gate) => {
            const userGated = !userUngatedFor(gate);
            return userGated;
          });
          return gatedGates.length === 0;
        }
        // Assume gated feature override has `gates` field set.
      }
    }
  } catch (error) {
    LogError(error);
  }

  return true;
};
