/* hs-eslint ignored failing-rules */
/* eslint-disable hubspot-dev/no-declarations */

import { getUniqueKey } from './utils/uuid';

declare const require: any;

let enabled = false;

const typeOfReason = (reason: any) => {
  return reason === null
    ? 'Null'
    : reason === undefined
    ? 'Undefined'
    : Object.prototype.toString.call(reason).slice(8, -1);
};

const sendAlerts = (reason: any) => {
  const Raven = require('raven-js');
  const unhandledRejectionUuid = getUniqueKey();
  const reasonType = typeOfReason(reason);

  const ravenOpts = {
    tags: {
      isUnhandledPromiseRejection: true,
      unhandledRejectionUuid,
      typeOfReason: reasonType,
    },
    extra: {
      reasonObjKeys: Object.keys(reason).join(),
    },
  };

  if (typeof reason === 'string') {
    Raven.captureMessage(reason, ravenOpts);
  } else {
    Raven.captureException(reason, ravenOpts);
  }
};

const isObject = (it: unknown): it is object => {
  return typeof it === 'object' ? it !== null : typeof it === 'function';
};

const handleUnhandledRejection = (e: PromiseRejectionEvent) => {
  e.preventDefault();

  if (isObject(e.promise)) {
    console.error('Unhandled Promise Rejection', e.reason);
    sendAlerts(e.reason);
  }
};

const disableRejectionTracking = () => {
  enabled = false;
  window.removeEventListener('unhandledrejection', handleUnhandledRejection);
};

const enableRejectionTracking = () => {
  if (enabled) {
    disableRejectionTracking();
  }

  enabled = true;

  window.addEventListener('unhandledrejection', handleUnhandledRejection);
};

export { enableRejectionTracking, disableRejectionTracking };
