import enviro from 'enviro';
import { MetricReport } from './metric/Metric';
import { getGlobal, getHubSpot } from './getGlobal';

export type SendBeacon = (url: string, data: string) => boolean;

// need to support the React Native globalThis
let _customSendBeaconFn: SendBeacon;

export function _sendBeacon(url: string, data: string): boolean {
  const global = getGlobal() as any;
  if (_customSendBeaconFn) {
    return _customSendBeaconFn(url, data);
  }
  return global.navigator && global.navigator.sendBeacon(url, data);
}

function sendBeacon(url: string, data = '') {
  if (!_sendBeacon) {
    return;
  }
  try {
    _sendBeacon(url, data);
  } catch (___err) {
    if (enviro.debug('METRICS')) {
      console.error('[metrics-js] Failed to send metric ', ___err);
    }
  }
}

export function setBeaconApi(beaconFn: SendBeacon) {
  _customSendBeaconFn = beaconFn;
}

interface StaticAppInfo {
  package: string;
  version: string;
}

const staticAppInfo: StaticAppInfo = {
  package:
    (getHubSpot() &&
      (getHubSpot() as any).bender &&
      (getHubSpot() as any).bender.currentProject) ||
    'unknown',
  version:
    (getHubSpot() &&
      (getHubSpot() as any).bender &&
      (getHubSpot() as any).bender.currentProjectVersion) ||
    'unknown',
};

export function setStaticAppInfo(newInfo: StaticAppInfo) {
  Object.assign(staticAppInfo, newInfo);
}

function getMetricsUrl() {
  return `https://metrics-fe-${enviro.getHublet()}.hubspot${
    enviro.isQa() ? 'qa' : ''
  }.com/metrics/v1/frontend/custom/send?hs_static_app=${
    staticAppInfo.package
  }&hs_static_app_version=${staticAppInfo.version}`;
}

export function send(metricReports: MetricReport[]) {
  // to test actually sending locally, run `sessionStorage.setItem('METRICS_DEPLOYED', 'true'); enviro.setDebug('METRICS', true);` in console
  if (!enviro.deployed('METRICS')) {
    if (enviro.debug('METRICS')) {
      console.log('[metrics-js] Dropping local datapoint', metricReports);
    }
    return;
  }

  if (enviro.debug('METRICS')) {
    console.log('[metrics-js] Datapoint sent', metricReports);
  }

  sendBeacon(getMetricsUrl(), JSON.stringify(metricReports));
}
