'use es6';

import { createStack } from 'hub-http';
import promiseClient from 'hub-http/adapters/promiseClient';
import * as core from 'hub-http/middlewares/core';
import { rewriteUrl } from 'hub-http/middlewares/debug';
import withCsrfFromBackground from 'sales-clients-common/utils/withCsrfFromBackground';
import { dynamicHubletEnvironmentUrl, customRetryOnError } from './middleware';
import withFetch from 'SignalsExtension/api/withFetch';
import withPortalId from 'SignalsExtension/api/withPortalId';

export default promiseClient(
  createStack(
    core.services,
    withFetch,
    core.jsonBody,
    core.httpsOnly,
    dynamicHubletEnvironmentUrl('app', 'hubspot'),
    withCsrfFromBackground,
    withPortalId,
    core.withQuery,
    rewriteUrl,
    core.validateStatus,
    core.jsonResponse,
    customRetryOnError
  )
);
