import { hasCooldownElapsed, setCooldown } from './Cooldown';
import { onUnauthorized } from './onUnauthorized';

type $tsMigrateAny = any;

/*
 * If a user hits a 401 in the gmail client, it usually means they're not logged in. This middleware
 * handles that case, calling the passed in reload function. The caveat to this logic is that certain
 * api calls (cookie verify and attributes) can 401, but we don't want to refresh
 * (to allow for inline login). In that case, the ignoreRedirect option prevents a redirect.
 * We throttle this reload to prevent an infite loop of reloading if something goes wrong
 */

const RELOAD_KEY = 'sales-extension-401-reload';
const RELOAD_COOLDOWN = 60 * 60 * 2 * 1000; // 2hrs in ms

export const reloadOnUnauthorized =
  (reloadFunc: $tsMigrateAny, onError: $tsMigrateAny) =>
  (options: $tsMigrateAny) =>
    onUnauthorized(() =>
      hasCooldownElapsed(RELOAD_KEY, RELOAD_COOLDOWN)
        .then((elapsed) => {
          // eslint-disable-next-line @typescript-eslint/no-floating-promises
          setCooldown(RELOAD_KEY);
          return elapsed && reloadFunc();
        })
        .catch(onError)
    )(options);
