// @ts-expect-error dependency missing types
import * as AttributesApi from 'SignalsExtension/api/AttributesApi';
import {
  DND_KEY,
  USER_REGISTRATION_KEY,
} from 'sales-clients-common/constants/ChromeAttributeKeys';

type Attributes = { [key: string]: string };
let attributes: Attributes = {};

export const fetchAttributes = () =>
  AttributesApi.fetchAttributes({
    [DND_KEY]: '',
    [USER_REGISTRATION_KEY]: '',
  }).then((result: Attributes): Attributes => {
    attributes = result;
    return attributes;
  });

export const getAttribute = (key: string): string => {
  return attributes[key] || 'true';
};

export const setAttribute = (
  key: string,
  value: string
): Promise<Attributes> => {
  attributes = { ...attributes, [key]: value };
  return AttributesApi.saveAttribute(key, value);
};

export const clearAttributes = () => {
  attributes = {};
};

export const getDoNotDisturb = () => {
  const stringValue = getAttribute(DND_KEY) || 'true';
  return stringValue.toLowerCase() !== 'true';
};

// DO NOT REMOVE unless no longer needed in SERA
export const setDoNotDisturb = (value: string) =>
  setAttribute(DND_KEY, `${!value}`);
