'use es6';
/* eslint-disable  hubspot-dev/no-confusing-browser-globals*/

/*
 *  Custom tracking client for the shell which does not attempt to use an authed api client
 *  so we don't redirect users when hub-http can't find a portal id
 *  Originally based on the default tracking client in usage-tracker-js
 */

// This is modified to support the service worker 'self' object rather than looking for the window object

import noAuthHttpClient from 'SignalsExtension/api/NoAuthHttpClient';
import { createClient } from 'usage-tracker-core';
import { genericClient } from 'usage-tracker-core/client';
import { getFullUrl } from 'hubspot-url-utils';
import { getHstcCookie } from './getHstcCookie';
import { getEmail } from '../background/models/user';
import { getPortalId } from '../data/PortalIdContainer';
import { getManifestVersion } from 'sales-clients-common/chrome/safeRuntime';
import { LogError, LogWarn } from 'sales-clients-common/utils/reportError';

const origin = getFullUrl('app-api');
const path = '/usage-logging/v1/log/hublytics-multi/no-auth';
const EXTENSION_VERSION = getManifestVersion().slice(1);

export const getTrackingClient = () =>
  createClient({
    getDebug: () => false,
    getEmail: () => getEmail(),
    getHubId: () => getPortalId(),
    getHstc: () => getHstcCookie(),
    getLang: genericClient.getLang,
    getTempStorage: self.window.localStorage.getItem.bind(
      self.window.localStorage
    ),
    setTempStorage: self.window.localStorage.setItem.bind(
      self.window.localStorage
    ),
    logWarning: console.log,
    logError: console.error,
    reportWarning: (error) =>
      LogWarn({
        error,
        fingerprint: ['getTrackingClient', `${error.status}`],
        tags: { source: 'getTrackingClient' },
        extraData: { errorData: JSON.stringify(error) },
        stacktrace: true,
      }),
    reportError: (error) =>
      LogError({
        error,
        fingerprint: ['getTrackingClient', `${error.status}`],
        tags: { source: 'getTrackingClient' },
        extraData: { errorData: JSON.stringify(error) },
        stacktrace: true,
      }),
    send: ({ events, query, isBeforeUnload }) => {
      const endpoint = `${origin}${path}?${query}`;

      const data = events;

      const sendXhr = () => {
        noAuthHttpClient.post(endpoint, { data }).catch((error) => {
          let errorData = '';
          try {
            errorData = JSON.stringify(error);
          } catch (__err) {
            errorData = error.message || '';
          }
          LogError({
            error,
            fingerprint: [
              'getTrackingClient',
              `${EXTENSION_VERSION}`,
              `${error.status}`,
            ],
            tags: { source: 'getTrackingClient' },
            extraData: { errorData, endpoint, data },
            stacktrace: true,
          });
        });
      };

      if (isBeforeUnload) {
        genericClient.sendBeacon(endpoint, data, sendXhr);
      } else {
        sendXhr();
      }
    },
  });
