// @ts-expect-error dependency missing types
import HubApiHttpClientProxy from 'sales-ext-common/apis/HubApiHttpClientProxy';
import { LogHttpError } from 'sales-clients-common/utils/reportError';
import { PromiseClient } from 'hub-http/adapters/promiseClient';

export const fetchConfigDomSelectors = () => {
  const fetchGmailSelectors = HubApiHttpClientProxy.get(
    'sales-ext-config/v1/dom-selectors/gmailSelectors.json'
  ).catch((error: object) => {
    LogHttpError({
      error,
      source: 'configDomSelectorsApi',
      method: 'configDomSelectorsApi',
      level: 'error',
      extraData: {
        message: `Failed to fetch selectors from sales-ext-config/v1/dom-selectors/gmailSelectors.json`,
      },
    });
    throw error;
  });

  return Promise.all([fetchGmailSelectors]).then(([gmailSelectors]) => ({
    gmailSelectors: gmailSelectors.toJS(),
  }));
};

