export type featureOverrideConfigType = {
  [key: string]: {
    status: string;
    gates?: string[];
  };
};

export const fetchFeatureOverrideConfig = (http: PromiseClient) => {
  return http
    .get<featureOverrideConfigType>(
      'sales-ext-config/v1/features/featureOverrides.json'
    )
    .catch((error: object) => {
      LogHttpError({
        error,
        source: 'fetchFeatureOverrideConfig',
        method: 'fetchFeatureOverrideConfig',
        level: 'error',
        extraData: {
          message:
            'Failed to fetch featureOverrides from sales-ext-config/v1/featureOverrides/featureOverrides.json',
        },
      });
      return {};
    });
};
