export class NotificationCacheManager {
  private ids: Map<number, boolean>;
  private readonly capacity = 100;
  private readonly limit = 50;

  private shift(): void {
    const iterator = this.ids.keys();
    while (this.ids.size > this.limit) {
      // @ts-expect-error - type violation in TypeScript 5.7
      this.ids.delete(iterator.next().value);
    }
  }

  constructor() {
    this.ids = new Map();
  }

  get cache(): Map<number, boolean> {
    return this.ids;
  }

  get isFull(): boolean {
    return this.ids.size >= this.capacity;
  }

  has(id: number): boolean {
    return this.ids.has(id);
  }

  clear(): void {
    this.ids.clear();
  }

  add(id: number): void {
    if (this.isFull) {
      this.shift();
    }
    this.ids.set(id, true);
  }
}

export const NotificationCache = new NotificationCacheManager();
