'use es6';

import { getTrackingClient } from './usageTrackerClient';

export class SignalsExtensionUsageTracker {
  static initAuthedTracker(events, properties) {
    if (this._tracker) {
      this._tracker = this._tracker.clone({ properties });
    } else {
      this._tracker = getTrackingClient().createTracker({
        events,
        properties,
      });
    }
  }

  // Note: email needs to be explicitly set to null to prevent usage-tracker-js from atttempting to fetch user info
  static initUnauthedTracker(events, properties) {
    properties.email = null;
    properties.hubId = null;
    this._tracker = getTrackingClient().createTracker({
      events,
      properties,
    });
  }

  static track(event, eventData = {}) {
    if (this._tracker) {
      this._tracker.track(event, eventData);
    } else {
      console.warn(
        'Attempting to track an event before the tracker is set. Please make sure the tracker is initialized.'
      );
    }
  }

  static getTracker() {
    return this._tracker;
  }
}
SignalsExtensionUsageTracker._tracker = null;
