/* * * *
 * If you need to initialize the container without data, please use the generic to specify the type.
 * If you can initialize the container with data, it's probably better to let TypeScript infer the type.
 * * * */
export class Container<T = void> {
  constructor(private data?: T) {}

  set(data: T | undefined) {
    this.data = data;
  }

  get(): T | undefined {
    return this.data;
  }

  getAsPromise(): Promise<T | undefined> {
    return Promise.resolve(this.data);
  }
}

export default {
  /**
   * @deprecated use `new Container<T>()` or `new Container(data)` instead
   */
  create: () => new Container<any>(),
};
