function delay(ms: number): Promise<void>;
function delay<V>(val: V, ms: number): Promise<V>;
function delay(val?: unknown, ms?: number): Promise<unknown> {
  if (ms === undefined) {
    ms = val as number | undefined;
    val = undefined;
  }

  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(val);
    }, ms);
  });
}

export default delay;
